<template>
  <div >
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <template v-slot:activator="{ on }">
        
          <v-btn  class="orgDialog"
           text  
           @click="moreM()"
          v-on="on">
          <span>Sil</span> 
         </v-btn>
 

      </template>

      <v-card>
        <v-card-title
          class="modal-header"
           
        >
          <span>Sil</span>
          <v-spacer></v-spacer>
          
           <v-btn
           icon
            color="primary"
            text
            @click="dialog = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
          <v-form @submit='cancelSubmit'  v-model="isFormValid" ref="form" >
        <v-card-text>
               <v-row>
                 
                   <v-col cols="12"  md="12">
                          <v-menu
                              v-model="menu"
                              :close-on-content-click="false"
                              :nudge-right="40"
                               transition="scale-transition"
                               offset-y
                               min-width="290px"
                          >
                          <template v-slot:activator="{ on }">
                             <v-text-field
                              :value="formData.cancelDate"
                              label="Silinmə tarixi"
                              append-icon="event"
                               readonly
                               v-on="on"
                              ></v-text-field>
                          </template>
                          <v-date-picker :first-day-of-week="1" v-model="formatcancelDate" @input="menu = false"></v-date-picker>
                          </v-menu>
                        
                       </v-col>

                   
             </v-row>
        </v-card-text> 
        <v-divider></v-divider> 
        <v-card-actions class='dialog-buttons'>
           <v-btn   @click="dialog = false; formData.formatpaymentDate=null; formData.paymentAmount=null"  class="btn-cancel" outlined>İMTİNA ET</v-btn> 
             <v-btn :disabled="!isFormValid"  type="submit" class="btn-add" >TƏSDİQ ET</v-btn> 
        </v-card-actions>
    </v-form>
      </v-card>
          <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
    </v-dialog>
  </div>
</template>
<script>
 
import {  mapActions } from "vuex"; 
 import ErrorDialog from '../popups/ErrorDialog';
  export default {
 props:["id"],
 components:{ErrorDialog},
    data () {
      return {
         formData:{ },
         isFormValid:false,
         dialog: false,
         menu:false,
         errorMsg:'',
         dialog_err:false,
         validationRules: [ v => !!v || 'Sahəni boş buraxmaq olmaz!'],
      }
    },
    methods:{
       reset() {   this.$refs.form.reset();   },
    ...mapActions(["cancelUnitOrgs","fetchMainOrgs"]),
       addc(){
       var  element = document.querySelectorAll('.tf-nc~ul li'); 
       for (var i = 0; i < element.length; i++) {  element[i].parentElement.parentElement.firstElementChild.classList.add("mystyle"); }
     },
      moreM(){
    document.querySelector('.more-menu').style.display='none'
      },
      cancelSubmit(e) {   
          e.preventDefault();
            this.formData.organizationUnitId=this.id; 
            this.cancelUnitOrgs(this.formData)
              .then(()=>{  
                this.fetchMainOrgs()
                .then(()=>{  this.addc();  }) 
                this.dialog=false; 
                
              })  
             .catch(error => {  
                this.dialog_err=true;
                this.errorMsg=error.response.data;  
         })
        },
     
},
     computed: {  
            formatcancelDate:
        {
            get() {
              if (this.formData.cancelDate === undefined) { return null; }
              return  this.formData.cancelDate.split('.').join('-').replace(/(\d{2})-(\d{2})-(\d{4})/, "$3-$2-$1"); }  ,
              set(value) { this.formData.cancelDate = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },
     },
  
   
  }
</script>
 