<template>
<div class="table-componenet users-table-design">
    <v-data-table 
      :headers="headers"
      :items="allusers"   
       item-key="personnelName" 
         :footer-props="{
        itemsPerPageText:'Məlumat sayı:',
           itemsPerPageOptions: [10,50,100, 1000], // -> Add this example
          
      }">
     
        <template v-slot:body="{ items }">
           <tbody>
          <tr :class="key === selectedRow ? 'custom-highlight-row' : ''" @click="rowSelect(key)" v-for="(item, key) in items" :key="item.name">
          <td :class="item.closed === true ? 'closed' : ''">{{key+1}}</td>
          <td>{{ item.loginId }}</td>
           <td>{{item.personnelName+" "+item.personnelSurname + ' '+ item.personnelPatronymic}}</td>
           
           <td> 
             <v-menu offset-y :close-on-content-click='false' v-if="item.closed==false">
      <template v-slot:activator="{ on }">
       <v-btn v-on="on" icon>
        <v-icon>more_vert</v-icon>  
      </v-btn>
      </template>
      
      <v-list class='button_list'> 
         <v-list-item>
          <UserRoles v-bind:userId = 'item.id'  v-bind:name_user="item.personnelName " v-bind:surname="item.personnelSurname " v-bind:patronymic=" item.personnelPatronymic "/>
         </v-list-item>

         <v-list-item>
          <ResetPassword v-bind:userId = 'item.id'  v-bind:name_user="item.personnelName+' '+item.personnelSurname+' '+item.personnelPatronymic" />
         </v-list-item>
      </v-list>
    </v-menu></td>
        </tr>
         <tr v-if="allusers.length==0" class="norecord">
          <td colspan="4"><div class="norecord-c">Məlumat yoxdur...</div></td>
        </tr>
      </tbody>
    </template>
  </v-data-table> 
    <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
  </div>

</template>
 
<script > 
import UserRoles from '../popups/UserRoles';
import ResetPassword from '../popups/ResetPassword';
import ErrorDialog from '../popups/ErrorDialog';
import { mapGetters, mapActions } from "vuex";  
  export default {
    components:{UserRoles,ResetPassword,ErrorDialog},
    data () {
      return {  
        errorMsg:'',
         dialog_err:false, 
          selectedRow: null, 
           headers:[
        { text: '№',   value: 'index',sortable:false },
        { text: 'İstfadəçi adı',   value: 'loginId'  },
        { text: 'A.S.A', value: 'personnelName'   },  
        { text: '',  value: 'more' ,sortable:false  },
         
      ]
      }
    },
      methods: {
    ...mapActions(["fetchUsers","deleteUser"]),
     rowSelect(idx) { this.selectedRow = idx; },
        },
  computed: mapGetters(["allusers","filteredusers"]),
  created() {
    this.fetchUsers() 
     .catch(error => {  
        if(error.response.data.errorCode=='Session.NotFound'|| error.response.data.errorCode=='Session.IsExpired'){ this.$router.push({ path: '/' });   }
         
              this.dialog_err=true;
                this.errorMsg=error.response.data; 
        
         })
  }
  
}
</script>
<style  >
.closed,
.closed ~ td{
   opacity: 0.5; 
}
</style>