<template>
     <v-app-bar
      app 
      dark
      class="header"
    >
      <div class="d-flex align-center">
        <v-img
         
          class="shrink mr-2"
          contain
          src="../assets/logo.png"
          transition="scale-transition"
          width="40"
        />

          <div class="outline">
                <div>AZƏRBAYCAN RESPUBLİKASININ İQTİSADİYYAT NAZİRLİYİ</div>
                <div>YANINDA ƏMLAK MƏSƏLƏLƏRİ DÖVLƏT XİDMƏTİ</div>
                  
          </div>
      </div>

      <v-spacer></v-spacer>
<div  v-if="loginuser!= null"  class="current_user"> <span>{{userinfo }}</span> </div>
        <v-menu  :close-on-content-click="closeOnContentClick" open-on-hover offset-y >
      <template v-slot:activator="{ on }">
        <v-btn  icon  v-on="on">
        <v-icon>mdi-magnify</v-icon>
      </v-btn>
      </template>
      <v-card class='search'>
        <v-text-field v-model='search'
            label="Axtar..."
          ></v-text-field> 
      </v-card> 
    </v-menu>
     <ChangePassword/>
      <UserTasks/>
    
       
       <v-btn icon router to="/logout" title="Çıxış">
        <v-icon>exit_to_app</v-icon>
      </v-btn> 
    </v-app-bar>
</template>

<script>
    import ChangePassword from '../popups/ChangePassword'
    import UserTasks from '../popups/UserTasks'
    import { mapGetters  } from "vuex";
export default {
    name:'Header',
    components:{ ChangePassword,UserTasks},
    data() {
    return{
    closeOnContentClick: false,
    user:this.loginuser,
  }
},
     
   computed:
    {
       ...mapGetters(["loginuser"]),
     search:
      {
        get () { return this.$store.getters.search },
        set (value) { this.$store.commit('updateSearch', value) }
      },

      
      
     userinfo:function(){
       if(this.loginuser.user.personnelName || this.loginuser.user.personnelSurname||this.loginuser.user.personnelPatronymic){
        return this.loginuser.user.personnelName+' '+this.loginuser.user.personnelSurname+' '+this.loginuser.user.personnelPatronymic;
      
     }
     
     else{
       return this.loginuser.user.loginId 
     }
     },
     
          
   
       
   
    },
   
   
}
</script>

<style >
.button__badge{
     background-color: #fa3e3e;
    border-radius: 2px;
    color: white;
    padding: 1px 3px;
    font-size: 10px;
    position: absolute;
    top: 0px;
    right: 0px;
    border-radius: 50%;
}
.theme--dark.v-sheet{
  background-color: #2294F2!important;
}
.outline{
    font-size: 11px;
    padding-top: 5px;
}
.v-btn{
    box-shadow: none;
    background: transparent!important;
    text-transform: unset!important;
}
.v-btn.v-size--default {
    font-size: 10px!important;
    width: auto!important;
        padding: 0px 6px!important
}

.search{
  padding: 0px 10px;
}

 

.search label{
   color: #5c5c5c!important;
   font-size: 16px!important;
}
.current_user{
      font-size: 14px;
    padding: 0px 10px;
}
</style>