<template>
<div class="table-componenet  hr-table-design">
    <v-data-table 
      :headers="headers"
      :items="allPosition"   
        item-key="personnelName" 
       :footer-props="{
        itemsPerPageText:'Məlumat sayı:',
        itemsPerPageOptions: [10,50,100, 1000], // -> Add this example
          
      }">
     
        <template v-slot:body="{ items }">
           <tbody>
          <tr :class="key === selectedRow ? 'custom-highlight-row' : ''" @click="rowSelect(key)" v-for="(item, key) in items" :key="key">
          <td >{{key+1}}</td> 
          <td>{{item.personnelName}}</td> 
          <td>{{item.personnelSurname}}</td>
          <td>{{item.personnelPatronymic}}</td> 
          <td>{{item.organizationUnitDescription}}</td>
          <td>{{item.description}}</td> 
          <td>{{item.cancelDate}}</td> 
      
            <td><v-menu offset-y :close-on-content-click='false' v-if="item.canceled===false" >
      <template v-slot:activator="{ on }">
       <v-btn  v-on="on" icon>
        <v-icon>more_vert</v-icon>  
      </v-btn>
      </template>
      <v-list class='button_list'>
          <v-list-item>
           <v-list-item-content>
             <v-list-item-title><CancelPersonnelPos v-bind:personnelPositionId="item.id"/></v-list-item-title>
          </v-list-item-content>
         </v-list-item> 
           </v-list>
    </v-menu>
    </td>
        </tr>
        <tr v-if="allPosition.length==0" class="norecord">
          <td colspan="8"><div class="norecord-c">Məlumat yoxdur...</div></td>
        </tr>
      </tbody>
    </template>
  </v-data-table> 
  <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
  </div>

</template>
 
<script>
import CancelPersonnelPos from '../popups/CancelPersonnelPos';
import { mapGetters, mapActions } from "vuex";
import ErrorDialog from '../popups/ErrorDialog';
export default {
  components:{
    CancelPersonnelPos,ErrorDialog
  },
    data () {
      return {
         errorMsg:'',
         dialog_err:false,
         selectedRow: null,
              headers:[
        { text: '№',   value: 'index',sortable:false },
        { text: 'Adı',   value: 'personnelName'  },
        { text: 'Soyadı', value: 'personnelSurname'   }, 
        { text: 'Ata adı', value: 'personnelPatronymic'   }, 
       { text: 'Şöbə', value: 'organizationUnitDescription'   }, 
       { text: 'Təsviri', value: 'description'   }, 
       { text: 'Ləğv olunma tarixi', value: 'cancelDate'   }, 
          { text: '',  value: 'more' ,sortable:false  }, 
      ]
        
         
      }
    },
      methods: {
    ...mapActions(["fetchPosition"]),
   rowSelect(idx) { this.selectedRow = idx; },
        },
  computed: mapGetters(["allPosition"]),
  created() {
    this.fetchPosition() 
        .catch(error => {  
        if(error.response.data.errorCode=='Session.NotFound'|| error.response.data.errorCode=='Session.IsExpired'){ this.$router.push({ path: '/' });   }
         
              this.dialog_err=true;
                this.errorMsg=error.response.data; 
        
         })
  }
  
}
</script>