<template>
  <div >
    <v-dialog
      v-model="dialog"
      width="700"
    >
      <template v-slot:activator="{ on }">
        
           <v-btn @click=" fetchRoles();  getViewOperations(); runfetchRoleById()"  class="edit-but" color="#2294f2"><v-icon v-on="on">perm_contact_calendar</v-icon> </v-btn>
      </template>

      <v-card>
        <v-card-title
          class="modal-header"
           
        >
          <span>{{name}} üçün mövcud səlahiyyətlər</span>
          <v-spacer></v-spacer>
          
           <v-btn
            icon
            color="primary"
            text
            @click="dialog = false;chbxSelect=null;search_dialog=''"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
          <v-form  @submit.prevent="updateGrantSubmit"   ref="form" >
         
             
                <div class='fixeddialogheader'>
                
                     <v-row>
            
                    <v-col cols="12"  md="6"   class="srch">
                   <input @keydown.enter.stop.prevent  type="text" style="    float: left;    min-width: 290px;    border: 1px solid #c1c1bd;    padding: 2px 6px;  border-radius: 3px;"  v-model='search_dialog' solo placeholder="Axtar..." />  
                     </v-col>
                     
                       <v-col cols="12"  md="6"  class="role-checkbox" > 
                           <v-radio-group    v-model="chbxSelect" :change='chbx()'>
                           <v-radio value="1"   label="Hamısını seç" ></v-radio>  
                           <v-radio value="0"   label="Hamısını sil"></v-radio> 
                        </v-radio-group>  
                    </v-col>
                     </v-row>
                     
                  <div > 
             </div>
         </div>
          <v-card-text  style="max-height: 45vh;  overflow: auto;">
             
              <div class="table-componenet">
  <v-simple-table>
    <template v-slot:default>
          
      <tbody>
 

    
        <tr v-for="(item,index )  in filteredViewOperations.filter(x => x.type =='MENU_ITEM')" :key="index">  
          <td style="text-align:left!important;">{{item.breadcrumbLabel}} </td> 
    
          <td style="width:20px!important"> <input type="checkbox" name="chbx"  v-model="menu_items" :value="item.id"/> </td>
        </tr>
 
      <tr  v-for="(item,index) in filteredViewOperations.filter(x => x.type =='VIEW_COMMAND')"  :key="index +'111'"> 
           
          <td style="text-align:left!important;">{{item.breadcrumbLabel }}</td> 
     
            <td style="width:20px!important"><input type="checkbox"   name="chbx"  v-model="view_commands" :value="item.id"/></td>
        </tr>
        
      </tbody>
    </template>
  </v-simple-table> 
  </div>    
        </v-card-text>   
        <v-divider></v-divider>
  
        <v-card-actions class='dialog-buttons'>
           <v-btn   @click="dialog = false; runfetchRoleById(); chbxSelect=null;search_dialog=''"  class="btn-cancel" outlined>İMTİNA ET</v-btn> 
             <v-btn   type="submit" class="btn-add" >TƏSDİQ ET</v-btn> 
        </v-card-actions>
    </v-form>
     
      </v-card>
    </v-dialog>
       <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex"; 
  import ErrorDialog from '../popups/ErrorDialog';
  export default {
      components:{ ErrorDialog},
  props:["roleId", "name"],
    data () {
      return { 
         chbxSelect:null, 
         obj:{}, 
         menu_items:[],
         view_commands:[],   
         errorMsg:'', 
         dialog_err: false,
         dialog: false,
         menu:false,
         validationRules: [ v => !!v || 'Sahəni boş buraxmaq olmaz!'],  }
    },
    methods:{
...mapActions(["fetchRoles", "fetchRoleById", "getViewOperations", "updateGrantRole" ]),
   runfetchRoleById(){
this.fetchRoleById(this.roleId)
     .then((res)=>{ 
       this.menu_items=res.data.permittedMenuItemIdList;
       this.view_commands=res.data.permittedViewCommandIdList; 
     
     })
   },
  

     chbx(){
       if(this.chbxSelect==0){
          this.menu_items=[];
           this.view_commands=[]
       }
       else  if(this.chbxSelect==1){
         this.menu_items=this.allViewOperations.filter(x => x.type =='MENU_ITEM').map(({ id }) => id);
         this.view_commands=this.allViewOperations.filter(x => x.type =='VIEW_COMMAND').map(({ id }) => id);
       }
     },
    updateGrantSubmit(e) {  
        
          e.preventDefault();
            this.obj.roleId=this.roleId; 
            this.obj.menuItemIdList=this.menu_items;
            this.obj.viewCommandIdList=this.view_commands;
            this.updateGrantRole(this.obj)
              .then(()=>{
                this.dialog=false; 
                this.fetchRoles();  
                this.runfetchRoleById(this.roleId);
           })  
                .catch(error => {  
                this.dialog_err=true;
                this.errorMsg=error.response.data; 
                this.dialog_new=false; 
         }) 
        },
    
  },
     computed: {
       ...mapGetters(["allRoles","filteredViewOperations","allViewOperations"]), 
       
     search_dialog:
      {
        get () { return this.$store.getters.search_dialog },
        set (value) { this.$store.commit('updateSearchDialog', value) }
      }
 
     },
  
 
  }
</script>

<style  >
.v-btn{
        text-transform: unset!important;
}
.modal-header{
        background-color: #14b6db!important;
    padding: 5px 15px!important;
}
.modal-header span{
        font-size: 14px;
    font-weight: normal;
    color: white;
}
.modal-header .v-icon{
    color: white;
    font-size: 20px;
}
.new-payment{
  text-align: center!important;
}
.v-dialog .v-card__actions{
    justify-content: center!important;
}

.btn-cancel{
    border: solid 1px #2294f2;
    color: #2294f2;
     
}
.edit-but{
     color: #2294f2!important;
}
.btn-add{
     background-color: #2294f2!important;
     color: white!important;
     text-transform: uppercase!important;
}

.btn-new-p{
      padding: 0px 15px!important;
}
.dialog-buttons .v-btn{
  width: 115px!important;
   box-shadow: unset;
}
.dialog-buttons{
    padding: 25px 0px;
}
.fixeddialogheader{
         padding: 12px 62px 0px 30px;
    -webkit-box-shadow: 0 0 13px 0 rgba(0, 0, 0, 0.15);
    box-shadow: 0 0 13px 0 rgba(0, 0, 0, 0.15);
    z-index: 999;
    position: relative;
}
.role-checkbox .v-input{
  margin-top: 0px!important;
    float: right!important;
}

.fixeddialogheader .srch .v-input__slot{
      border: 1px solid #e5dfdf;
      box-shadow: none!important;
}

</style>