<template>
  <div>
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <template v-slot:activator="{ on }">
        
         <v-btn  icon   class='new_button'
          text
          @click="fetchExistOrgs()"
          v-on="on">
          <v-icon>create_new_folder</v-icon>
          <span>Yeni</span>
         </v-btn>
         
      </template>

      <v-card>
        <v-card-title
          class="modal-header"
           
        >
          <span> Insan resursları > Ştat vahidləri > Yeni ştat vahidi yarat</span>
          <v-spacer></v-spacer>
          
           <v-btn
           icon
            color="primary"
            text
            @click="dialog = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
          <v-form  @submit="addPersonnelPosSubmit"  v-model="isFormValid" ref="form" >
        <v-card-text>
                   <v-row>
                  
                         <v-col cols="12"  md="12"   >
                         <v-text-field  :rules="validationRules" v-model="formData.description" label="Təsviri"   required ></v-text-field>
                   
                         </v-col>
                      
                          
                           <v-col   cols="12"   md="12" > 
                           <v-select :rules="validationRules"  v-model="formData.organizationUnitId"    label="Şöbə adı" :items="existPos" item-value="id" item-text="description">
                            
                             </v-select>   
                        </v-col>
                         
                      </v-row>
                   <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions class='dialog-buttons'>
           <v-btn   @click="dialog = false; reset()"  class="btn-cancel" outlined>İMTİNA ET</v-btn> 
             <v-btn :disabled="!isFormValid"  type="submit" class="btn-add" >ƏLAVƏ ET</v-btn> 
        </v-card-actions>
    </v-form>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import ErrorDialog from '../popups/ErrorDialog';
import { mapGetters, mapActions } from "vuex";  
  export default {
   components:{ErrorDialog},
    data () {
      return {
        formData:{ },
        errorMsg:'',
       dialog_err:false,
         dialog: false,
         existPos:[],
         isFormValid:false, 
         validationRules: [
        v => !!v || 'Sahəni boş buraxmaq olmaz!', 
         ],
      }
    },

     computed:{
        
    ...mapGetters(["allOrgs" ]),
     
      },
 
  methods: {
     ...mapActions(["fetchUnitOrgs", "addPosition","fetchPosition" ]),
       reset() { this.$refs.form.reset();  }, 

        addPersonnelPosSubmit(e) {   
        e.preventDefault(); 
        this.addPosition(this.formData)
        .then(()=>{
         this.dialog=false; 
         this.fetchPosition();
         this.reset();
        })
         .catch(error => {  
           
                this.dialog_err=true;
                  this.errorMsg=error.response.data;  
         })
        
       },

       fetchExistOrgs(){
         this.fetchUnitOrgs()
         .then(()=>{
            this.existPos=this.allOrgs.filter(pos=>{return pos.canceled === false});
         })

       }
        },
     
  created() {
   
  }
  }
</script>

 