<template>
  <div>
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <template v-slot:activator="{ on }">
        
         <v-btn  icon   class='new_button'
          text
          v-on="on">
          <v-icon>create_new_folder</v-icon>
          <span>Yeni</span>
         </v-btn>
         
      </template>

      <v-card>
        <v-card-title
          class="modal-header"
           
        >
          <span> Insan resursları > Satıcılar > Yeni satıcı yarat</span>
          <v-spacer></v-spacer>
          
           <v-btn
           icon
            color="primary"
            text
            @click="dialog = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
        </v-card-title>
          <v-form  @submit="addSellerSubmit"  v-model="isFormValid" ref="form" >
        <v-card-text>
               <v-row>
                  <v-col   cols="12"   md="12" > 
                     <v-select :rules="validationRules"
                       v-model="formData.personnelId"   
                        label="Əməkdaş"
                         :items='allworkers' 
                        item-value="id" 
                        
                        :item-text="item => item.name +' '+item.surname+' '+ item.patronymic"  
                          >
 
                    </v-select> 
                  
 
            
                 </v-col> 
             </v-row>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions class='dialog-buttons'>
           <v-btn   @click="dialog = false; formData.personnelId=null"  class="btn-cancel" outlined>İMTİNA ET</v-btn> 
             <v-btn :disabled="!isFormValid"  type="submit" class="btn-add" >ƏLAVƏ ET</v-btn> 
        </v-card-actions>
    </v-form>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import ErrorDialog from '../popups/ErrorDialog';
import { mapGetters, mapActions } from "vuex";  
  export default {
    components:{ErrorDialog},
 
    data () {
      return {
         formData:{ personnelId:null },
         isFormValid:false,
             dialog: false,
               errorMsg:'',
        dialog_err:false,
         validationRules: [ v => !!v || 'Sahəni boş buraxmaq olmaz!'],
      }
    },

     computed:{ ...mapGetters(["allworkers", "allworkers" ])},
     methods: {
     ...mapActions([ "addSeller","fetchSellers","fetchWorkers" ]),
         reset() {   this.$refs.form.reset();   },
      
        addSellerSubmit(e) {   
        e.preventDefault(); 
        this.addSeller(this.formData)
        .then(()=>{
         this.dialog=false;
           this.fetchSellers()
          this.reset();
        })
         .catch(error => {  
                this.dialog_err=true;
                this.errorMsg=error.response.data;  
         })
         
       
       }
        },
     
  created() {
    this.fetchSellers(); 
     this.fetchWorkers(); 
  }
  }
</script>

<style scoped>
.v-btn{
        text-transform: unset!important;
}
.modal-header{
        background-color: #14b6db!important;
    padding: 5px 15px!important;
}
.modal-header span{
        font-size: 14px;
    font-weight: normal;
    color: white;
}
.modal-header .v-icon{
    color: white;
    font-size: 20px;
}

.v-dialog .v-card__actions{
    justify-content: center!important;
}

.btn-cancel{
    border: solid 1px #2294f2;
    color: #2294f2;
     
}
.btn-add{
     background-color: #2294f2!important;
     color: white!important;

}
.dialog-buttons .v-btn{
  width: 115px!important;
   box-shadow: unset;
}
.dialog-buttons{
    padding: 25px 0px;
}
</style>