<template>
  <div>
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <template v-slot:activator="{ on }">
        
          <v-btn icon   class='hrmorevert'
           text @click="moreM();pay();getViewComponents(viewComponentId);"
          v-on="on">
          <v-icon>payment</v-icon>
          <span>Ödəniş</span>
         </v-btn>
      </template>

      <v-card>
        <v-card-title
          class="modal-header"
           
        >
          <span>Ödəniş</span>
          <v-spacer></v-spacer>
          
           <v-btn
           icon
            color="primary"
            text
            @click="dialog = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn> 
        </v-card-title>
         
        <v-card-text>
               <v-row>
                  <v-col   cols="12"   md="12" > 
               
                     
                   <div class="table-componenet">
  <v-simple-table>
    <template v-slot:default>
      <thead>
        <tr>
          <th id='1' class="text-center">№</th>
          <th id='2'  class="text-center">Tarix</th>
          <th id='3'  class="text-center">Miqdar</th> 
              
          <th id='6'  class="text-center"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-if="allpayments.length === 0" class="norecord">
          <td colspan="4">Ödəniş yoxdur...</td>
        </tr>
        <tr v-for="(item, index) in  allpayments" :key="index">
          <td>{{ index+1 }}</td>
          <td>{{ item.paymentDate}}</td>  
          <td>{{ item.paymentAmount }}</td>
          <td  style="width:20px"> 
          <UpdatePayment v-bind:contractId='contractId' v-bind:paymentId="item.id" />
          </td>
        </tr>
      </tbody>
    </template>
  </v-simple-table> 

  </div> 
 
                 </v-col> 
                 <v-col  cols="12"   md="12"><div  v-if="formData.length != 0"  class="current_debt"><span class="dept_tit">Qalıq borc: </span><span class="dept_tit">{{formData.debt.currentDebt}}</span> </div> </v-col>
                 <v-col  cols="12"   md="12">
                   <NewPayment  v-if='allview_components.find(x => x.code === "ADD_PAYMENT_TO_CONTRACT")' v-bind:contractId='contractId'/>
                 </v-col>
             </v-row>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions class='dialog-buttons'>
           <v-btn   @click="dialog = false;"  class="btn-cancel" outlined>BAĞLA</v-btn> 
            
        </v-card-actions>
  
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex"; 
   import NewPayment from '../popups/NewPayment'
  import UpdatePayment from '../popups/UpdatePayment'
  export default {
    props:["contractId","viewComponentId"],
    components:{
     NewPayment , UpdatePayment
   },
    data () {
      return { 
     dialog:false,
      payarray: this.allpayments,
      }
    },

     computed:{ ...mapGetters(["allpayments", "formData","allview_components"]), 
     
     },
     methods: {
     ...mapActions([ "fetchPayment", "getContract","getViewComponents"]),
           moreM(){ document.querySelector('.more-menu').style.display='none' },

       pay(){
          this.fetchPayment(this.contractId)
       .then(( )=>{
    //   this.payarray=res.data;
     }) 
       }
        },

     
  created() {
    
  //  this.getContract(this.contractId); 
  }
  }
</script>

<style scoped>
.v-btn{
        text-transform: unset!important;
}
.modal-header{
        background-color: #14b6db!important;
    padding: 5px 15px!important;
}
.modal-header span{
        font-size: 14px;
    font-weight: normal;
    color: white;
}
.modal-header .v-icon{
    color: white;
    font-size: 20px;
}

.v-dialog .v-card__actions{
    justify-content: center!important;
}

.btn-cancel{
    border: solid 1px #2294f2;
    color: #2294f2;
     
}
.btn-add{
     background-color: #2294f2!important;
     color: white!important;

}
.dialog-buttons .v-btn{
  width: 115px!important;
   box-shadow: unset;
}
.dialog-buttons{
    padding: 25px 0px;
}
.norecord{
      font-style: italic;
    border-bottom: 1px solid #dfdbdb; 
    letter-spacing: 1.88px;}

    .current_debt{
          text-align: center;
    border-top: 1px solid rgba(0, 0, 0, 0.12)!important;
    border-bottom: 1px solid rgba(0, 0, 0, 0.12)!important;
    padding: 10px 0px;
    color: black;
    font-size: 16px;
    }
</style>