<template>
  <div>
    <v-dialog
      v-model="dialog"
      width="950"
    >
      <template v-slot:activator="{ on }">
        
          <v-btn icon   class='hrmorevert'
           text @click="moreM();payPlan()"
          v-on="on">
          <v-icon>money</v-icon>
          <span>Ödəmə planı</span>
         </v-btn>
      </template>

      <v-card>
        <v-card-title
          class="modal-header"
           
        >
          <span>Ödəmə planı</span>
          <v-spacer></v-spacer>
          
           <v-btn
           icon
            color="primary"
            text
            @click="dialog = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
         
        <v-card-text>
               <v-row>
                  <v-col   cols="12"   md="12" > 
               
                     
                   <div class="table-componenet">
                   
  <v-simple-table>
    <template v-slot:default>
      <thead>
        <tr>
          <th id='1' class="text-center">№</th>
          <th id='2'  class="text-center">Son tarix</th>
          <th id='3'  class="text-center">Miqdar</th> 
            <th id='3'  class="text-center">Cari borc</th> 
              <th id='3'  class="text-center">Ümumi gecikmiş gün sayı</th> 
                <th id='3'  class="text-center">Ümumi cərimə miqdarı</th> 
           <th id='3'  class="text-center">Qeyd</th> 
          <th id='4'  class="text-center">Faiz</th>   
            <th id='4'  class="text-center">Ödəmə listi</th>   
                   <th id='6'  v-if="router!= '/archive'&&router!= '/deletedcontracts'"  class="text-center"></th>
          <th id='6'  v-if="router!= '/archive'&&router!= '/deletedcontracts'"  class="text-center"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-if="allpayplan.length === 0" class="norecord">
          <td colspan="8">Plan daxil edilməyib...</td>
        </tr>
        <tr v-for="(item, index) in payp" :key="index">
          <td>{{ index+1 }}</td>
          <td>{{ item.deadline }}</td>
          <td>{{ item.paymentAmount}}</td> 
          <td>{{ item.currentDebt}}</td> 
          <td>{{ item.totalDelayedDayCount}}</td> 
          
          <td>{{ item.totalFeeAmount}}</td> 
          <td>{{ item.note}}</td> 
          <td v-if='item.penaltyFeeTariff'>{{ item.penaltyFeeTariff.feePercent }}</td> 
           <td><v-menu offset-y :close-on-content-click='false' content-class="more-menu">
      <template v-slot:activator="{ on }">
       <v-btn   v-on="on" icon>
        <v-icon>more_vert</v-icon>  
      </v-btn>
      </template>
          <v-simple-table>
    <template v-slot:default>
      <thead>
        <tr>
          <th id='1' class="text-center">№</th>
          <th id='2'  class="text-center">Gecikmiş gün sayı</th>
          <th id='3'  class="text-center">Miqdar</th> 
            <th id='3'  class="text-center">Tarix</th>  
           
        </tr>
      </thead>
      <tbody>
        <tr v-for="(i, index) in item.paymentList" :key="index+'p'">
          <td>{{ index+1 }}</td>
          <td>{{ i.delayedDayCount }}</td>
          <td>{{ i.paidAmount }}</td>
            <td>{{ i.paymentDate }}</td>
            </tr>
               </tbody>
    </template>
  </v-simple-table> 
          </v-menu>
    </td>
         <td v-if="router!= '/archive'&&router!= '/deletedcontracts'" style="width:20px"> 
          <Delete @onSubmit="payPlan"  v-bind:contractId='contractId' v-bind:id="item.id" v-bind:viewName="name"/>
          </td>
          <td v-if="router!= '/archive'&&router!= '/deletedcontracts'" style="width:20px"> 
          <UpdatePaymentPlan  @onSubmit="payPlan" v-bind:contractId='contractId' v-bind:paymentPlanId="item.id" />
          </td>
        </tr>
      </tbody>
    </template>
  </v-simple-table> 

  </div> 
 
                 </v-col> 
                
                 <v-col v-if="router!= '/archive'&&router!= '/deletedcontracts'" cols="12"   md="12">
                   <NewPaymentPlan @onSubmit="payPlan"  v-bind:contractId='contractId'/>
                 </v-col>
             </v-row>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions class='dialog-buttons'>
           <v-btn   @click="dialog = false;"  class="btn-cancel" outlined>BAĞLA</v-btn> 
            
        </v-card-actions>
  
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex"; 
import Delete from '../popups/Delete';
 import NewPaymentPlan from '../popups/NewPaymentPlan'
  import UpdatePaymentPlan from '../popups/UpdatePaymentPlan'
  export default {
    props:["contractId", "router"],
   components:{
    NewPaymentPlan , UpdatePaymentPlan, Delete
  },
    data () {
      return { 
     dialog:false,
     payp:[],
     name:'payPlan',
      }
    },

     computed:{ ...mapGetters(["allpayplan"]), },
     methods: {
     ...mapActions([ "fetchPaymentPlan"]),
       moreM(){
  document.querySelector('.more-menu').style.display='none'
},
       payPlan(){
       this.fetchPaymentPlan(this.contractId)
         .then((res)=>{
        this.payp=res.data;
        
       })
       }
        },
     
 
  }
</script>

<style scoped>
.v-btn{
        text-transform: unset!important;
}
.modal-header{
        background-color: #14b6db!important;
    padding: 5px 15px!important;
}
.modal-header span{
        font-size: 14px;
    font-weight: normal;
    color: white;
}
.modal-header .v-icon{
    color: white;
    font-size: 20px;
}

.v-dialog .v-card__actions{
    justify-content: center!important;
}

.btn-cancel{
    border: solid 1px #2294f2;
    color: #2294f2;
     
}
.btn-add{
     background-color: #2294f2!important;
     color: white!important;

}
.dialog-buttons .v-btn{
  width: 115px!important;
   box-shadow: unset;
}
.dialog-buttons{
    padding: 25px 0px;
}
.norecord{
      font-style: italic;
    border-bottom: 1px solid #dfdbdb; 
    letter-spacing: 1.88px;}

    .current_debt{
          text-align: center;
    border-top: 1px solid rgba(0, 0, 0, 0.12)!important;
    border-bottom: 1px solid rgba(0, 0, 0, 0.12)!important;
    padding: 10px 0px;
    color: black;
    font-size: 16px;
    }
</style>