<template>
  <div>
    <v-dialog
      v-model="dialog"
      width="70%"
    >
      <template v-slot:activator="{ on }">
         
            <v-btn  
            @click="userTasks(loginuser.user.id);newtask=false;"
              icon  v-on="on" 
               title="Bildiriş" 
             >
        <v-icon>notifications</v-icon>
          <span v-if="alltasksCount" class="button__badge">{{alltasksCount}}</span>
      </v-btn>
      </template>

      <v-card>
        <v-card-title
          class="modal-header"
           
        >
          <span>Bildirişlər</span>
          <v-spacer></v-spacer>
          
             
           <v-btn
           icon
            color="primary"
            text
            @click="dialog=false;"
          ><v-icon>cancel</v-icon>
            
          </v-btn>

       
          
        </v-card-title>
          <v-form    v-model="isFormValid" ref="form" >
         

        <v-divider></v-divider> 
        <div style="text-align: center;  padding: 35px 0px; font-size: 25px;" v-if="alluser_tasks.length==0">Bildiriş yoxdur... </div>
         <div class="table-componenet notification-table"  v-if="alluser_tasks.length!=0">
           
  <v-simple-table>
    <template v-slot:default>
      <thead>
        <tr>
          <th id='1' class="text-center">№</th>
          <th id='8' class="text-center">Müqavilənin nömrəsi</th>
          <th id='2'  class="text-center">Təsviri</th> 
          <th id='4'  class="text-center">Göndərən şəxsin A.S.A</th>
          <th id='5'  class="text-center">Göndərən şəxsin vəzifəsi</th>
          <th id='7'  class="text-center">Müqavilə tarixi</th>  
          <th id='7'  class="text-center">Son tarix</th>   
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in alluser_tasks" :key="item.id">
          <td  v-bind:class="{ viewed: !item.viewed }"> <router-link target='_blank' :to="{ path: '/update/'+ item.contractId  , query: { status:  'view' , notif:'note' } }">{{ index+1 }}</router-link></td>  
         
            <td> <router-link target='_blank' :to="{ path: '/update/'+ item.contractId  , query: {  status:  'view' , notif:'note'   } }">{{ item.contractContractNumber }}</router-link></td> 
          <td><router-link target='_blank' :to="{ path: '/update/'+ item.contractId  , query: { status:  'view'  , notif:'note'    } }">{{ item.typeDescription }}</router-link></td>  
          <td><router-link target='_blank' :to="{ path: '/update/'+ item.contractId  , query: { status:  'view' , notif:'note'     } }">{{ item.createdByPersonnelName+' '+item.createdByPersonnelSurname+' '+item.createdByPersonnelPatronymic }}</router-link></td>

          <td><router-link target='_blank' :to="{ path: '/update/'+ item.contractId  , query: { status:   'view'   , notif:'note'   } }">{{ item.createdByPersonnelPositionDescription }}</router-link></td>
            <td><router-link target='_blank' :to="{ path: '/update/'+ item.contractId  , query: { status:   'view'  , notif:'note'    } }">{{ item.contractContractDate }}</router-link></td> 
          <td><router-link target='_blank' :to="{ path: '/update/'+ item.contractId  , query: { status:   'view'   , notif:'note'   } }">{{ item.deadline }}</router-link></td> 
 
        </tr>
      </tbody>
    </template>
  </v-simple-table> 
  </div>

        <v-card-actions class='dialog-buttons'>
           <v-btn   @click="dialog=false;"  class="btn-cancel" outlined>BAĞLA</v-btn> 
            
        </v-card-actions>
    </v-form>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex"; 
 
  export default {
     props:["contractId"], 
    data () {
      return {
         formData:{   },
         isFormValid:false,
         dialog: false,
         menu:false,
         validationRules: [ v => !!v || 'Sahəni boş buraxmaq olmaz!'],
         unViewedTaskCount:null,
          viewedTaskIdList:[],
         newtask:true,
      }
    },
    methods:{
...mapActions(["userTasks", "unViewedTasksCount","unComTasksView"]),
    
    unViewedTasksCountRun(){
       
       this.userTasks(this.loginuser.user.id)
       .then((res)=>{  
           let obj={};
           obj.viewedTaskIdList = res.data.filter(a => a.viewed==false).map(a => a.id);
           obj.userId=this.loginuser.user.id; 
           this.unComTasksView(obj) 
           .then(()=>{
               this.unViewedTasksCount(this.loginuser.user.id) 
              
           })
      })
    },

   
 
 
  },
 
   watch: {
    dialog (val) {
      !val && this.unViewedTasksCountRun(); 
    }
  },
computed:{
   ...mapGetters(["alluser_tasks","loginuser","alltasksCount"]),
  
   
}, 
created(){
  this.unViewedTasksCount(this.loginuser.user.id);
}
  }
</script>
<style scoped>
table td{
  position: relative;
}
   .viewed ,  .viewed~td{
   background: #f1d3193b;
    
   }

   .notification-table a{
     color: #262424!important;
text-decoration: none!important;
   }

    .notification-table a:hover{
     opacity:0.5!important; 
   }
</style>
 