 <template>
 <div class='treecontent'>
  <div class="tf-tree example">
    
  <ul>
    <li v-for="(item, index) in allmainOrgs.filter(x => x.canceled == false)" :key="index">
    
       <v-menu offset-y :close-on-content-click="false"  content-class="more-menu">
      <template v-slot:activator="{ on }"> 
         
        <span v-bind:class="{ disable: item.canceled }" v-on="on" class="tf-nc tf-nc-first">{{item.description}}</span>
      </template> 
      <v-list> 
        <v-list-item>
          <v-list-item-title><NewOrg v-bind:name="'fornew'"  v-bind:typeId="item.typeId"  v-bind:organizationId="item.id"/></v-list-item-title>
        </v-list-item>
      
           <v-list-item>
          <v-list-item-title><NewOrg v-bind:name="'foredit'"  v-bind:typeId="item.typeId" v-bind:organizationId="item.id" v-bind:editType="'edit_main_org'"/></v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu> 
      <ul v-if='item.organizationUnitList'>
        <li  v-for="(item, index) in organizationUnits.filter(x => x.canceled == false)" :key="index">
           <v-menu offset-y :close-on-content-click="false"  content-class="more-menu">
      <template v-slot:activator="{ on }"> 
        <span  v-bind:class="{ disable: item.canceled }"  v-on="on" class="tf-nc">{{item.description}}</span>
      </template>
    <v-list> 
        <v-list-item>
          <v-list-item-title><NewOrg v-bind:name="'fornew'"  v-bind:typeId="item.typeId" v-bind:organizationId="1" v-bind:parentOrganizationUnitId="item.id"/></v-list-item-title>
        </v-list-item>
           <v-list-item>
          <v-list-item-title><NewOrg v-bind:name="'foredit'"  v-bind:typeId="item.typeId" v-bind:editType="'edit_org_unit'" v-bind:parentOrganizationUnitId="item.id"/></v-list-item-title>
        </v-list-item>
        <v-list-item>
          <v-list-item-title><CancelOrg v-bind:id="item.id"/></v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
            <ul v-if='item.subOrganizationUnitList'>
              <li  v-for="(item, index) in item.subOrganizationUnitList.filter(x => x.canceled == false)"  :key="index">
          
                      <v-menu offset-y :close-on-content-click="false"  content-class="more-menu">
      <template v-slot:activator="{ on }"> 
        <span  v-bind:class="{ disable: item.canceled }" v-on="on" class="tf-nc">{{item.description}}</span>
      </template>
    <v-list> 
       <v-list-item>
          <v-list-item-title><NewOrg v-bind:name="'fornew'"  v-bind:typeId="item.typeId"  v-bind:organizationId="1" v-bind:parentOrganizationUnitId="item.id"/></v-list-item-title>
        </v-list-item>
        
           <v-list-item>
          <v-list-item-title><NewOrg v-bind:name="'foredit'"  v-bind:typeId="item.typeId" v-bind:editType="'edit_org_unit'" v-bind:parentOrganizationUnitId="item.id"/></v-list-item-title>
        </v-list-item>
        <v-list-item>
          <v-list-item-title><CancelOrg v-bind:id="item.id"/></v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
       <ul v-if='item.subOrganizationUnitList'>
              <li  v-for="(item, index) in item.subOrganizationUnitList.filter(x => x.canceled == false)"  :key="index">
          
                      <v-menu offset-y :close-on-content-click="false"  content-class="more-menu">
      <template v-slot:activator="{ on }"> 
        <span  v-bind:class="{ disable: item.canceled }" v-on="on" class="tf-nc">{{item.description}}</span>
      </template>
    <v-list> 
      

           <v-list-item>
          <v-list-item-title><NewOrg v-bind:name="'foredit'"  v-bind:typeId="item.typeId" v-bind:editType="'edit_org_unit'" v-bind:parentOrganizationUnitId="item.id"/></v-list-item-title>
        </v-list-item>
        <v-list-item>
          <v-list-item-title><CancelOrg v-bind:id="item.id"/></v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
      
                </li>
             
          </ul>
                </li>
             
          </ul>
        </li>  
          
      </ul>
    </li>
  </ul>
</div>
 <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
</div>
</template>
<script> 
import ErrorDialog from '../popups/ErrorDialog';
import NewOrg from '../popups/NewOrg'
import CancelOrg from '../popups/CancelOrg'
import { mapGetters, mapActions } from "vuex";
export default { 
 components:{
   NewOrg,CancelOrg,ErrorDialog
  },
     data () {
      return {  
        errorMsg:'',
         dialog_err:false, }},
    computed:{
    ...mapGetters(["allmainOrgs","allOrgs"]),

    organizationUnits: function () {
    return this.allmainOrgs[0].organizationUnitList.filter(i => i.parentOrganizationUnitId === null)
  }
    
    },
    methods: {
     ...mapActions(["fetchMainOrgs","fetchUnitOrgs"]),
     addc(){
       var  element = document.querySelectorAll('.tf-nc~ul li'); 
       for (var i = 0; i < element.length; i++) {  element[i].parentElement.parentElement.firstElementChild.classList.add("mystyle"); }
     },
     
     },

      created() {
        this.fetchMainOrgs()
        .then(()=>{
          this.addc(); 
       
        }) 
         .catch(error => {  
        if(error.response.data.errorCode=='Session.NotFound'|| error.response.data.errorCode=='Session.IsExpired'){ this.$router.push({ path: '/' });   }
         
              this.dialog_err=true;
                this.errorMsg=error.response.data; 
        
         })
        this.fetchUnitOrgs(); 
       
  }
}
</script>

<style scoped>
.disable{
 pointer-events: none!important;
 opacity: 0.5!important;
 display: none!important;
}
.treecontent{
      display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    background: white;
}
.tf-tree {
	font-size: 16px;
	overflow: auto;
  text-align: center; 
      height: 100%;
    display: flex;
    align-items: center;
}

.tf-tree * {
	box-sizing: border-box;
	margin: 0;
	padding: 0
}

.tf-tree ul {
	display: inline-flex
}

.tf-tree li {
	align-items: center;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	padding: 0 1em;
	position: relative
}

.tf-tree li ul {
	margin: 2em 0 0
}

.tf-tree li li:before {
	border-top: .0625em solid #2294f2;
	content: "";
	display: block;
	height: .0625em;
	left: -.03125em;
	position: absolute;
	top: -1em;
	width: 100%
}

.tf-tree li li:first-child:before {
	left: calc(50% - .03125em);
	max-width: calc(50% + .0625em)
}

.tf-tree li li:last-child:before {
	left: auto;
	max-width: calc(50% + .0625em);
	right: calc(50% - .03125em)
}

.tf-tree li li:only-child:before {
	display: none
}

.tf-tree li li:only-child>.tf-nc:before,
.tf-tree li li:only-child>.tf-node-content:before {
	/* height: 1.0625em;
	top: -1.0625em */
    height: 1.4em;
	top: -1.4em 
}

.tf-tree .tf-nc,
.tf-tree .tf-node-content {
	border: .0625em solid #2294f2;
	display: inline-block;
	padding: .5em 1em;
	position: relative;
  font-size: 14px;
    color: #2294f2;
        max-width: 175px;
        cursor: pointer;
}

.tf-tree .tf-nc:before,
.tf-tree .tf-node-content:before {
	top: -1.03125em
}

.tf-tree .tf-nc:after,
.tf-tree .tf-nc:before,
.tf-tree .tf-node-content:after,
.tf-tree .tf-node-content:before {
	border-left: .0625em solid  #2294f2;
	content: "";
	display: block;
	height: 1em;
	left: calc(50% - .03125em);
	position: absolute;
	width: .0625em
}

.tf-tree .tf-nc:after,
.tf-tree .tf-node-content:after {
	top: calc(100% + .03125em);
  display: none;
}


.tf-tree .tf-nc:only-child:after,
.tf-tree .tf-node-content:only-child:after,
.tf-tree>ul>li>.tf-nc:before,
.tf-tree>ul>li>.tf-node-content:before {
	display: none
}

.tf-tree.tf-gap-sm li {
	padding: 0 .6em
}

.tf-tree.tf-gap-sm li>.tf-nc:before,
.tf-tree.tf-gap-sm li>.tf-node-content:before {
	height: .6em;
	top: -.6em
}

.tf-tree.tf-gap-sm li>.tf-nc:after,
.tf-tree.tf-gap-sm li>.tf-node-content:after {
	height: .6em
}

.tf-tree.tf-gap-sm li ul {
	margin: 1.2em 0
}

.tf-tree.tf-gap-sm li li:before {
	top: -.63125em
}

.tf-tree.tf-gap-sm li li:only-child>.tf-nc:before,
.tf-tree.tf-gap-sm li li:only-child>.tf-node-content:before {
	height: .6625em;
	top: -.6625em
}

.tf-tree.tf-gap-lg li {
	padding: 0 1.5em
}

.tf-tree.tf-gap-lg li>.tf-nc:before,
.tf-tree.tf-gap-lg li>.tf-node-content:before {
	height: 1.5em;
	top: -1.5em
}

.tf-tree.tf-gap-lg li>.tf-nc:after,
.tf-tree.tf-gap-lg li>.tf-node-content:after {
	height: 1.5em
}

.tf-tree.tf-gap-lg li ul {
	margin: 3em 0
}

.tf-tree.tf-gap-lg li li:before {
	top: -1.53125em
}

.tf-tree.tf-gap-lg li li:only-child>.tf-nc:before,
.tf-tree.tf-gap-lg li li:only-child>.tf-node-content:before {
	height: 1.5625em;
	top: -1.5625em
}

.tf-tree li.tf-dotted-children .tf-nc:after,
.tf-tree li.tf-dotted-children .tf-nc:before,
.tf-tree li.tf-dotted-children .tf-node-content:after,
.tf-tree li.tf-dotted-children .tf-node-content:before {
	border-left-style: dotted
}

.tf-tree li.tf-dotted-children li:before {
	border-top-style: dotted
}

.tf-tree li.tf-dotted-children>.tf-nc:before,
.tf-tree li.tf-dotted-children>.tf-node-content:before {
	border-left-style: solid
}

.tf-tree li.tf-dashed-children .tf-nc:after,
.tf-tree li.tf-dashed-children .tf-nc:before,
.tf-tree li.tf-dashed-children .tf-node-content:after,
.tf-tree li.tf-dashed-children .tf-node-content:before {
	border-left-style: dashed
}

.tf-tree li.tf-dashed-children li:before {
	border-top-style: dashed
}

.tf-tree li.tf-dashed-children>.tf-nc:before,
.tf-tree li.tf-dashed-children>.tf-node-content:before {
	border-left-style: solid
}
 .v-menu__content .v-list-item {
    min-height: 27px!important;}

     .tf-tree  .mystyle:after{ display: block;}
     /* .tf-nc.tf-nc-first::after{
       display: none;
     }
      .tf-nc.tf-nc-first~ul{
       margin-top: 15px;
     } */
</style>