<template>
  <div>
    <v-dialog
      v-model="dialog"
      width="600"
    >
      <template v-slot:activator="{ on }">
        
         <v-btn  icon   class='new_button'
          text
          v-on="on">
          <v-icon>create_new_folder</v-icon>
          <span>Yeni</span>
         </v-btn>
         
      </template>

      <v-card>
        <v-card-title
          class="modal-header"
           
        >
          <span> Köməkçi vasitələr > Peniya > Yenisini yarat</span>
          <v-spacer></v-spacer>
          
           <v-btn
           icon
            color="primary"
            text
            @click="dialog = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
          <v-form  @submit="addFeeTarif"  v-model="isFormValid" ref="form" >
        <v-card-text>
               <v-row>
                 
                        <!-- <v-col cols="12"  md="6"   >
                         <v-text-field label="Əlavə sabit məbləğ"  v-model="formData.fixedAmount" required ></v-text-field>
                         
                         </v-col> -->

                        <v-col cols="12"  md="4"   >
                         <v-text-field label="Faiz"  v-model="formData.feePercent" required ></v-text-field>
                         
                         </v-col>
                             <v-col cols="12"  md="4"   >
                         <v-text-field label="Minimum məbləğ"  v-model="formData.minAmount" required ></v-text-field>
                         
                         </v-col>

                             <v-col cols="12"  md="4"   >
                         <v-text-field label="Maksimum məbləğ"  v-model="formData.maxAmount" required ></v-text-field>
                         
                         </v-col>
                          

                          <v-col cols="12" md="12">
        <v-textarea
          solo
          name="input-7-4"
          label="Təsviri"
           v-model="formData.description"
        ></v-textarea>
      </v-col>
             </v-row>
               <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions class='dialog-buttons'>
           <v-btn   @click="reset();dialog = false"  class="btn-cancel" outlined>İMTİNA ET</v-btn> 
             <v-btn :disabled="!isFormValid"  type="submit" class="btn-add" >ƏLAVƏ ET</v-btn> 
        </v-card-actions>
    </v-form>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import {   mapActions } from "vuex"; 
 import ErrorDialog from '../popups/ErrorDialog';
  export default {
  components:{ErrorDialog},
    data () {
      return {
         formData:{   },
         isFormValid:false,
             dialog: false,
         validationRules: [ v => !!v || 'Sahəni boş buraxmaq olmaz!'],
         errorMsg:'',
        dialog_err:false,
      }
    },

    
     methods: {
     ...mapActions([ "addFeeTariffs","fetchFeeTariffs" ]),
         reset() {   this.$refs.form.reset();   },
      
        addFeeTarif(e) {   
        e.preventDefault(); 
        this.addFeeTariffs(this.formData)
        .then(()=>{
           this.dialog=false;
           this.fetchFeeTariffs();
           this.reset();

        })
         .catch(error => {  
                this.dialog_err=true;
                this.errorMsg=error.response.data;  
         })
         
       
       }
        },
     
  created() {
    this.fetchFeeTariffs(); 
     
  }
  }
</script>

<style scoped>
.v-btn{
        text-transform: unset!important;
}
.modal-header{
        background-color: #14b6db!important;
    padding: 5px 15px!important;
}
.modal-header span{
        font-size: 14px;
    font-weight: normal;
    color: white;
}
.modal-header .v-icon{
    color: white;
    font-size: 20px;
}

.v-dialog .v-card__actions{
    justify-content: center!important;
}

.btn-cancel{
    border: solid 1px #2294f2;
    color: #2294f2;
     
}
.btn-add{
     background-color: #2294f2!important;
     color: white!important;

}
.dialog-buttons .v-btn{
  width: 115px!important;
   box-shadow: unset;
}
.dialog-buttons{
    padding: 25px 0px;
}
</style>