 <template>
  <div class="table-componenet contract-table-design" v-if='allnewagreement'>
    
    <v-data-table 
      :custom-sort="customSort"
      :headers="headers"
      :items="filteredagreements"   
       item-key="contractNumber" 
      :footer-props="{
      itemsPerPageText:'Məlumat sayı:',
      itemsPerPageOptions: [10,50,100, 1000], // -> Add this example
          
      }"
        
    >
           <template v-slot:body="{ items }">
           <tbody>
          <tr :class="key === selectedRow ? 'custom-highlight-row' : ''" @click="rowSelect(key)" v-for="(item, key) in items" :key="item.name">
          <td>{{key+1}}</td> 
          <td style="width:23%">{{ item.contractTypeDescription }}</td>
          <td>{{ item.contractNumber }}</td> 
          <td>{{ item.sellerPersonnelName+' '+item.sellerPersonnelSurname +' '+item.sellerPersonnelPatronymic }}</td>
          <td>{{  item.currentProcessorPersonnelName+' '+item.currentProcessorPersonnelSurname+' '+item.currentProcessorPersonnelPatronymic }}</td>
           <td>{{ item.contractDate }}</td> 
               <td style="color:#ca892f;" v-if='Array.isArray(proccess)'>{{ $t(item.processStatus) }}</td>
           <td><v-menu offset-y :close-on-content-click='closeOnContentClick' content-class="more-menu">
      <template v-slot:activator="{ on }">
       <v-btn  @click="getContract(item.id)" v-on="on" icon>
        <v-icon>more_vert</v-icon>  
      </v-btn>
      </template>
      <v-list class='button_list'>
          <v-list-item  v-if='item.subMenuItemList.find(x => x.code === "CONTRACT_CANCEL")'>
           <v-list-item-content>
             <v-list-item-title><CancelContract v-bind:closeOnContentClick="closeOnContentClick" v-bind:contractId="item.id" v-bind:viewComponentId="item.subMenuItemList.find(x => x.code === 'CONTRACT_CANCEL').viewComponentId"/></v-list-item-title>
          </v-list-item-content>
         </v-list-item>

           <v-list-item  v-if='item.subMenuItemList.find(x => x.code === "CONTRACT_COMPLETE")'>
           <v-list-item-content>
             <v-list-item-title><CompleteContract v-bind:contractId="item.id"  v-bind:viewComponentId="item.subMenuItemList.find(x => x.code === 'CONTRACT_COMPLETE').viewComponentId"/></v-list-item-title>
          </v-list-item-content>
         </v-list-item>

         <v-list-item  v-if='item.subMenuItemList.find(x => x.code === "CONTRACT_PAYMENTS")'>
           <v-list-item-content>
             <v-list-item-title><Payment v-bind:contractId="item.id"  v-bind:viewComponentId="item.subMenuItemList.find(x => x.code === 'CONTRACT_PAYMENTS').viewComponentId"/></v-list-item-title>
          </v-list-item-content>
         </v-list-item>

          <v-list-item v-if='item.subMenuItemList.find(x => x.code === "CONTRACT_HISTORY")'>
           <v-list-item-content>
             <v-list-item-title><HistoryContract v-bind:contractId="item.id" v-bind:viewComponentId="item.subMenuItemList.find(x => x.code === 'CONTRACT_HISTORY').viewComponentId"/></v-list-item-title>
          </v-list-item-content>
         </v-list-item>

         <v-list-item v-if='item.subMenuItemList.find(x => x.code === "CONTRACT_VIEW")'>
          
            <router-link :to="{ path: '/update/'+ item.id  , query: { status: 'view' } }">
  
           <v-list-item-content>
             <v-list-item-title><v-icon>folder_special</v-icon>Baxış</v-list-item-title>
          </v-list-item-content>
            </router-link>
         </v-list-item>

           <v-list-item v-if='item.subMenuItemList.find(x => x.code === "CONTRACT_EDIT")'>
               
          
          <router-link :to="{ path: '/update/'+ item.id  , query: { status: 'update', prSt: item.processStatus, contType: item.contractTypeDescription } }">
           <v-list-item-content>
             <v-list-item-title><v-icon>edit</v-icon><span>Redaktə</span></v-list-item-title>
          </v-list-item-content>
          </router-link>
         </v-list-item>

           <v-list-item v-if='item.subMenuItemList.find(x => x.code === "CONTRACT_SEND")'>
           <v-list-item-content>
             <v-list-item-title><Send v-bind:contractId="item.id"  v-bind:viewComponentId="item.subMenuItemList.find(x => x.code === 'CONTRACT_SEND').viewComponentId"/></v-list-item-title>
          </v-list-item-content>
         </v-list-item>

          <v-list-item v-if='item.subMenuItemList.find(x => x.code === "CONTRACT_RECOVER")'>
           <v-list-item-content>
             <v-list-item-title><RecoverContract v-bind:contractId="item.id"  v-bind:viewComponentId="item.subMenuItemList.find(x => x.code === 'CONTRACT_RECOVER').viewComponentId"/></v-list-item-title>
          </v-list-item-content>
         </v-list-item>


           <v-list-item v-if='item.subMenuItemList.find(x => x.code === "CONTRACT_DELETE")'>
           <v-list-item-content>
             <v-list-item-title><Delete v-bind:contractId="item.id"  v-bind:viewName="name"/></v-list-item-title>
          </v-list-item-content>
         </v-list-item>

        <v-list-item v-if='item.subMenuItemList.find(x => x.code === "CONTRACT_DOCUMENTS")'>
           <v-list-item-content>
             <v-list-item-title><ContractDocument v-bind:contractId="item.id"  v-bind:router="router"  v-bind:viewComponentId="item.subMenuItemList.find(x => x.code === 'CONTRACT_DOCUMENTS').viewComponentId"/></v-list-item-title>
          </v-list-item-content>
         </v-list-item>

        <v-list-item v-if='item.subMenuItemList.find(x => x.code === "CONTRACT_PAYMENT_PLAN")'>
           <v-list-item-content>
             <v-list-item-title><ContractPaymentPlans v-bind:contractId="item.id"  v-bind:router="router"  v-bind:viewComponentId="item.subMenuItemList.find(x => x.code === 'CONTRACT_PAYMENT_PLAN').viewComponentId"/></v-list-item-title>
          </v-list-item-content>
         </v-list-item>
      </v-list>
    </v-menu>
    </td>
     
   </tr>
      <tr v-if="filteredagreements.length==0" class="norecord">
          <td colspan="7"><div class="norecord-c">Məlumat yoxdur...</div></td>
        </tr>
   </tbody>
    </template>
     </v-data-table> 
      <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
  </div>

</template>
 
<script > 
import ErrorDialog from '../popups/ErrorDialog';
import ContractDocument from '../popups/ContractDocument';
import ContractPaymentPlans from '../popups/ContractPaymentPlans';
import Payment from '../popups/Payment';
import CancelContract from '../popups/CancelContract';
import CompleteContract from '../popups/CompleteContract';
 import HistoryContract from '../popups/HistoryContract';
import Send from '../popups/Send';
import RecoverContract from '../popups/RecoverContract';
import Delete from '../popups/Delete';
import { mapGetters, mapActions } from "vuex";
 
  export default {
     props:["proccess"], 
      components:{ErrorDialog,ContractDocument,ContractPaymentPlans,Payment, CancelContract,CompleteContract, Send , HistoryContract,RecoverContract, Delete },
 
    data () {
      return {   
      selectedRow: null,
        name:"contracts",
        router: this.$router.currentRoute.path,
        errorMsg:'', 
        dialog_err:false,
         itemType: null 
      }
    },
     methods: {
    ...mapActions(["fetchContracts", "getContract" ]),
        rowSelect(idx) { this.selectedRow = idx; },

            customSort: function(items, index, isDesc) {
             items.sort((a, b) => {
           if (index[0]=='contractDate') {
            var  dda= a[index].slice(0,2);
              var mma= a[index].slice(3,5);
              var yya= a[index].slice(6,10);
              var adate=mma+'.'+dda+'.'+yya;

              var  ddb= b[index].slice(0,2);
              var mmb= b[index].slice(3,5);
              var yyb= b[index].slice(6,10);
              var bdate=mmb+'.'+ddb+'.'+yyb;

              if (!isDesc[0]) { 
                 return new Date(bdate).getTime() - new Date(adate).getTime();
               } else { 
                return new Date(adate).getTime() - new Date(bdate).getTime();
            }
            
          }
           else {
          if (!isDesc[0]) {
            return a[index] < b[index] ? -1 : 1;
          } else {
            return b[index] < a[index] ? -1 : 1;
          }
        }
          
      });
      return items;
    }
         
        },

        
  computed: {...mapGetters(["allnewagreement","filteredagreements","formData","loginuser","closeOnContentClick"]),
    conType:
      {
        get () { return this.$store.getters.conType },
      },
        actionQueryString:function(){
           if(this.loginuser.user.menuList.find(x => x.code =="TOP_MENU").menuItemList.find(x => x.routePath ==this.router)){
             
          return this.loginuser.user.menuList.find(x => x.code =="TOP_MENU").menuItemList.find(x => x.routePath ==this.router).actionQueryString;
        
          }
         return false
         
          },

     

    headers(){
      if(this.router==='/archive'){
        return [
        { text: '№',   value: 'index',sortable:false },
        { text: 'Müqavilənin tipi',   value: 'contractTypeDescription'  },
        { text: 'Müqavilənin nömrəsi', value: 'contractNumber'   }, 
        { text: 'Satıcının A.S.A',   value: 'sellerPersonnelName'   },
        { text: 'Məsul şəxsin A.S.A',  value: 'currentProcessorPersonnelName'  },
        { text: 'Müqavilə tarixi',  value: 'contractDate'   },
        { text: 'Statusu',   value: 'processStatus',sortable:false  },
         { text: '',   value: 'more_vert',sortable:false },
      ] 
      }
      else{
         return [
        { text: '№',   value: 'index',sortable:false },
        { text: 'Müqavilənin tipi',   value: 'contractTypeDescription'  },
        { text: 'Müqavilənin nömrəsi', value: 'contractNumber'  }, 
        { text: 'Satıcının A.S.A',   value: 'sellerPersonnelName'  },
        { text: 'Məsul şəxsin A.S.A',  value: 'currentProcessorPersonnelName'  },
        { text: 'Müqavilə tarixi',  value: 'contractDate'   },
         { text: '',   value: 'more_vert',sortable:false },
      ];
    
      }
    }
     
  },
 
    created() {
     if(this.loginuser.user.menuList.find(x => x.code =="TOP_MENU").menuItemList.find(x => x.routePath ==this.router)){
         if(this.conType && this.conType!=100){
              this.fetchContracts(this.actionQueryString+'&&contractTypeId='+ this.conType)
          .catch(error => {  
             if(error.response.data.errorCode=='Session.NotFound'|| error.response.data.errorCode=='Session.IsExpired'){
                   this.$router.push({ path: '/' });   }  
     
                this.dialog_err=true;
                this.errorMsg=error.response.data;  
               })
         }
         else{
   this.fetchContracts(this.actionQueryString)
          .catch(error => {  
             if(error.response.data.errorCode=='Session.NotFound'|| error.response.data.errorCode=='Session.IsExpired'){
                   this.$router.push({ path: '/' });   }  
     
                this.dialog_err=true;
                this.errorMsg=error.response.data;  
               })
         }
     
         
       }
       else{
          var list = document.getElementById("mainMenu").firstElementChild;
         list.click();
       }
   
       }

  
}
</script>
<style  >
 
.custom-highlight-row{
  background: #fcf3b6!important;}
  .contract_page{
    height: 100%;
  
}
 .table-componenet .v-data-table{
    height: 100%;
  }
 .table-componenet .v-data-table__wrapper{
        height: calc(100% - 54px);
  }

.contract-table-design.table-componenet{
     height: calc(100% - 93px);
}
 
  .hr-table-design.table-componenet{
     height: calc(100% - 75px);
}


  .search-table-design.table-componenet{
     height: calc(100% - 145px);
}
  .users-table-design.table-componenet{
     height: calc(100% - 30px);
}
  
</style>
 