<template>
  <div>
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <template v-slot:activator="{ on }">
         <v-btn  icon   class='hrmorevert'
           text
          v-on="on">
          <v-icon>vpn_key</v-icon>
          <span>Şifrəni yenilə</span>
         </v-btn>
         
      </template>

      <v-card>
        <v-card-title class="modal-header">
          <span>İstifadəçilər > İstifadəçilər > Şifrəni yenilə</span>
          <v-spacer></v-spacer>
          
           <v-btn
            icon
            color="primary"
            text
            @click="dialog = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
       
        <v-card-text>
                      <v-row v-if="hideButtons">
                        <v-col  class="text-center" cols="12"   md="12" > 
                        <div style="padding-top:20px">{{name_user}} üçün yeni şifrə təyin olunsun?</div> 
                 </v-col> 
                       </v-row>
                         
                         
                        <v-row v-if="!hideButtons">
                         
                         <v-col cols="12"  md="12"   >
                           <div style="text-align: center; font-size: 20px; padding-top: 24px;">
                             <span>Yeni şifrə</span>
                              <span style="padding: 10px; border: 1px solid #d3cbcb;"> {{ps}} </span>
                           </div>
                        
                         </v-col> 
                         
                      </v-row>           
        </v-card-text>
          <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
        <v-divider></v-divider>

        <v-card-actions class='dialog-buttons'>
           <v-btn v-if="hideButtons"  @click="dialog = false"  class="btn-cancel" outlined>XEYR</v-btn> 
             <v-btn  v-if="hideButtons"   @click="  resetPassSubmit()" class="btn-add" >BƏLİ</v-btn> 
               <v-btn   v-if="!hideButtons"   @click=" hideButtons=true; dialog = false;" class="btn-add" >BAĞLA</v-btn> 
        </v-card-actions>
      
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
 import ErrorDialog from '../popups/ErrorDialog';
  export default {
      components:{ErrorDialog },
  props:['userId',"name_user" ],
    data () {
      return { 
        ps:'',
        formData:{},
        hideButtons:true,
        dialog: false,
         validationRules: [
        v => !!v || 'Sahəni boş buraxmaq olmaz!', 
         ],
         errorMsg:'',
        dialog_err:false,
          
      }
    },
    computed:{
      ...mapGetters(["allRoles","allusers", "secretuserlg" ]),
     

    },
    methods: {
     ...mapActions(["resetPassword"]),
   
     

       resetPassSubmit( ) {   
       
       this.formData.userToResetId=this.userId;
         this.resetPassword(this.formData)
            .then(response => {   
                this.ps=response.data.temporaryPasword;
                this.hideButtons=false;
          })
          .catch(error =>{ 
                  this.dialog=false; 
                   this.dialog_err=true;
                   this.errorMsg=error.response.data; 
         })
           
       }
    },
    
  }
</script>

<style>
 
.v-btn{
        text-transform: unset!important;
}
.modal-header{
        background-color: #14b6db!important;
    padding: 5px 15px!important;
}
.modal-header span{
        font-size: 14px;
    font-weight: normal;
    color: white;
}
.modal-header .v-icon{
    color: white;
    font-size: 20px;
}

.v-dialog .v-card__actions{
    justify-content: center!important;
}

.btn-cancel{
    border: solid 1px #2294f2;
    color: #2294f2;
     
}
.btn-add{
     background-color: #2294f2!important;
     color: white!important;

}
.dialog-buttons .v-btn{
  width: 115px!important;
   box-shadow: unset;
}
.dialog-buttons{
    padding: 25px 0px;
}
.hrmorevert span{
      align-self: center;
    font-size: 1rem;
    letter-spacing: normal;
}

.hrmorevert.v-btn.v-size--default {
  padding: 0px!important;
  color: #1976d2!important;
  height: auto!important;
}
</style>