<template>
  <div >
    <v-dialog
      v-model="dialog"
      width="700"
    >
      <template v-slot:activator="{ on }">
        
          <v-btn  class="orgDialog"
           text @click="getUnitType(); editTypeAction();clicked=false; moreM()"
          v-on="on">
          <span v-if="name==='fornew'">Əlavə et</span>
          <span v-if='name==="foredit"'>Redaktə et</span>
         </v-btn>
 

      </template>

      <v-card>
        <v-card-title
          class="modal-header"
           
        >
          <span v-if="name==='fornew'">Yeni</span>
          <span v-if='name==="foredit"'>Redaktə</span>
         
          <v-spacer></v-spacer>
          
           <v-btn
           icon
            color="primary"
            text
            @click="dialog = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
           
        </v-card-title>
          <v-form @submit="submitOrgUnit"  v-model="isFormValid" ref="form" >
        <v-card-text>
               <v-row>
              
                    <v-col cols="12"  md="6"   >
                         <v-text-field label="Email" type="email" :rules="validationEmailRules" placeholder="example@mail.ru" v-model="formData.emailAddress" required ></v-text-field>
                       
                         </v-col>    
                 
                 <v-col cols="12"  md="6"   >
                         <v-text-field label="Faks nömrəsi"  v-model="formData.faxNumber" required ></v-text-field>
                       
                         </v-col>
                <v-col cols="12"  md="6"   >
                         <v-text-field label="Telefon nömrəsi"    v-model="formData.phoneNumber" required ></v-text-field>
                       
                         </v-col>

                    <v-col cols="12"  md="6"   v-if="name=='fornew'"> 
                           <v-select v-model="formData.typeId"  label="Tipi" :items="unitTypes" item-value="id" item-text="description">
   
           </v-select>
            

                         </v-col>
                     
                         <v-col cols="12"  md="6"   >
                         <v-text-field label="Veb saytı"  v-model="formData.webAddress" required ></v-text-field>
                       
                         </v-col>
                           <v-col cols="12"  md="6"   >
                         <v-text-field  class="required" label="Təsviri" v-model="formData.description" required ></v-text-field>
                       
                         </v-col> 
             </v-row>
        </v-card-text> 
        <v-divider></v-divider> 
        <v-card-actions class='dialog-buttons'>
           <v-btn   @click="dialog = false; formData.formatpaymentDate=null; formData.paymentAmount=null"  class="btn-cancel" outlined>İMTİNA ET</v-btn> 
             <v-btn  :disabled="!formData.description||clicked"  type="submit" class="btn-add" >TƏSDİQ ET </v-btn>  
        </v-card-actions>
    </v-form>
      </v-card>
          <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
    </v-dialog>
  </div>
</template>
<script>
 
import {  mapActions,mapGetters } from "vuex"; 
 import ErrorDialog from '../popups/ErrorDialog';
  export default {
 props:["name","parentOrganizationUnitId","organizationId","editType", "typeId"],
 components:{ErrorDialog},
    data () {
      return {
        unitTypes:[],
        obj:{},
         formData:{ phoneNumber:'+994'},
         isFormValid:false,
         dialog: false,
         menu:false,
         errorMsg:'',
         dialog_err:false,
         clicked: false,
         validationRules: [ v => !!v || 'Sahəni boş buraxmaq olmaz!'],
         validationEmailRules: [
         v => /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(v)   || 'Email düzgün qeyd olunmayıb!', 
         ],
      }
    },
    methods:{
       reset() {   this.$refs.form.reset();   },
    ...mapActions(["updateOrgs","updateUnitOrgs","fetchOrganizationUnitType", "fetchMainOrgs","fetchUnitOrgs","fetchSubOrgs","addOrganizationUnit"]),
       getUnitType(){
         this.fetchOrganizationUnitType()
         .then(()=>{
            this.unitTypes=this.allorgUnitType.filter(x => x.parentId == this.typeId);
         })
       },
   addc(){
       var  element = document.querySelectorAll('.tf-nc~ul li');
     
       for (var i = 0; i < element.length; i++) {
               element[i].parentElement.parentElement.firstElementChild.classList.add("mystyle");
}
     },
        moreM(){
    document.querySelector('.more-menu').style.display='none'
      },
    submitOrgUnit(e){    
        e.preventDefault(); 

        if(this.name==='fornew'){

        this.formData.parentOrganizationUnitId=this.parentOrganizationUnitId;
         this.formData.organizationId=this.organizationId;
        this.addOrganizationUnit(this.formData)
        .then(()=>{  
         this.clicked = true;
         this.reset();
         this.fetchMainOrgs()
         .then(()=>{ 

          this.dialog=false;  
          this.addc(); 

         }) })
         .catch(error => {  
                this.dialog_err=true;
                this.errorMsg=error.response.data;  
         }) 
         
         }
           if(this.name==='foredit'){
             this.obj.address=this.formData.address;
             this.obj.description=this.formData.description;
             this.obj.emailAddress=this.formData.emailAddress;
             this.obj.faxNumber=this.formData.faxNumber;
             this.obj.phoneNumber=this.formData.phoneNumber;
             this.obj.webAddress=this.formData.webAddress;
             this.obj.typeId=this.formData.typeId;
             if(this.editType==='edit_main_org'){
             this.updateOrgs(this.obj)
             .then(()=>{  
               this.fetchMainOrgs();
                this.dialog=false; 
                   this.clicked = true;
              })  
             .catch(error => {  
                this.dialog_err=true;
                this.errorMsg=error.response.data;  
                 })    
             }
             else{
               this.obj.organizationUnitId=this.formData.id;
                 this.updateUnitOrgs(this.obj)
                   .then(()=>{  
                  this.fetchMainOrgs();
                 this.dialog=false; 
                    this.clicked = true;
              })  
             .catch(error => {  
                this.dialog_err=true;
                this.errorMsg=error.response.data;  
                 }) 
             }
           }
       },
          editTypeAction(){
            if(this.editType==="edit_main_org"){
              this.fetchMainOrgs()
              .then(()=>{
                this.formData=this.allmainOrgs.find(x => x.id == this.organizationId); 
              })
            }

            else if(this.editType==="edit_org_unit"){
                 this.fetchUnitOrgs()
                   .then(()=>{
                     this.formData=this.allOrgs.find(x => x.id == this.parentOrganizationUnitId);
                    
              })
            }
          }

     

        
     
},
     computed: { 
       ...mapGetters(["allorgUnitType","allmainOrgs","allOrgs","allsuborgs"]), 

     },
  
   created(){
            console.log(this.unitTypes);
   }
  }
</script>

 <style  >
 .orgDialog{
   min-width: auto!important;
 }
 .orgDialog span{
      font-size: 1rem;
    font-family: Segoe UI;
    letter-spacing: 0.5px;
 }
 </style>