 <template>
  <div>
    <v-dialog
      v-model="dialog" 
      width="700"
    >
      <template v-slot:activator="{ on }">
        
         <v-btn  icon   class='hrmorevert'
          text
          @click="getP();fetchPosition()"
           
          v-on="on">
          <v-icon>edit</v-icon>
          <span>Redaktə et</span>
         </v-btn>
         
      </template>

      <v-card>
        <v-card-title
          class="modal-header"
           
        >
          <span> İnsan resursları > Əməkdaşlar > Redaktə et</span>
          <v-spacer></v-spacer>
          
           <v-btn
           icon
            color="primary"
            text
            @click="dialog = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
          <v-form  @submit="addWorkerSubmit"  v-model="isFormValid" ref="form" >
        <v-card-text>
                   <v-row> 
                         <v-col cols="12"  md="4"   >
                         <v-text-field  class="required" :rules="validationRules" v-model="formData.name" label="Adı"   required ></v-text-field>
                   
                         </v-col>
                      
                         <v-col cols="12"  md="4"   >
                         <v-text-field class="required" :rules="validationRules"  v-model="formData.surname" label="Soyadı"   required ></v-text-field>
                     
                         </v-col>
                           
                            <v-col cols="12"  md="4"   >
                         <v-text-field  class="required" :rules="validationRules" v-model="formData.patronymic" label="Ata adı"   required ></v-text-field>
                        
                         </v-col>
                      
                          <v-col cols="12"  md="4"   >
                         <v-text-field class="required"   :rules="validationEmailRules"  v-model="formData.emailAddress" label="Email"  placeholder="example@mail.ru"  required ></v-text-field>
                        
                         </v-col>
                        <v-col cols="12"  md="4"> 
                         <v-menu
                              v-model="menu1"
                              :close-on-content-click="false"
                              :nudge-right="40"
                               transition="scale-transition"
                               offset-y
                               min-width="290px"
                          >
                          <template v-slot:activator="{ on }">
                             <v-text-field
                              :value="formData.birthDate" 
                              label="Doğum tarixi"
                              append-icon="event"
                               readonly
                              
                               v-on="on"  class="required"
                              ></v-text-field>
                          </template>
                         
                          <v-date-picker :max="yesterday"  :first-day-of-week="1" v-model="formatbirthdate" @input="menu1 = false"></v-date-picker>
                          </v-menu> 
                         </v-col>
                        <v-col cols="12"  md="4"> 
                         <v-menu
                              v-model="menu2"
                              :close-on-content-click="false"
                              :nudge-right="40"
                               transition="scale-transition"
                               offset-y
                               min-width="290px"
                          >
                          <template v-slot:activator="{ on }">
                             <v-text-field
                              :value="formData.entranceDate" 
                              label="İşə qəbul tarixi"
                              append-icon="event"
                               readonly
                               v-on="on"  
                                disabled
                               class="required"
                              ></v-text-field>
                          </template>
                         
                          <v-date-picker :max="nowDate"  :first-day-of-week="1"  v-model="formatenterancedate" @input="menu2 = false"></v-date-picker>
                          </v-menu>
                        
                         </v-col>
                         <v-col   cols="12"   md="4" > 
                           <v-select :rules="validationRules" class="required"  v-model="formData.positionId"    label="Vəzifəsi" :items="emptyP"  item-value="id" item-text="description">
                            
                             </v-select>   
                          
                        </v-col>
                        
                           <v-col cols="12"  md="4"   >
                         <v-text-field   v-model="formData.faxNumber" label="Faks nömrəsi"  ></v-text-field>
                        
                         </v-col>
                             <v-col cols="12"  md="4"   >
                         <v-text-field class="required"  :rules="validationRules" v-model="formData.phoneNumber" label="Telefon nömrəsi"   required ></v-text-field>
                        
                         </v-col>
                           <v-col cols="12"  md="4"   >
                         <v-text-field class="required" :rules="validationRules" v-model="formData.pinCode"  maxLength="7" label="FİN kodu"   required >
                                     <v-tooltip bottom  slot="append">
      <template v-slot:activator="{ on, attrs }">
        
         <v-icon v-on="on" color="primary" dark  v-bind="attrs">
                  forum
                </v-icon>
      </template>
     <div class="align-center">
        <v-img 
          class="shrink mr-2"
          contain
          src="../assets/tooltip.png"
          transition="scale-transition"
          width="500"
        />
     </div>
    </v-tooltip>
                         </v-text-field>
                        
                         </v-col>

                          

                          <v-col cols="12"  md="12"> 
                         <v-menu
                              v-model="menu21"
                              :close-on-content-click="true"
                              :nudge-right="40"
                               transition="scale-transition"
                               offset-y
                               min-width="290px"
                          >
                          <template v-slot:activator="{ on }">
                             <v-text-field
                              :value="obj.personnelPositionChangeDate" 
                              append-icon="event"
                               readonly
                               v-on="on"  
                               :placeholder="formData.name+' '+ formData.surname+' '+ formData.patronymic+' üçün vəzifə dəyişikliyi olunursa  bu sahəni mütləq doldurun!'" 
                                label="Vəzifə dəyişikliyi olunan tarix"  
                              ></v-text-field>
                          </template>
                         
                          <v-date-picker  :first-day-of-week="1"  v-model="formatpersonnelPositionChangeDate" @input="menu2 = false"></v-date-picker>
                          </v-menu>
                        
                         </v-col>
                      </v-row>
                 
          <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions class='dialog-buttons'>
           <v-btn   @click="dialog = false; "  class="btn-cancel" outlined>İMTİNA ET</v-btn> 
             <v-btn   :disabled="!isFormValid"   type="submit" class="btn-add" >TƏSDİQ ET</v-btn> 
             
        </v-card-actions>
    </v-form>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import ErrorDialog from '../popups/ErrorDialog';
import { mapGetters, mapActions } from "vuex";  
  export default {
    components:{ErrorDialog},
     props:["personelId"],
    data () {
      return {
          oldPos:'',
         nowDate: new Date().toISOString().slice(0,10),
        yesterday:new Date(new Date().setDate(new Date().getDate()-1)).toISOString().slice(0,10),
        date: new Date(),  
        obj:{},
        formData:{ phoneNumber:'+994'},
        errorMsg:'',
        dialog_err:false,
        dialog: false,
        menu1:false, 
        menu2:false,  
         menu21:false,  
        isFormValid:false, 
        validationRules: [
        v => !!v || 'Sahəni boş buraxmaq olmaz!', 
         ],
               validationEmailRules: [
         v => /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(v)  || 'Email düzgün qeyd olunmayıb!', 
         ],
         emptyP:[]
      }
    },

     computed:{
        formatbirthdate:
        {
            get() {
              if (this.formData.birthDate === undefined) { return null; }
              return  this.formData.birthDate.split('.').join('-').replace(/(\d{2})-(\d{2})-(\d{4})/, "$3-$2-$1"); }  ,
              set(value) { this.formData.birthDate = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },

         formatenterancedate:
        {
            get() {
              if (this.formData.entranceDate === undefined) { return null; }
              return  this.formData.entranceDate.split('.').join('-').replace(/(\d{2})-(\d{2})-(\d{4})/, "$3-$2-$1"); }  ,
              set(value) { this.formData.entranceDate = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },

          formatpersonnelPositionChangeDate:
        {
            get() {
              if (this.obj.personnelPositionChangeDate === undefined) { return null; }
              return  this.obj.personnelPositionChangeDate.split('.').join('-').replace(/(\d{2})-(\d{2})-(\d{4})/, "$3-$2-$1"); }  ,
              set(value) { this.obj.personnelPositionChangeDate = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },
    ...mapGetters(["allPosition","allworkers" ]),

        
      },
 
  methods: {
     ...mapActions(["fetchPosition", "updateWorker","fetchWorkers", "fetchPositionCombo" ]),
       reset() { this.$refs.form.reset();  }, 

        addWorkerSubmit(e) {    
        e.preventDefault(); 
        this.obj.personnelId=this.personelId;
        this.obj.birthDate=this.formData.birthDate;
        this.obj.emailAddress=this.formData.emailAddress;
        this.obj.entranceDate=this.formData.entranceDate;
        this.obj.faxNumber=this.formData.faxNumber;
        this.obj.name=this.formData.name;
        this.obj.surname=this.formData.surname;
        this.obj.pinCode=this.formData.pinCode; 
        this.obj.patronymic=this.formData.patronymic;
        this.obj.personnelPositionId=this.formData.positionId; 
        this.obj.phoneNumber=this.formData.phoneNumber;
        if(this.oldPos!=this.formData.positionId && this.obj.personnelPositionChangeDate==null ){
             this.dialog_err=true;
                this.errorMsg='Vəzifə dəyişikliyi olunan tarix boş ola bilməz!'; 
        }
        else{
        this.updateWorker(this.obj)
        .then(()=>{ 
         
         this.reset();
         this.fetchWorkers()
         .then(()=>{
            
         this.dialog=false; 
         })
        })
         .catch(error => {  
                this.dialog_err=true;
                this.errorMsg=error.response.data;  
         })
      }  
       },

 

getP(){ 
    this.fetchWorkers()
     .then(()=>{
             this.formData=this.allworkers.find(item => item.id === this.personelId); 
             this.oldPos=this.allworkers.find(item => item.id === this.personelId).positionId;
             this.fetchPositionCombo()
              .then((res)=>{  this.emptyP=res.data.filter(pos=>{return pos.personnelId === undefined || pos.personnelId==this.formData.id}); })
             })
  
     },
        },
     
 
  }  
</script>

<style scoped>
.v-btn{
        text-transform: unset!important;
}
.modal-header{
        background-color: #14b6db!important;
    padding: 5px 15px!important;
}
.modal-header span{
        font-size: 14px;
    font-weight: normal;
    color: white;
}
.modal-header .v-icon{
    color: white;
    font-size: 20px;
}

.v-dialog .v-card__actions{
    justify-content: center!important;
}

.btn-cancel{
    border: solid 1px #2294f2;
    color: #2294f2;
     
}
.btn-add{
     background-color: #2294f2!important;
     color: white!important;

}
.dialog-buttons .v-btn{
  width: 115px!important;
   box-shadow: unset;
}
.dialog-buttons{
    padding: 25px 0px;
}
</style>

