<template>
  <div>
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <template v-slot:activator="{ on }">
        
         <v-btn  icon   class='new_button'
          text
          v-on="on">
          <v-icon>create_new_folder</v-icon>
          <span>Yeni</span>
         </v-btn>
         
      </template>

      <v-card>
        <v-card-title
          class="modal-header"
           
        >
          <span> İstifadəçilər > İstifadəçi səlahiyyətləri > Yeni səlahiyyət yarat</span>
          <v-spacer></v-spacer>
          
           <v-btn
           icon
            color="primary"
            text
            @click="dialog = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
          <v-form  @submit="addRoleSubmit"  v-model="isFormValid" ref="form" >
        <v-card-text>
                   <v-row>
                      <v-col cols="12"  md="12"   >
                         <v-text-field  :rules="validationRules" v-model="formData.code" label="Kodu"   required ></v-text-field>
                   
                         </v-col>
                         <v-col cols="12"  md="12"   >
                         <v-text-field  :rules="validationRules" v-model="formData.description" label="Təsviri"   required ></v-text-field>
                   
                         </v-col>
                      
                          
                           <v-col   cols="12"   md="12" > 
                           <v-select :rules="validationRules"  v-model="formData.baseRoleId"    label="Baza rolu" :items="allRoles" item-value="id" item-text="description">
                            
                             </v-select>   
                        </v-col>
                             <v-col cols="12"  md="12" >
                        <v-checkbox
                              label="Ancaq özünə aid olanları göstər"
                              v-model="formData.reportOnlyRelated"
                         ></v-checkbox> 
                         
                    </v-col> 
                      </v-row>
                 
        </v-card-text>
   <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
        <v-divider></v-divider>

        <v-card-actions class='dialog-buttons'>
           <v-btn   @click="dialog = false; reset()"  class="btn-cancel" outlined>İMTİNA ET</v-btn> 
             <v-btn :disabled="!isFormValid"  type="submit" class="btn-add" >ƏLAVƏ ET</v-btn> 
        </v-card-actions>
    </v-form>

      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import ErrorDialog from '../popups/ErrorDialog';
import { mapGetters, mapActions } from "vuex";  
  export default {
    components:{ErrorDialog},
 
    data () {
      return {
        formData:{ 
        },
        errorMsg:'',
       dialog_err:false,
         dialog: false,
       
         isFormValid:false, 
         validationRules: [
        v => !!v || 'Sahəni boş buraxmaq olmaz!', 
         ],
      }
    },

     computed:{
        
    ...mapGetters(["allRoles" ]),
     
      },
 
  methods: {
     ...mapActions(["fetchRoles", "addRole"  ]),
       reset() { this.$refs.form.reset();  }, 

        addRoleSubmit(e) {   
        e.preventDefault(); 
        
           this.formData.loginAllowed=true;
        this.addRole(this.formData)
        .then(()=>{
         this.dialog=false; 
         this.fetchRoles();
         this.reset();
        })
         .catch(error => {  
             this.dialog_err=true;
                this.errorMsg=error.response.data; 
         })
        
       }
        },
     
  created() {
    this.fetchRoles(); 
  }
  }
</script>

 