<template>
<div class="table-componenet  hr-table-design">
    <v-data-table 
      :headers="headers"
      :items="allsellers"   
       item-key="personnelName" 
         :footer-props="{
        itemsPerPageText:'Məlumat sayı:',
           itemsPerPageOptions: [10,50,100, 1000], // -> Add this example
          
      }">
     
        <template v-slot:body="{ items }">
           <tbody>
          <tr :class="key === selectedRow ? 'custom-highlight-row' : ''" @click="rowSelect(key)" v-for="(item, key) in items" :key="item.name">
          <td >{{key+1}}</td>
          <td>{{ item.personnelName }}</td>
          <td>{{ item.personnelSurname}}</td>
          <td>{{ item.personnelPatronymic }}</td> 
          <td>{{ item.personnelPositionDescription }}</td>
          <td  style="width:20px"> 
          <Delete v-bind:id="item.id" v-bind:viewName="name"/>
          </td>
        </tr>
         <tr v-if="allsellers.length==0" class="norecord">
          <td colspan="6"><div class="norecord-c">Məlumat yoxdur...</div></td>
        </tr>
      </tbody>
    </template>
  </v-data-table > 
  </div>

</template>
 
<script >
 
import Delete from '../popups/Delete';
 import { mapGetters, mapActions } from "vuex";
 export default {
      components:{Delete},
     
    data () {
      return {
        selectedRow: null,
        name:'sellers',
         dialog: false,
        headers:[
        { text: '№',   value: 'index',sortable:false },
        { text: 'Adı',   value: 'personnelName'  },
        { text: 'Soyadı', value: 'personnelSurname'   }, 
        { text: 'Ata adı',   value: 'personnelPatronymic'   },
        { text: 'Vəzifəsi',   value: 'personnelPositionDescription'   },
        { text: 'Sil',  value: 'delete' ,sortable:false  },
         
      ]
       
      }
    },
      methods: {
    ...mapActions(["fetchSellers" ]),
      rowSelect(idx) { this.selectedRow = idx; },
 
        },
  computed: mapGetters(["allsellers","filteredworkers"]),
  created() {
    this.fetchSellers() 
     .catch(error => {  
        if(error.response.data.errorCode=='Session.NotFound'){ this.$router.push({ path: '/' });   }  
         })
  }
  
}
</script>