<template>
<div class="table-componenet hr-table-design">
    <v-data-table 
     :custom-sort="customSort"
      :headers="headers"
      :items="allworkers"   
       item-key="name" 
         :footer-props="{
        itemsPerPageText:'Məlumat sayı:',
           itemsPerPageOptions: [10,50,100, 1000], // -> Add this example
          
      }">
     
        <template v-slot:body="{ items }">
           <tbody>
          <tr :class="key === selectedRow ? 'custom-highlight-row' : ''" @click="rowSelect(key)" v-for="(item, key) in items" :key="key">
          <td >{{key+1}}</td>
          <td>{{ item.name }}</td> 
          <td>{{ item.surname }}</td>
          <td>{{ item.birthDate }}</td>
          <td>{{ item.positionDescription }}</td>
          <td>{{ item.entranceDate }}</td>
          <td>{{ item.leaveDate }}</td>
           <td>
             <v-menu v-if='item.leaveDate==null' offset-y :close-on-content-click='false' >
      <template v-slot:activator="{ on }">
       <v-btn v-on="on" icon>
        <v-icon>more_vert</v-icon>  
      </v-btn>
      </template>
      
      <v-list class='button_list'>
         <v-list-item>
          <NewUser v-bind:nameWorker = 'item.name'  v-bind:surnameWorker = 'item.surname' v-bind:personelId = 'item.id'  />
         </v-list-item>
          <v-list-item>
          <LeaveWorker v-bind:personelId = 'item.id'  />
         </v-list-item>
          <v-list-item>
          <UpdateWorker v-bind:personelId = 'item.id'  />
         </v-list-item>
      </v-list>
    </v-menu></td>
        </tr>
        <tr v-if="allworkers.length==0" class="norecord">
          <td colspan="8"><div class="norecord-c">Məlumat yoxdur...</div></td>
        </tr>
      </tbody>
    </template>
  </v-data-table> 
    <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
  </div>

</template>
 
<script >
import UpdateWorker from '../popups/UpdateWorker';
import NewUser from '../popups/NewUser';
import LeaveWorker from '../popups/LeaveWorker';
import ErrorDialog from '../popups/ErrorDialog';
import { mapGetters, mapActions } from "vuex";  
  export default {
     
    components:{NewUser,LeaveWorker,UpdateWorker,ErrorDialog},
    data () {
      return {
          errorMsg:'',
         dialog_err:false,
        dialog: false,
        selectedRow: null,
              headers:[
        { text: '№',   value: 'index',sortable:false },
        { text: 'Adı',   value: 'name'  },
        { text: 'Soyadı', value: 'surname'   }, 
        { text: 'Doğum tarixi',   value: 'birthDate'   },
        { text: 'Vəzifəsi',   value: 'positionDescription'   },
        { text: 'İşə qəbul tarixi',  value: 'entranceDate'   },
       { text: 'İşdən çıxma tarixi',  value: 'leaveDate',sortable:false  }, 
          { text: '',  value: 'more' ,sortable:false  }, 
      ]
          
      }
    },
      methods: {
    ...mapActions(["fetchWorkers", "deleteWorker"]),
  rowSelect(idx) { this.selectedRow = idx; },
               customSort: function(items, index, isDesc) {
             items.sort((a, b) => {
           if (index[0]=='entranceDate') {
            var  dda= a[index].slice(0,2);
              var mma= a[index].slice(3,5);
              var yya= a[index].slice(6,10);
              var adate=mma+'.'+dda+'.'+yya;

              var  ddb= b[index].slice(0,2);
              var mmb= b[index].slice(3,5);
              var yyb= b[index].slice(6,10);
              var bdate=mmb+'.'+ddb+'.'+yyb;

              if (!isDesc[0]) { 
                 return new Date(bdate).getTime() - new Date(adate).getTime();
               } else { 
                return new Date(adate).getTime() - new Date(bdate).getTime();
            }
          }
             else {
          if (!isDesc[0]) {
            return a[index] < b[index] ? -1 : 1;
          } else {
            return b[index] < a[index] ? -1 : 1;
          }
        }
      });
      return items;
    }
        },
  computed: mapGetters(["allworkers","filteredworkers"]),
  created() {
    this.fetchWorkers() 
     .catch(error => {  
        if(error.response.data.errorCode=='Session.NotFound'|| error.response.data.errorCode=='Session.IsExpired'){ this.$router.push({ path: '/' });   }
         
              this.dialog_err=true;
                this.errorMsg=error.response.data; 
        
         })
  }
  
}
</script>