     <v-row v-if='view_name==="update"' class='tab-buttons'>
           <v-col cols="12"  md="4" class='left-bt'>
              <v-btn  @click="$router.go(-1)" outlined>İMTİNA ET</v-btn> 
           </v-col>
           <v-spacer></v-spacer>
            <v-col class="text-right right-bt" cols="12"  md="4"> 
                      
              <v-btn type="submit" class="btn-next" >REDAKTƏ ET</v-btn> 
             </v-col>
   </v-row>  
    <v-row  v-if='view_name==="view"'  class='tab-buttons'>
          
           <v-spacer></v-spacer>
            <v-col v-if="notif!='note'" class="text-right right-bt" cols="12"  md="4"> 
              <v-btn  @click="$router.go(-1)" class="btn-next" >GERİYƏ QAYIT</v-btn> 
             </v-col>
   </v-row> 
 
 </v-container>
         </v-form>
            <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
  </v-card>
 </div>
</template>

<script> 
import ErrorDialog from '../popups/ErrorDialog';
import { mapGetters, mapActions } from "vuex";  
  export default {
    components:{ErrorDialog},   
    data () {
      return {
        id:this.$route.params.id,
        view_name:this.$route.query.status,
          pr_st:this.$route.query.prSt,
           contType:this.$route.query.contType,
             notif:this.$route.query.notif,
        errorMsg:'',
       
            saleType:null, 
         valid: false,
         firstname: '',
         lastname: '',
       formFields:[],
         subformFields:[],
        dialog_err:false,
        menu15: false,
        menu25: false,
        menu35: false,
        menu45: false,
        menu55: false,
        menu65: false,

         discountAmount:null,
         discountPercent:null,
         salePrice:null
      }
    },

      computed: { 
           saleTypeDis(){
        if(this.saleType==0){
          return true
        }
         
          return false
        
       },

            saleTypeVal(){
        if(this.saleType==0 || this.saleType==1){
          return true
        }
         
          return false
        
       },
         formatcontractDate:
        {get(){
           if (this.formData.contractDate === undefined) { return null; }
          return  this.formData.buyer.contractDate },
            
              set(value) { this.formData.contractDate = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },
         formatexecutiveOrderDate:
        {get(){
           if (this.formData.executiveOrderDate === undefined) { return null; }
          return  this.formData.buyer.executiveOrderDate },
            
              set(value) { this.formData.executiveOrderDate = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },
         formatauctionDate:
        {get(){
           if (this.formData.auctionDate === undefined) { return null; }
          return  this.formData.buyer.auctionDate },
           
              set(value) { this.formData.auctionDate = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },
         formatrentContractDate:
        {get(){ 
            
          if (this.formData.rentContractDate === undefined) { return null; }

           return  this.formData.buyer.rentContractDate 
           },
          
              set(value) { this.formData.rentContractDate = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },
         formatproclamationDate:
        {     get(){
            if (this.formData.buyer.proclamationDate === null) { return null; }
          return  this.formData.buyer.proclamationDate
           }, 
              set(value) { this.formData.soldAsset.proclamationDate = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },

        formatpowerOfAttorneyDate:
        {    get(){
           if (this.formData.powerOfAttorneyDate === undefined) { return null; }
          return  this.formData.buyer.powerOfAttorneyDate },
       
              set(value) { this.formData.buyer.powerOfAttorneyDate = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },

        
       ...mapGetters(["allformContract","allcontracttype","formData","allcontractliability","allsellers","alltown","allregion","allcountry" ]),
      
      
   
       
  },

    methods:{
           

          ...mapActions(["setContractViewedByUser","getForms","fetchContracts", "getContract", "fetchSellers","fetchContType","fetchContractLiability","fetchCity","fetchCountry","fetchRegion", "updateContract","getForms"]),
          setConViewedByUser(){
               if(this.view_name==="view"){
                 this.setContractViewedByUser(this.id)
                    .catch(error =>{ 
                 if(error.response.data.errorCode=='Session.NotFound'|| error.response.data.errorCode=='Session.IsExpired'){
                   this.$router.push({ path: '/' });   } 
                this.dialog_err=true;
                this.errorMsg=error.response.data;
                   
         })

               }
               else{
                  return false;
               }
          },

         
          updateContractSubmit(e) {   
         e.preventDefault();
     
              this.formData.id=this.id; 
              if(this.formData.buyer.identityDocumentSerial){
                this.formData.buyer.identityDocumentSerial.toUpperCase();
              }
              this.formData.sellerId=this.formData.seller.id;
              this.formData.debt.discountAmount=this.discountAmount; 
              this.formData.debt.discountPercent=this.discountPercent;  
              this.formData.debt.salePrice=this.salePrice;
              this.formData.formName="ContractForm."+ this.getContType(this.formData.contractTypeId);
                    for(var key in this.formData) {
             
              if(this.formData[key] === "") {
               delete this.formData[key]
        }
    }

             for(var key1 in this.formData.buyer) {
              if(this.formData.buyer[key1] === "") {
               delete this.formData.buyer[key1]
        }
    }
            for(var key2 in this.formData.soldAsset) {
              if(this.formData.soldAsset[key2] === "") {
               delete this.formData.soldAsset[key2]
        }
    }
           for(var key3 in this.formData.debt) {
              if(this.formData.debt[key3] === "") {
               delete this.formData.debt[key3]
        }
    }
            this.updateContract(this.formData)
              .then(()=>{ this.$router.go(-1); })
              .catch(error =>{ 
                 if(error.response.data.errorCode=='Session.NotFound'|| error.response.data.errorCode=='Session.IsExpired'){
                   this.$router.push({ path: '/' });   } 
                this.dialog_err=true;
                this.errorMsg=error.response.data;
                   
         })

            
        },

      
       
     getContTypeTitle(id){   
                    if(this.allcontracttype.find(x => x.id == id)){
                  return  this.allcontracttype.find(x => x.id == id).description
                  }
                      
                  },

                getContType(id){    
                         return  this.allcontracttype.find(x => x.id == id).code; 
                      
                  },
               
                getFormData(){
                  var id=this.formData.contractTypeId;
                  if (this.allformContract && this.allformContract.find(x => x.name === `ContractForm.${this.getContType(id)}`)&&this.allformContract.find(x => x.name === `ContractForm.${this.getContType(id)}`).formFieldList!= null  ){
                  this.formFields= this.allformContract.find(x => x.name === `ContractForm.${this.getContType(id)}`).formFieldList;
 
                      }
                         else {
                                
                     return false
                   }
                      },

                  checkField(name){
                    if (this.formFields!= null){
                  return this.formFields.find(x => x.name == name);
                   }
                     else {
                                
                     return false
                   }
                  },

                checkFieldBuyer(name,t){
                      if (this.checkField('buyer')!= null ){
                  return this.checkField('buyer').subFieldList.find(x => x.name == name).allowedValueSet.includes(t)
                      }

                     else {
                               
                     return false
                   }
                  },

                   checkFieldMandatory(name){
                    if (this.checkField(name)!= null){
                   return this.checkField(name).mandatory;
                   }

                     else {
                                
                     return false
                   } },


                   checkEditable(name){
                    if (this.checkField(name)!= null  ){
                   return this.checkField(name).editable;
                   } 
                     else { return false  }
                      },

                  checkFieldChilds(parentName,childName){
                   
              if (this.formFields.find(x => x.name === parentName)!= null && this.formFields.find(x => x.name === parentName)!== 'undefined'){ 
                 
                   return this.formFields.find(x => x.name === parentName).subFieldList.find(x => x.name === childName); 
                  
              }
                   else {
                           
                     return false
                   }
                  },


            checkFieldSubChilds(parentName,childName, subChild){
              if (this.checkFieldChilds(parentName,childName).subFieldList!= null && this.checkFieldChilds(parentName,childName)!= null  ){ 
                   return this.checkFieldChilds(parentName,childName).subFieldList.find(x => x.name === subChild); 
              }
                   else {
                                
                     return false
                   }
                  },

                   checkFieldChildsByType(name){
                      if (this.checkFieldChilds('buyer',name)!= null && this.formData!= null && this.formData.buyer!= null && this.formData.buyer.type!= null&&this.checkFieldChilds('buyer',name).visibleBasedOnFieldValueSet!= null ){ 
                   return this.checkFieldChilds('buyer',name).visibleBasedOnFieldValueSet.includes(this.formData.buyer.type); 
       
                   } 
                   
                   else {
                                 
                     return false
                    }
                   
                   }
    
       
  },

    watch : {
              discountAmount:function(val) {
                  this.discountAmount = val;
                  if(this.salePrice && val  && this.saleType==1 && this.view_name==="update" && this.saleTypeVal){
                 
                  this.discountPercent = Math.round((val * 100/this.salePrice)*100)/100;
                  }
               },

               discountPercent: function (val) {
                   this.discountPercent = val
                    if(this.salePrice && val && this.saleType==0 && this.view_name==="update"&& this.saleTypeVal){ 
                      this.discountAmount =  Math.round((val*this.salePrice/100)*100)/100;
                    }
                    
               },
               salePrice: function (val) {
                   this.salePrice = val;
                    if(  this.discountPercent && this.view_name==="update" && this.saleTypeVal ){
                  this.discountAmount =  Math.round((val*this.discountPercent/ 100)*100)/100; 
                    }
               },
             
            },
    
 created(){
 
   this.getContract(this.id)
   .then(()=>{
        this.salePrice=this.formData.debt.salePrice;
         this.discountAmount=this.formData.debt.discountAmount; 
         this.discountPercent=this.formData.debt.discountPercent;  
   })
  
    this.fetchContType();
   this.fetchCity();
   this.fetchCountry();
   this.fetchRegion();
   this.fetchContractLiability();
   this.fetchSellers();
   this.setConViewedByUser()
   this.getForms()
     .then(()=>{  this.getFormData();   });  
  
 }
     
  }
</script>

<style > 
.tables-tab{
    box-shadow: none!important;
}
.view_disable div.v-input{
  pointer-events: none!important;
}


</style>