<template>
      <v-dialog
      v-model="dialog"
      width="350" 
    >
      <template v-slot:activator="{ on }"  >
           <v-btn v-on="on"  icon :class="{ hrmorevert: viewName==='contracts'}" @click="moreM()">
             <v-icon  color="red">delete</v-icon> <span  v-if='viewName==="contracts"'>Sil</span> </v-btn>

            
      </template>

      <v-card>
         <v-card-title class="modal-header">
          <span>Sil</span>
          <v-spacer></v-spacer>
          
           <v-btn
            icon
            color="primary"
            text
            @click="dialog = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
         
        <v-card-text>
               <v-row>
                  <v-col  class="text-center" cols="12"   md="12" > 
                        <div style="padding-top:20px">Silmək istədiyinizə əminsinizmi?</div> 
                 </v-col> 

                  <v-col   v-if='viewName==="contracts"'  class="cont-del text-center" cols="12"   md="12"  >
                        <v-checkbox 
                              label="Silinənlər siyahısında görünsün"
                              v-model="obj.switchOnlyStatus"
                         ></v-checkbox>  
                    </v-col> 
             </v-row>
        </v-card-text>
 
        <v-divider></v-divider>

        <v-card-actions class='dialog-buttons'>
           <v-btn     @click="dialog = false"   class="btn-cancel" outlined>XEYR</v-btn> 
             <v-btn v-if='viewName==="sellers"'  @click="deleteSeller(id)"   class="btn-add" >BƏLİ</v-btn> 
            <v-btn v-if='viewName==="dictionary"'  @click="deleteDictionary()"   class="btn-add" >BƏLİ</v-btn> 
             <v-btn v-if='viewName==="userrole"'  @click="delUser()"   class="btn-add" >BƏLİ</v-btn>
              <v-btn v-if='viewName==="contracts"'  @click="delContract()"   class="btn-add" >BƏLİ</v-btn>
            <v-btn v-if='viewName==="feeTarriffs"'  @click="deleteFeet()"   class="btn-add" >BƏLİ</v-btn>
              <v-btn v-if='viewName==="documents"'  @click="deleteDoc()"   class="btn-add" >BƏLİ</v-btn>
            <v-btn v-if='viewName==="payPlan"'  @click="deletePlan()"   class="btn-add" >BƏLİ</v-btn> 
        </v-card-actions>
    
      </v-card>
       <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
    </v-dialog>
    
</template>

<script>
import ErrorDialog from '../popups/ErrorDialog';
import { mapActions } from "vuex";  
  export default {
    components:{ErrorDialog},
   props:['id', 'viewName', 'slctedItem',"userId", 'contractId'],
    data () {
      return {
         dialog: false,
         obj:{ switchOnlyStatus:true},
         errorMsg:'',
        dialog_err:false,
      }
    },
      methods: {
    ...mapActions(["fetchPaymentPlan","deletePayPlan","deleteDocument","deleteSeller","deleteDictionaryItem", "deleteUserRoles","fetchUserRoles","deleteContract", "deleteFeeTariffs" ]),

       deleteDictionary() {  
        this.obj.dictionaryCode=this.slctedItem;
        this.obj.dictionaryItemId=this.id;
        this.deleteDictionaryItem(this.obj)
        .then( response=>{
                this.dialog=false;
                 return response
        })
         .catch(error => {  
                this.dialog_err=true;
                this.errorMsg=error.response.data; 
                    this.dialog=false; 
         })
        
        
       },

       delUser(){
         this.obj.userId=this.userId;
         this.obj.roleIds=this.id;
        this.deleteUserRoles(this.obj)
        .then(()=>{
          this.dialog=false;
           this.fetchUserRoles(this.userId);
        })
         .catch(error => {  
                this.dialog_err=true;
                this.errorMsg=error.response.data; 
                    this.dialog=false; 
         })
       },

     delContract(){
         this.obj.contractId=this.contractId;
        
        this.deleteContract(this.obj)
        .then(()=>{
          this.dialog=false;
           
        })
           .catch(error => {  
                this.dialog_err=true;
                this.errorMsg=error.response.data; 
                    this.dialog=false; 
         })
        
       },
      moreM(){
        if(document.querySelector('.more-menu')){
         document.querySelector('.more-menu').style.display='none'
         }
         },
         deleteDoc(){
         this.obj.contractId=this.contractId;
         this.obj.documentId=this.id;
     

        this.deleteDocument(this.obj)
        .then(()=>{
          this.dialog=false;
           
        })
         .catch(error => {  
                this.dialog_err=true;
                this.errorMsg=error.response.data; 
                    this.dialog=false; 
         })
       },
      deletePlan(){
         this.obj.contractId=this.contractId;
         this.obj.paymentPlanId=this.id;
     

        this.deletePayPlan(this.obj)
        .then(()=>{
              this.$emit('onSubmit');
          this.dialog=false;
              this.fetchPaymentPlan(this.contractId);
        })
         .catch(error => {  
           
                this.dialog_err=true;
                this.errorMsg=error.response.data; 
                    this.dialog=false; 
         })
       },
       deleteFeet(){
           
        this.deleteFeeTariffs(this.id)
        .then(()=>{
          this.dialog=false;
           
        })
         .catch(error => {  
                this.dialog_err=true;
                this.errorMsg=error.response.data; 
                    this.dialog=false; 
                    
         })
       },
        },
   
  
}
</script>
<style>
.cont-del .v-input__control{
  margin:auto!important;
}
</style>