<template>
  <div >
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <template v-slot:activator="{ on }">
        
           <v-btn @click="roleById();fetchRoles()" class="edit-but" color="#2294f2"><v-icon v-on="on">edit</v-icon> </v-btn>
      </template>

      <v-card>
        <v-card-title
          class="modal-header"
           
        >
          <span>Rolu redaktə et</span>
          <v-spacer></v-spacer>
          
           <v-btn
           icon
            color="primary"
            text
            @click="dialog = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
          <v-form  @submit="updateRoleSubmit"   ref="form" >
          <v-card-text>
                   <v-row>
                      <v-col cols="12"  md="12"   >
                         <v-text-field  :rules="validationRules" v-model="formData.code" label="Kodu"   required ></v-text-field>
                   
                         </v-col>
                         <v-col cols="12"  md="12"   >
                         <v-text-field  :rules="validationRules" v-model="formData.description" label="Təsviri"   required ></v-text-field>
                   
                         </v-col>
                      
                          
                           <v-col    cols="12"   md="12" > 
                           <v-select    v-model="baseRoleValue"    label="Baza rolu" :items="allRoles" item-value="id" item-text="description">
                         
                             </v-select>    
                        </v-col>  

                             <v-col cols="12"  md="12" >
                        <v-checkbox
                              label="Ancaq özünə aid olanları göstər"
                              v-model="formData.reportOnlyRelated"
                         ></v-checkbox> 
                         
                    </v-col> 
                 
                      </v-row>
                 
        </v-card-text>   
        <v-divider></v-divider>
  
        <v-card-actions class='dialog-buttons'>
           <v-btn   @click="dialog = false;"  class="btn-cancel" outlined>İMTİNA ET</v-btn> 
             <v-btn   type="submit" class="btn-add" >TƏSDİQ ET</v-btn> 
        </v-card-actions>
    </v-form>
     
      </v-card>
    </v-dialog>
       <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex"; 
 import ErrorDialog from '../popups/ErrorDialog';
  export default {
    components:{ErrorDialog},
  props:["roleId"],
    data () {
      return {
         formData:{  },
       obj:{},
         dialog: false,
         menu:false,
         validationRules: [ v => !!v || 'Sahəni boş buraxmaq olmaz!'],
        errorMsg:'',
       baseRoleValue:null,
         dialog_err:false,
      }
    },
    methods:{
...mapActions(["fetchRoles", "updateRole","fetchRoleById" ]),
  

         updateRoleSubmit(e) {   
          e.preventDefault();
                this.obj.roleId=this.roleId; 
                this.obj.code=this.formData.code;
                this.obj.description=this.formData.description;
                this.obj.reportOnlyRelated=this.formData.reportOnlyRelated;
                this.obj.loginAllowed=true;
                this.obj.baseRoleId=this.baseRoleValue;
                     
            this.updateRole(this.obj)
              .then(()=>{
                this.dialog=false; 
                this.fetchRoles(); 
                this.obj={};
              }) 
               .catch(error => {  
                this.dialog_err=true;
                this.errorMsg=error.response.data;  
         }) 
        },

        roleById(){
  this.fetchRoleById(this.roleId)
     .then((res)=>{
       this.formData=res.data; 
       if(res.data.baseRole){
       this.baseRoleValue=res.data.baseRole.id;
       }
     });
        }
  },
     computed: {
       ...mapGetters(["allRoles"]),  
     },
     
  
  }
</script>

<style scoped>
.v-btn{
        text-transform: unset!important;
}
.modal-header{
        background-color: #14b6db!important;
    padding: 5px 15px!important;
}
.modal-header span{
        font-size: 14px;
    font-weight: normal;
    color: white;
}
.modal-header .v-icon{
    color: white;
    font-size: 20px;
}
.new-payment{
  text-align: center!important;
}
.v-dialog .v-card__actions{
    justify-content: center!important;
}

.btn-cancel{
    border: solid 1px #2294f2;
    color: #2294f2;
     
}
.edit-but{
     color: #2294f2!important;
}
.btn-add{
     background-color: #2294f2!important;
     color: white!important;
     text-transform: uppercase!important;
}

.btn-new-p{
      padding: 0px 15px!important;
}
.dialog-buttons .v-btn{
  width: 115px!important;
   box-shadow: unset;
}
.dialog-buttons{
    padding: 25px 0px;
}
</style>