  <template>
<div class="contract_page">
  <template>      
    <v-row class='searchdiv filterdiv' align="center">
     
           <v-col  cols="12" sm="6" md="2"  >
                          <v-menu
                              v-model="menu1"
                              :close-on-content-click="false"
                              :nudge-right="40"
                               transition="scale-transition"
                               offset-y
                               min-width="290px"
                          >
                          <template v-slot:activator="{ on }">
                             <v-text-field
                               :value="formData.contractDateBegin "
                                clearable 
                                 @click:clear ="formData.contractDateBegin=''"
                                label="Başlanğıc tarix"
                                append-icon="event"
                                readonly
                               v-on="on" 
                               outlined
                              ></v-text-field>
                          </template>
                          <v-date-picker  :first-day-of-week="1"   v-model="formatcontractDateBegin" @input="menu1 = false"></v-date-picker>
                          </v-menu>
                        
                       </v-col>
                         <v-col  cols="12" sm="6" md="2"  >
                          <v-menu
                              v-model="menu2"
                              :close-on-content-click="false"
                              :nudge-right="40"
                               transition="scale-transition"
                               offset-y
                               min-width="290px"
                          >
                          <template v-slot:activator="{ on }">
                             <v-text-field
                               :value="formData.contractDateEnd"
                                 
                                @click:clear ="formData.contractDateEnd=''"
                                clearable 
                                label="Son tarix"
                                append-icon="event"
                                readonly
                               v-on="on" 
                               outlined
                              ></v-text-field>
                          </template>
                          <v-date-picker  :first-day-of-week="1" v-model="formatcontractDateEnd" @input="menu2 = false"></v-date-picker>
                          </v-menu> 
                       </v-col>
<v-col  cols="12"   md='3'>
        
           <v-select  class="contract_type" outlined label="Müqavilənin tipi" :items="filterArrayType" item-value="id" item-text="description" v-model="formData.contractTypeIdSearch"> </v-select>
   
      </v-col>
                      <v-col cols="12"  md="2"   >
                         <v-text-field outlined label="Müqavilənin nömrəsi"  v-model="formData.contractNumberSearchText"   ></v-text-field>
                      
                         </v-col>
                   

        <v-col  cols="12"   md='3'>
        
           <v-select  outlined label="Müqavilənin mərhələsi" :items="filterArray" item-value="code" item-text="description" v-model="formData.processStatus"> </v-select>
   
      </v-col>
      
       
                          <v-col   cols="12"   md="2" > 
                          <v-text-field outlined label="Obyektin ünvanı"   required v-model="formData.soldAssetAddressAddressSearchText"></v-text-field>
                     </v-col>

     <v-col   cols="12"   md="2" > 
                          <v-text-field outlined label="Obyektin adı"   required v-model="formData.soldAssetNameSearchText"></v-text-field>
                     </v-col>
                        <v-col cols="12"  md="3"  >
                            <div class="buyerinfo">
                             <input class="left-input" placeholder="Alıcının adı"   v-model="formData.buyerNameSearchText"> 
                             <input class="left-input" placeholder="Soyadı"    v-model="formData.buyerSurnameSearchText"> 
                             <input class="left-input" placeholder="Ata adı"    v-model="formData.buyerPatronymicSearchText"> 
                             </div>
                         </v-col>
 <v-col cols="12"  md="2"   >
                         <v-text-field outlined label="Qeydiyyat №"  v-model="formData.registrationNumberSearchText" required ></v-text-field>
                         
                         </v-col>
                            <v-col cols="12"  md="2" class="debitorbox" >
                        <v-checkbox
                              label="Yalnız borcu olanları göstər"
                              v-model="formData.filterDebtorsOnly"
                         ></v-checkbox> 
                    </v-col> 
                            <v-col cols="12"  md="1" >
                          <v-btn   class="btn-add" style=" width: 100%!important;" @click="submitSearchedData()">AXTAR</v-btn>  
                    </v-col> 
                    
    </v-row>
</template>
<template>
  <div class="table-componenet search-table-design">
    
    <v-data-table 
    :custom-sort="customSort"
      :headers="headers"
      :items="allsearchedContract"   
       item-key="contractNumber" 
         :footer-props="{
        itemsPerPageText:'Məlumat sayı:',
           itemsPerPageOptions: [10,50,100, 1000], // -> Add this example
          
      }"
        
    >
           <template v-slot:body="{ items }">
           <tbody>
          <tr :class="key === selectedRow ? 'custom-highlight-row' : ''" @click="rowSelect(key)" v-for="(item, key) in items" :key="item.name">
          <td >{{key+1}}</td>  
          <td  style="width:23%">{{ item.contractTypeDescription }}</td>
          <td>{{ item.contractNumber }}</td> 
          <td>{{ item.sellerPersonnelName+' '+ item.sellerPersonnelSurname +' '+item.sellerPersonnelPatronymic }}</td>
          <td>{{  item.currentProcessorPersonnelName+' '+item.currentProcessorPersonnelSurname+' '+item.currentProcessorPersonnelPatronymic }}</td>
           <td>{{ item.contractDate }}</td> 
          <td style="color:#ca892f;">{{ $t(item.processStatus) }}</td>
           <td v-if="item.subMenuItemList"><v-menu offset-y :close-on-content-click='closeOnContentClick' content-class="more-menu">
      <template v-slot:activator="{ on }">
       <v-btn  @click="getContract(item.id)" v-on="on" icon>
        <v-icon>more_vert</v-icon>  
      </v-btn>
      </template>
      <v-list class='button_list'>
          <v-list-item  v-if='item.subMenuItemList.find(x => x.code === "CONTRACT_CANCEL")'>
           <v-list-item-content>
             <v-list-item-title><CancelContract v-bind:closeOnContentClick="closeOnContentClick" v-bind:contractId="item.id" v-bind:viewComponentId="item.subMenuItemList.find(x => x.code === 'CONTRACT_CANCEL').viewComponentId"/></v-list-item-title>
          </v-list-item-content>
         </v-list-item>

           <v-list-item  v-if='item.subMenuItemList.find(x => x.code === "CONTRACT_COMPLETE")'>
           <v-list-item-content>
             <v-list-item-title><CompleteContract v-bind:contractId="item.id"  v-bind:viewComponentId="item.subMenuItemList.find(x => x.code === 'CONTRACT_COMPLETE').viewComponentId"/></v-list-item-title>
          </v-list-item-content>
         </v-list-item>

         <v-list-item  v-if='item.subMenuItemList.find(x => x.code === "CONTRACT_PAYMENTS")'>
           <v-list-item-content>
             <v-list-item-title><Payment v-bind:contractId="item.id"  v-bind:viewComponentId="item.subMenuItemList.find(x => x.code === 'CONTRACT_PAYMENTS').viewComponentId"/></v-list-item-title>
          </v-list-item-content>
         </v-list-item>

          <v-list-item v-if='item.subMenuItemList.find(x => x.code === "CONTRACT_HISTORY")'>
           <v-list-item-content>
             <v-list-item-title><HistoryContract v-bind:contractId="item.id" v-bind:viewComponentId="item.subMenuItemList.find(x => x.code === 'CONTRACT_HISTORY').viewComponentId"/></v-list-item-title>
          </v-list-item-content>
         </v-list-item>

         <v-list-item v-if='item.subMenuItemList.find(x => x.code === "CONTRACT_VIEW")'>
          
            <router-link :to="{ path: '/update/'+ item.id  , query: { status: 'view' } }">
  
           <v-list-item-content>
             <v-list-item-title><v-icon>folder_special</v-icon>Baxış</v-list-item-title>
          </v-list-item-content>
            </router-link>
         </v-list-item>

           <v-list-item v-if='item.subMenuItemList.find(x => x.code === "CONTRACT_EDIT")'>
               
          
          <router-link :to="{ path: '/update/'+ item.id  , query: { status: 'update', prSt: item.processStatus, contType: item.contractTypeDescription  } }">
           <v-list-item-content>
             <v-list-item-title><v-icon>edit</v-icon><span>Redaktə</span></v-list-item-title>
          </v-list-item-content>
          </router-link>
         </v-list-item>

           <v-list-item v-if='item.subMenuItemList.find(x => x.code === "CONTRACT_SEND")'>
           <v-list-item-content>
             <v-list-item-title><Send v-bind:contractId="item.id"  v-bind:viewComponentId="item.subMenuItemList.find(x => x.code === 'CONTRACT_SEND').viewComponentId"/></v-list-item-title>
          </v-list-item-content>
         </v-list-item>

          <v-list-item v-if='item.subMenuItemList.find(x => x.code === "CONTRACT_RECOVER")'>
           <v-list-item-content>
             <v-list-item-title><RecoverContract v-bind:contractId="item.id"  v-bind:viewComponentId="item.subMenuItemList.find(x => x.code === 'CONTRACT_RECOVER').viewComponentId"/></v-list-item-title>
          </v-list-item-content>
         </v-list-item>


           <v-list-item v-if='item.subMenuItemList.find(x => x.code === "CONTRACT_DELETE")'>
           <v-list-item-content>
             <v-list-item-title><Delete v-bind:contractId="item.id"  v-bind:viewName="name"/></v-list-item-title>
          </v-list-item-content>
         </v-list-item>

        <v-list-item v-if='item.subMenuItemList.find(x => x.code === "CONTRACT_DOCUMENTS")'>
           <v-list-item-content>
             <v-list-item-title><ContractDocument v-bind:contractId="item.id"   v-bind:viewComponentId="item.subMenuItemList.find(x => x.code === 'CONTRACT_DOCUMENTS').viewComponentId"/></v-list-item-title>
          </v-list-item-content>
         </v-list-item>

        <v-list-item v-if='item.subMenuItemList.find(x => x.code === "CONTRACT_PAYMENT_PLAN")'>
           <v-list-item-content>
             <v-list-item-title><ContractPaymentPlans v-bind:contractId="item.id"   v-bind:viewComponentId="item.subMenuItemList.find(x => x.code === 'CONTRACT_PAYMENT_PLAN').viewComponentId"/></v-list-item-title>
          </v-list-item-content>
         </v-list-item>
      </v-list>
    </v-menu>
    </td>
        </tr>
         <tr v-if="allsearchedContract.length==0" class="norecord">
          <td colspan="8"><div class="norecord-c">Məlumat yoxdur...</div></td>
        </tr>
      </tbody>
    </template>
  </v-data-table> 
   <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/> 
  </div>
  </template>
  </div>
</template>

<script>
import ErrorDialog from '../popups/ErrorDialog';
import ContractDocument from '../popups/ContractDocument';
import ContractPaymentPlans from '../popups/ContractPaymentPlans';
import Payment from '../popups/Payment';
import CancelContract from '../popups/CancelContract';
import CompleteContract from '../popups/CompleteContract';
 import HistoryContract from '../popups/HistoryContract';
import Send from '../popups/Send';
import RecoverContract from '../popups/RecoverContract';
import Delete from '../popups/Delete';
import { mapGetters, mapActions } from "vuex"; 

 
export default {
  name: 'Newagreement',
  
     data () {
      return { 
           contt:[{
            code: [],
            description: "Hamısı",
            id: 100,
            parentId: null,
          readonly: true
            }
        ],
              conttype:[{
            code: "Hamısı",
            description: "Hamısı",
            id: [],
            parentId: null,
          readonly: true
            }
        ],
          filterArray:[],
          filterArrayType:[],
           selectedRow: null,
          name:"contracts",
            //  formData:{},
             errorMsg:'', 
        dialog_err:false,
          menu1:false,
          menu2:false,
                     headers:[
       { text: '№',   value: 'index',sortable:false },
        { text: 'Müqavilənin tipi',   value: 'contractTypeDescription'  },
        { text: 'Müqavilənin nömrəsi', value: 'contractNumber'   }, 
        { text: 'Satıcının A.S.A',   value: 'sellerPersonnelName'   },
        { text: 'Məsul şəxsin A.S.A',  value: 'currentProcessorPersonnelName'  },
        { text: 'Müqavilə tarixi',  value: 'contractDate'   },
        { text: 'Statusu',   value: 'processStatus',sortable:false  },
         { text: '',   value: 'more_vert',sortable:false }, 
      ] 
      }
    },
 components:{ErrorDialog,ContractDocument,ContractPaymentPlans,Payment, CancelContract,CompleteContract, Send , HistoryContract,RecoverContract, Delete },
 
 methods: {
    ...mapActions([ "fetchContractProcessState", "getSearchData","getContract", "fetchContType" ]),

  
       rowSelect(idx) { this.selectedRow = idx; },
         submitSearchedData(){ 
               for(var key in this.formData) {
                 if(this.formData[key] === "" ) {
               delete this.formData[key]
        }
    }
           
           
           this.getSearchData(this.formData)
            .catch(error => {   
             if(error.response.data.errorCode=='Session.NotFound'|| error.response.data.errorCode=='Session.IsExpired'){
                   this.$router.push({ path: '/' });   }  
     
                this.dialog_err=true;
                this.errorMsg=error.response.data;  
               })
         },
                     customSort: function(items, index, isDesc) {
             items.sort((a, b) => {
           if (index[0]=='contractDate') {
            var  dda= a[index].slice(0,2);
              var mma= a[index].slice(3,5);
              var yya= a[index].slice(6,10);
              var adate=mma+'.'+dda+'.'+yya;

              var  ddb= b[index].slice(0,2);
              var mmb= b[index].slice(3,5);
              var yyb= b[index].slice(6,10);
              var bdate=mmb+'.'+ddb+'.'+yyb;

              if (!isDesc[0]) { 
                 return new Date(bdate).getTime() - new Date(adate).getTime();
               } else { 
                return new Date(adate).getTime() - new Date(bdate).getTime();
            }
          }
             else {
          if (!isDesc[0]) {
            return a[index] < b[index] ? -1 : 1;
          } else {
            return b[index] < a[index] ? -1 : 1;
          }
        }
      });
      return items;
    }
 
        },

        
  computed: {
      ...mapGetters([ "loginuser","closeOnContentClick", "allcontractProcessState","allsearchedContract","allcontracttype"]),
   
     formData:
      {
        get () { return this.$store.getters.comboData },
        set (value) { this.$store.commit('updateCombo', value) }
      },
      
         formatcontractDateBegin:
        {
            get() {
              if (this.formData.contractDateBegin === undefined) { return null; }
              return  this.formData.contractDateBegin.split('.').join('-').replace(/(\d{2})-(\d{2})-(\d{4})/, "$3-$2-$1"); }  ,
              set(value) { this.formData.contractDateBegin = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },

           formatcontractDateEnd:
        {
            get() {
              if (this.formData.contractDateEnd === undefined) { return null; }
              return  this.formData.contractDateEnd.split('.').join('-').replace(/(\d{2})-(\d{2})-(\d{4})/, "$3-$2-$1"); }  ,
              set(value) { this.formData.contractDateEnd = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },
       
  },

    created() {
       this.fetchContractProcessState()
       .then(()=>{
         this.filterArray=this.contt.concat(this.allcontractProcessState); 
       });
        this.fetchContType()
       .then(()=>{
         this.filterArrayType=this.conttype.concat(this.allcontracttype); 
       }) 

       
      
       } 
  
 
}
</script>

<style >
.contract_type .v-menu__content{
  right: 0px!important;
}
.left-input{
    float: left;
    width: 31%;
    text-align: center;
}
.left-input:first-child{ 
    width: 38%;
}
.buyerinfo{
        border: 1px solid #c1bcbc;
    display: block;
    height: 32px;
    padding: 3px;
    border-radius: 4px;
}
.searchdiv .v-text-field__details{
    display: none!important
}

.debitorbox .v-input{
    margin-top: 0px!important;
}

.debitorbox .v-messages{
    display: none!important;
}

/* .searchdiv .col-12{
    width:20%!important;
    max-width:20%!important;
} */
</style>
 