<template>
  <div>
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <template v-slot:activator="{ on }">
        
      
          <v-btn  icon  v-on="on"  title="Şifrəni dəyiş" >
        <v-icon>person_outline</v-icon>
      </v-btn>
      </template>

      <v-card>
        <v-card-title
          class="modal-header"
           
        >
          <span>Şifrəni dəyiş</span>
          <v-spacer></v-spacer>
          
           <v-btn
           icon
            color="primary"
            text
            @click="dialog = false;reset();"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
          <v-form   @submit="changePasswordSubmit" v-model="isFormValid" ref="form" >
        <v-card-text>
               <v-row>
                   <v-col cols="12"  md="12"   >
                         <v-text-field 
                          autocomplete="new-password"
                           :append-icon="show ? 'visibility_off' : 'visibility'"
                            :type="show ? 'text' : 'password'"
                            v-model="formData.currentPassword"
                             label="Cari şifrə"    
                              @click:append="show = !show"  required ></v-text-field>
                          
                         </v-col>
                 
                       <v-col cols="12"  md="12"   >
                         <v-text-field
                          autocomplete="new-password"
                           :append-icon="show1 ? 'visibility_off' : 'visibility'"
                           :type="show1 ? 'text' : 'password'"
                          v-model="formData.newPassword"
                           label="Yeni şifrə"  
                            @click:append="show1 = !show1" required 
                            ></v-text-field>
                          
                         </v-col>
                  
             </v-row>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions class='dialog-buttons'>
           <v-btn   @click="dialog = false;  reset();"  class="btn-cancel" outlined>İMTİNA ET</v-btn> 
             <v-btn :disabled="!isFormValid"  type="submit" class="btn-add" >TƏSDİQ ET</v-btn> 
        </v-card-actions>
    </v-form>
      </v-card>
    </v-dialog>
         <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>

  </div>
</template>
<script>
import { mapActions } from "vuex"; 
 import ErrorDialog from '../popups/ErrorDialog';
  export default {
     props:["contractId"], 
     components:{ ErrorDialog},
    data () {
      return {
          show: false,
      show1: false,
         formData:{   },
         isFormValid:false,
               errorMsg:'',
            dialog:false,
             dialog_err:false,
             menu:false,
         validationRules: [ v => !!v || 'Sahəni boş buraxmaq olmaz!'],
      }
    },
    methods:{
...mapActions(["changePassword" ]),
  reset() {
        this.$refs.form.reset();
        
        
      },
changePasswordSubmit(e){
    e.preventDefault();  
        this.changePassword(this.formData)
        .then(()=>{
           this.dialog=false;  
           this.reset(); 
        })
          .catch(error => {  
                this.dialog_err=true;
                this.errorMsg=error.response.data;  
         })
}
}, 
  }
</script>

 