<template>
      <v-dialog
      v-model="dialog"
      width="800" 
    >
      <template v-slot:activator="{ on }"  >
           <v-btn v-on="on" class='hrmorevert' @click="moreM();fetchContractDocuments(contractId)">
             <v-icon >file_copy</v-icon>
             <span>Sənədlər </span>
             </v-btn>
      </template>

      <v-card>
         <v-card-title class="modal-header">
          <span>Sənədlər</span>
          <v-spacer></v-spacer>
          
           <v-btn
            icon
            color="primary"
            text
            @click="dialog = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
         
        <v-card-text>
         
                
                                      
        
                            <v-row>

                               <v-col   cols="12"   md="12"  v-if="allDocuments.length === 0" class="norecord">
          <div style="text-align:center"> Sənəd əlavə olunmayıb...</div>
        </v-col>
                  <v-col  v-if="allDocuments.length != 0"   cols="12"   md="12" > 
               
                     
                   <div class="table-componenet">
  <v-simple-table>
    <template v-slot:default>
      <thead>
        <tr>
          <th id='1' class="text-center">№</th>
          <th id='1' class="text-center">Məsul şəxsin A.S.A</th> 
          <th id='2'  class="text-center">Faylın adı</th>
          <th id='3'  class="text-center">Təsviri</th> 
              
          <th  v-if="router!='/deletedcontracts'&&router!= '/archive'" id='6'  class="text-center">Sil</th>
            <th id='6'  class="text-center">Yüklə</th>
        </tr>
      </thead>
      <tbody>
       
        
        <tr v-for="(item, index) in allDocuments" :key="item.id">
          <td>{{ index+1 }}</td>
          <td>{{ item.uploadedByPersonnelName + ' '+item.uploadedByPersonnelSurname+' '+item.uploadedByPersonnelPatronymic }}</td>
          <td>{{ item.originalFileName}}</td> 
          <td>{{ item.description}}</td> 
          <td  v-if="router!='/deletedcontracts'&&router!= '/archive'" style="width:20px"> 
              <Delete v-bind:id="item.id" v-bind:viewName="name"  v-bind:contractId="contractId" />  
          </td>
           <!-- <td v-if="item.originalFileName && (item.originalFileName.split('.').pop()==='pdf'||item.originalFileName.split('.').pop()==='txt'||item.contentType.split('/').shift()==='image'||item.contentType.split('/').shift()==='xls')"  style="width:20px"> 
             <IframeDoc v-bind:id="item.id" v-bind:contractId="contractId"/>  
          
          </td> -->
          <td  class="down_file"><a target="_blank" :href='`http://192.168.65.249:8080/Contracts/${contractId}/Documents/${item.id}/File`'><v-icon>vertical_align_bottom</v-icon></a></td>
    
        </tr>  
      </tbody>
    </template>
  </v-simple-table> 
 
  </div> 
 
                 </v-col> 
    
       
             </v-row> 
            <v-col  cols="12"   md="12" class="new_role_but">
    <v-dialog
      v-model="dialog_new"
      width="500"
      v-if="router!='/deletedcontracts'&&router!= '/archive'"
    >
      <template v-slot:activator="{ on }">
        
          <v-btn  icon  
           text
          v-on="on">
         
           <span>SƏNƏD ƏLAVƏ ET</span> 
         </v-btn>
      </template>

      <v-card>
        <v-card-title
          class="modal-header"
           
        >
          <span>Yeni Sənəd</span>
          <v-spacer></v-spacer>
          
           <v-btn
           icon
            color="primary"
            text
            @click="dialog_new = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
            <v-card-text>
         
           <v-row>
            
                  <v-col  class="text-center inputfile" cols="12"    md="12" > 
                       <input   multiple style="display:none;" ref="fileInput0" type="file" @change="onFileSelected"> 
                       <v-btn  class="file_button"  @click="$refs.fileInput0.click()"   label="File input"> {{fname}}</v-btn>
                 </v-col> 
                      <div class="clerafix"></div>
                  <v-col cols="12" md="12">
         <v-textarea class="txtarea" 
          solo
          name="input-7-4"
           label="Təsviri"
          v-model="description" 
          
        ></v-textarea> 
             </v-col>
            </v-row> 
        <v-divider></v-divider> 
     
   <v-card-actions class='dialog-buttons'>
           <v-btn     @click="selectedFile=null; description=''; fname='Faylı seçin';dialog_new = false;"   class="btn-cancel" outlined>IMTINA ET</v-btn> 
             <v-btn  @click="onUpload()" :disabled="fname==null ||fname=='Faylı seçin'"  class="btn-add" >ƏLAVƏ ET</v-btn> 
             
        </v-card-actions>
        </v-card-text>
             <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
      </v-card>
        
    </v-dialog>
  </v-col>
             
        </v-card-text>

        <v-divider></v-divider>

       
     <v-card-actions class='dialog-buttons'>
           <v-btn   @click="dialog = false;"  class="btn-cancel" outlined>BAĞLA</v-btn> 
            
        </v-card-actions>
      </v-card>
    </v-dialog>
</template>

<script>
// import IframeDoc from '../popups/IframeDoc';
import ErrorDialog from '../popups/ErrorDialog';
 import Delete from '../popups/Delete';
 import { mapGetters, mapActions } from "vuex";
 export default {
      components:{Delete,ErrorDialog },
    props:["contractId", "router"],
   data(){
       return{
         downl_file:'',
         name:"documents",
            count:0,
            dialog_new:false,
            dialog: false,
            dialog_fayl: false,
            selectedFile:null,
            fname:'Faylı seçin', 
            description:'',
            errorMsg:'',
        dialog_err:false,  
        obj:{},      
       }
   },
   computed: { ...mapGetters(["allDocuments", "loginuser"]),
  
   },
   methods:{

        ...mapActions(["fetchContractDocuments", "addContractDocuments", "downloadContractDocument"]),
           onFileSelected(event){
            this.selectedFile=event.target.files[0];
            this.fname= event.target.files[0].name; 

          
      }, 
moreM(){
  document.querySelector('.more-menu').style.display='none'
},
      downloadFile(id){
        this.obj.contractId=this.contractId;
        this.obj.documentId=id;
        this.downloadContractDocument(this.obj)
         .then((response)=>{
               this.downl_file=response.data
               
               }) 
                .catch(error => {  
                this.dialog_err=true;
                this.errorMsg=error.response.data; 
                this.dialog_new=false; 
         })
      },
      onUpload(){
           
           const uploadedFileDatas = new FormData();
               
              uploadedFileDatas.append('uploadedDocumentData', new Blob([JSON.stringify( {description: this.description})], {type : 'application/json'}));  
              uploadedFileDatas.append('uploadedDocumentFile', this.selectedFile, this.selectedFile.fname);  

               uploadedFileDatas.contractId=this.contractId;
              this.addContractDocuments(uploadedFileDatas)
               .then(()=>{
               this.dialog_new=false;
               
                this.fetchContractDocuments(this.contractId);
                this.selectedFile=null;
                this.description="";
                this.fname='Faylı seçin';
               })  
                .catch(error => {  
                this.dialog_err=true;
                this.errorMsg=error.response.data; 
                this.dialog_new=false; 
         })

 
             
            },
            

       
   
   },
    created(){
     
    },
    
   
  
}
</script>

 <style  >
 .down_file a{
   text-decoration: none!important;
 }

 .down_file a i{
      color: #14b6db!important;
 }
.iframe_dialog img{
  width:100%!important;
   height:100%!important;
}
 #list .row{
   align-items: center;
 }

  .txtarea label{
    font-size: 12px!important;
 }
 .file_button{
       width: 100%!important;
    border: 1px solid #e4e4e3;
        font-size: 16px!important;
    letter-spacing: unset!important;
 
    color: rgba(0, 0, 0, 0.6);
    overflow: hidden;
 }
.file_add{ 
    margin-top: 10px;
    text-align: right;
}
.file_add button{
    background: #14b6db!important;
    color: white;}
 
#itmList{
   border: 1px solid #ecebeb;
   margin-bottom: 8px;
}

.toAdd{
  display: none;
}

.downloadFile{
      min-width: auto!important;
      color:#14b6db!important;
}
 </style>