<template>
<div class="contract_page">
  <Filterbar/>
  <div class="table-componenet">
  <v-simple-table>
    <template v-slot:default>
      <thead>
        <tr>
          <th id='1' class="text-center">№</th>
          <th id='2'  class="text-center">Təsviri</th>
          <!-- <th id='3'  class="text-center">Əlavə sabit məbləğ</th> -->
          <th id='4'  class="text-center">Faiz</th>
          <th id='5'  class="text-center">Maksimum məbləğ</th>
            <th id='7'  class="text-center">Minimum məbləğ</th>   
          <th id='6'  class="text-center"></th>
        </tr>
      </thead>
      <tbody>
         <tr v-if="allfeeTarifs.length==0" class="norecord">
          <td colspan="7"><div class="norecord-c">Məlumat yoxdur...</div></td>
        </tr>
        <tr v-for="(item, index) in allfeeTarifs" :key="item.id">
          <td>{{ index+1 }}</td>
          <td>{{ item.description }}</td> 
          <!-- <td>{{ item.fixedAmount }}</td> -->
          <td>{{ item.feePercent }}</td>
          <td>{{ item.maxAmount }}</td>
          <td>{{ item.minAmount }}</td> 
         <td  style="width:20px"> 
          <Delete v-bind:id="item.id" v-bind:viewName="name"/>
          </td>
        </tr>
      </tbody>
    </template>
  </v-simple-table> 
  </div>
  </div>
</template>
 
<script >
 import Filterbar from '../components/Filterbar' 
 import Delete from '../popups/Delete';
 import { mapGetters, mapActions } from "vuex";
 export default {
      components:{Delete,Filterbar},
     
    data () {
      return {
        name:'feeTarriffs',
         dialog: false,  
      }
    },
      methods: {
    ...mapActions(["fetchFeeTariffs" ]),
 
        },
  computed: mapGetters(["allfeeTarifs"]),
  created() {
    this.fetchFeeTariffs()
      .catch(error => {  
        if(error.response.data.errorCode=='Session.NotFound'){ this.$router.push({ path: '/' });   }  
         }) 
  }
  
}
</script>