<template>
  <div>
    <v-dialog
      v-model="dialog"
      width="300"
    >
      <template v-slot:activator="{ on }">
        
          <v-btn  icon   class='hrmorevert'
           text
          v-on="on">
          <v-icon>unarchive</v-icon>
          <span>Ləğv et</span>
         </v-btn>
      </template>

      <v-card>
        <v-card-title
          class="modal-header"
           
        >
          <span>Ştat vahidinin ləğvi</span>
          <v-spacer></v-spacer>
          
           <v-btn
           icon
            color="primary"
            text
            @click="dialog = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
          <v-form @submit="cancelPersonnelPosSubmit" v-model="isFormValid" ref="form" >
        <v-card-text>
                  <v-row>
                 
                          <v-col cols="12"  md="12">
                          <v-menu
                              v-model="menu"
                              :close-on-content-click="false"
                              :nudge-right="40"
                               transition="scale-transition"
                               offset-y
                               min-width="290px"
                          >
                          <template v-slot:activator="{ on }">
                             <v-text-field
                              :value="formData.cancelDate"
                              label="Ləğv tarixi"
                              append-icon="event"
                               readonly
                               v-on="on"
                              ></v-text-field>
                          </template>
                          <v-date-picker :first-day-of-week="1" v-model="formatcancelDate" @input="menu = false"></v-date-picker>
                          </v-menu>
                        
                       </v-col> 
        
 
             </v-row>
              <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
        </v-card-text>

        <v-divider></v-divider>

      <v-card-actions class='dialog-buttons'>
           <v-btn   @click="dialog = false; reset()"  class="btn-cancel" outlined>İMTİNA ET</v-btn> 
             <v-btn :disabled="!isFormValid"  type="submit" class="btn-add" >LƏĞV ET</v-btn> 
        </v-card-actions>
          </v-form>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import ErrorDialog from '../popups/ErrorDialog';
import { mapGetters, mapActions } from "vuex";  
  export default {
    components:{ErrorDialog},
  props:["personnelPositionId"],
    data () {
      return {
         isFormValid:false,
         formData:{},
         dialog:false,
         menu:false, 
          errorMsg:'',
       dialog_err:false,
      }
    },

     computed:{
        ...mapGetters(["allpayments"]),
           formatcancelDate:
        {
            get() {
              if (this.formData.cancelDate === undefined) { return null; }
              return  this.formData.cancelDate.split('.').join('-').replace(/(\d{2})-(\d{2})-(\d{4})/, "$3-$2-$1"); }  ,
              set(value) { this.formData.cancelDate = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },
         
        
        },
     methods: {
     ...mapActions(["fetchPosition", "cancelPersonnelPos"]),
       
        reset() {
        this.$refs.form.reset(); 
      },

      
        cancelPersonnelPosSubmit(e) { 
        e.preventDefault(); 
        this.formData.personnelPositionId=this.personnelPositionId;
        this.cancelPersonnelPos(this.formData)
        .then(()=>{
           this.dialog=false; 
           this.fetchPosition();
          this.reset();
        })
          .catch(error => {  
                this.dialog_err=true;
                this.errorMsg=error.response.data;  
         })
       
       }
       
        },
     
 
  }
</script>

<style scoped>
.v-btn{
        text-transform: unset!important;
}
.modal-header{
        background-color: #14b6db!important;
    padding: 5px 15px!important;
}
.modal-header span{
        font-size: 14px;
    font-weight: normal;
    color: white;
}
.modal-header .v-icon{
    color: white;
    font-size: 20px;
}

.v-dialog .v-card__actions{
    justify-content: center!important;
}

.btn-cancel{
    border: solid 1px #2294f2;
    color: #2294f2;
     
}
.btn-add{
     background-color: #2294f2!important;
     color: white!important;

}
.dialog-buttons .v-btn{
  width: 115px!important;
   box-shadow: unset;
}
.dialog-buttons{
    padding: 25px 0px;
} 
</style>