<template>
  <div>
    <v-dialog
      v-model="dialog"
      width="600"
    >
      <template v-slot:activator="{ on }">
         <v-btn @click="fetchRoles();roleid=null" icon   class='hrmorevert'
           text
          v-on="on">
          <v-icon>create_new_folder</v-icon>
          <span>İstifadəçi yarat</span>
         </v-btn>
         
      </template>

      <v-card>
        <v-card-title class="modal-header">
          <span> Insan resursları > Əməkdaşlar > Yeni İstifadəçi yarat</span>
          <v-spacer></v-spacer>
          
           <v-btn
            icon
            color="primary"
            text
            @click="dialog = false;roleid=null"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
        <v-form @submit="addUserSubmit">
        <v-card-text>
                      <v-row>
                         <v-col cols="12"  md="6"   >
                            <v-text-field v-model="nameWorker" label="Adı" disabled  required ></v-text-field>
                          
                         </v-col> 
                         <v-col cols="12"  md="6"   >
                            <v-text-field label="Soyadı"  v-model="surnameWorker"  disabled  required ></v-text-field>
                  
                         </v-col> 
                       </v-row>
                            <v-row v-if="hideButtons">
                         <v-col   cols="12"   md="12" >  
                              <v-select :rules="validationRules" v-model="roleid"  label="İstifadəçi səlahiyyəti"  :items="allRoles" item-value="id" item-text="description"> </v-select>  
                        
                         </v-col> 
                        </v-row>
                         
                        <v-row v-if="!hideButtons">
                         <v-col   cols="12"   md="6" > 
                              <div style=" font-size: 15px;">
                              <span style="color: #baafaf;">İstifadəçi adı: </span>
                              <span style="padding: 5px;  "> {{lg}} </span>
                           </div>
                             
              
                         </v-col>
                         <v-col cols="12"  md="6">
                             <div style=" font-size: 15px;">
                             <span style="color: #baafaf;">Şifrə: </span>
                             <span style="padding: 5px;  "> {{ps}} </span>
                           </div>
                         </v-col> 
                         
                      </v-row>           
        </v-card-text>
          
        <v-divider></v-divider>

        <v-card-actions class='dialog-buttons'>
           <v-btn v-if="hideButtons"  @click="dialog = false;roleid=null"  class="btn-cancel" outlined>İMTİNA ET</v-btn> 
             <v-btn  v-if="hideButtons"   type="submit" class="btn-add" >ƏLAVƏ ET</v-btn> 
               <v-btn   v-if="!hideButtons"   @click="dialog = false; roleid=null" class="btn-add" >BAĞLA</v-btn> 
        </v-card-actions>
        </v-form>
      </v-card>
    </v-dialog>
      <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
  </div>
</template>
<script>
import ErrorDialog from '../popups/ErrorDialog';
import { mapGetters, mapActions } from "vuex";
import { mapFields } from 'vuex-map-fields';
  export default {
      components:{ErrorDialog },
  props:['nameWorker','surnameWorker', 'personelId'],
    data () {
      return {
        lg:'',
        ps:'',
       errorMsg:'',
        dialog_err:false,
        hideButtons:true,
        dialog: false,
         validationRules: [
        v => !!v || 'Sahəni boş buraxmaq olmaz!', 
         ],
        
          
      }
    },
    computed:{
      ...mapGetters(["allRoles","allusers", "secretuserlg" ]),
      ...mapFields([
      'newuser.roleid',   
       ]), 

    },
    methods: {
     ...mapActions(["fetchRoles","fetchUsers","addUser"]),
   
     

       addUserSubmit(e ) {   
       e.preventDefault();
         this.addUser({'personnelId':this.personelId, 'roleId':this.roleid})
            .then(response => {
              
                this.lg=response.data.logonId;
                this.ps=response.data.password;
                this.hideButtons=false;
                this.roleid=null
          })
          .catch(error =>{ 
                  this.dialog=false;
                    this.roleid=null
                this.dialog_err=true;
                this.errorMsg=error.response.data; 
             
         })
           
       }
    },
      created() {
    
      }
  }
</script>

<style>
 
.v-btn{
        text-transform: unset!important;
}
.modal-header{
        background-color: #14b6db!important;
    padding: 5px 15px!important;
}
.modal-header span{
        font-size: 14px;
    font-weight: normal;
    color: white;
}
.modal-header .v-icon{
    color: white;
    font-size: 20px;
}

.v-dialog .v-card__actions{
    justify-content: center!important;
}

.btn-cancel{
    border: solid 1px #2294f2;
    color: #2294f2;
     
}
.btn-add{
     background-color: #2294f2!important;
     color: white!important;

}
.dialog-buttons .v-btn{
  width: 115px!important;
   box-shadow: unset;
}
.dialog-buttons{
    padding: 25px 0px;
}
.hrmorevert span{
      align-self: center;
    font-size: 1rem;
    letter-spacing: normal;
}

.hrmorevert.v-btn.v-size--default {
  padding: 0px!important;
  color: #1976d2!important;
  height: auto!important;
}
</style>