 function sessionFunction(){ 

  if(router.currentRoute.path!='/'){

  store.dispatch('checkSession')
       .catch(() => {
        console.log('session out');
        if(router.currentRoute.path!='/'){
         router.push({ path: '/' });
        }
         
      })
    }
    else{
       return false
    }

}

setInterval(sessionFunction, 1080000);

