<template>
  <div class="d-flex flex-row login-page" >
    
    <div class='login-left'>
      <v-app-bar
      app 
      dark
      class="header hidden-md-and-up"
    >
      <div class="d-flex align-center ">
        <v-img
          class="shrink mr-2"
          contain
          src="../assets/logo.png"
          transition="scale-transition"
          width="40"
        />

          <div class="outline">
                 <div>AZƏRBAYCAN RESPUBLİKASININ İQTİSADİYYAT NAZİRLİYİ</div>
                 <div>YANINDA ƏMLAK MƏSƏLƏLƏRİ DÖVLƏT XİDMƏTİ</div>
          </div>
      </div>

      <v-spacer></v-spacer>
 
    </v-app-bar>
        <div class='lr-content'>   
          <div class='login-desc'>Sistemə daxili istifadəçilər tərəfindən giriş</div> 
                 <p style="background: #f80f0f; color: white;  max-width: 355px;">{{errorMessage.errorDescription}}</p>
                <v-form  @submit.prevent="login()">
                  <div v-if='!displaynewpass'>
                  <v-text-field
                    label="İstifadəçi adı" 
                   autocomplete="new-password"
                    type="text"
                    v-model='userss.loginId'
                    :rules="nameRules"
                  /> 
                  <v-text-field
                    id="password"
                    label="Şifrə" 
                   autocomplete="new-password"
                    type="password"
                     v-model='userss.password'
                     :rules="passwordRules"
                  />
                  </div>
                   <div v-if='displaynewpass'>
                      <div style="border: 1px solid #cececc; padding: 11px; font-weight: normal;" class='login-desc'>Zəhmət olmasa şifrəni dəyişin!</div> 
                       <v-text-field
                         :append-icon="show ? 'visibility_off' : 'visibility'"
                         :type="show ? 'text' : 'password'"
                          id="newpassword"
                          label="Yeni şifrəni təyin edin" 
                          autocomplete="new-password"  
                          v-model='newLog.newPassword'
                          
                           @click:append="show = !show"
                  />
                 
                     <v-text-field
                        :append-icon="show1 ? 'visibility_off' : 'visibility'"
                         id="renewpassword"
                         label="Yeni şifrəni təkrar daxil edin" 
                         autocomplete="new-password"
                         :type="show1 ? 'text' : 'password'"
                          v-model='newLog.renewpassword' 
                         :rules="passwordConfirmationRule" 
                         @click:append="show1 = !show1"
                  />  
                   </div> 
               
              <div class='d-flex login-buttons'>
                 
                <v-btn style="width: 100%!important;" type="submit" color="primary">DAXİL OL</v-btn>
              </div> 
               </v-form>
            </div>
    </div>
      <div class='login-right hidden-sm-and-down'> 
        <div class='lr-content'>
         <v-img
         
          class="login-logo"
          contain
          src="../assets/logo.png"
          transition="scale-transition"
          width="120"
        />

          <div class="outline">
                <div>AZƏRBAYCAN RESPUBLİKASININ</div>
                <div>İQTİSADİYYAT NAZİRLİYİ YANINDA</div>
                <div>ƏMLAK MƏSƏLƏLƏRİ DÖVLƏT XİDMƏTİ</div>
          </div> 
          </div>
  </div>
  </div>
</template>

<script>
 

export default {
  data(){
    return{
      newLog:{

      },
      userss:{},
       displaynewpass:false,
       show: false,
      show1: false,
      errorMessage:"",
        nameRules: [
        v => !!v || 'İstifadəçi adını daxil edin!', 
      ],
       passwordRules: [
        v => !!v || 'Şifrəni daxil edin!', 
      ] 
    }
  },
   computed: {
     
     passwordConfirmationRule() {
       return [(this.newPassword === this.renewpassword) || 'Password must match']
    },
 
  },
   methods: {
    login() { 
      this.$store.dispatch('retrieveToken', this.userss)
        .then(response => {
          if(response.data.user==null)
          {  
             this.displaynewpass=true;
             if(this.newLog.renewpassword!=null){

             this.$store.dispatch('permananentPass', this.newLog)
             .then(() => {
              
                    this.$router.push({ path: '/index' })
             

                })
               .catch(error => {  
               this.errorMessage=error.response.data;
         })

         }
          } 
          else
          {
              this.$router.push({ path: '/index' })
              return response
          }
          
         })
         .catch(error => {  
           if(error.response){
          
            this.errorMessage=error.response.data; 
            }
         })
    }
}
}
</script>

<style>
 .login-logo{
   display:inline-flex;
 }
.login-page{
  background: white;
          align-items: center;
          height: 100%;
         width: 100%;
}
    .login-left, .login-right{
      width:50%;
      text-align: center;
      display: inline-flex;
    }
  .login-right  
  {
          height: 100%; 
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.5);
    background-image: linear-gradient(to bottom, #2294f2, #164ddb);
    color: white;
    }

      .lr-content,  .login-right  .lr-content div{
      margin: auto;
    }

    .lr-content .outline{
      font-family: Segoe UI;
    font-size: 18px; 
    line-height: 1.76;
    letter-spacing: normal;
    text-align: center;
    color: #ffffff;
    padding-top: 20px;
    }
.login-left .v-input{
  padding-top: 30px
}
   input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active  {
    -webkit-box-shadow: 0 0 0 30px white inset !important;
}
.login-buttons {
    padding: 50px 0px;}
.login-buttons .v-btn{
    min-width: 140px!important;
}

.login-desc{
      font-family: Segoe UI;
    font-size: 20px;
    font-weight: 600; 
    font-style: normal;
    line-height: 1.23;
    letter-spacing: normal;
    color: #14b6db;
    padding-bottom: 30px;
}
.forgetpass{
  text-align: left
}
.forgetpass a{
      font-family: Segoe UI;
    font-size: 12px; 
    letter-spacing: normal;
    text-align: center;
    color: #606060!important; 
    text-decoration: unset;
}

.theme--dark.v-sheet{
  background-color: #2294F2!important;
}
.outline{
    font-size: 11px;
    padding-top: 5px;
        font-family: Segoe UI!important;
}
.login-left {
   
   padding-top: 70px;
}
@media only screen and (max-width: 959px)
 {
.login-left {
   width: 100%!important;
   
}}
</style>