<template>
  <div>
    <v-dialog
      v-model="dialog"
      width="700"
    >
      <template v-slot:activator="{ on }">
        
          <v-btn  icon   class='hrmorevert'
           text @click="moreM()"
          v-on="on">
          <v-icon>unarchive</v-icon>
          <span>Ləğv et</span>
         </v-btn>
      </template>

      <v-card>
        <v-card-title
          class="modal-header"
           
        >
          <span>Müqavilənin ləğvi</span>
          <v-spacer></v-spacer>
          
           <v-btn
           icon
            color="primary"
            text
            @click="dialog = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
          <v-form @submit="cancelContractSubmit" v-model="isFormValid" ref="form" >
        <v-card-text>
                  <v-row>
                 
                          <v-col cols="12"  md="4">
                          <v-menu
                              v-model="menu"
                              :close-on-content-click="false"
                              :nudge-right="40"
                               transition="scale-transition"
                               offset-y
                               min-width="290px"
                          >
                          <template v-slot:activator="{ on }">
                             <v-text-field
                              :value="formData.cancelDate"
                              label="Ləğv tarixi"
                              append-icon="event"
                               readonly
                               v-on="on"
                              ></v-text-field>
                          </template>
                          <v-date-picker :first-day-of-week="1" v-model="formatcancelDate" @input="menu = false"></v-date-picker>
                          </v-menu>
                        
                       </v-col>
                 

                   <v-col cols="12"  md="4"   >
                         <v-text-field label="Sərəncam №"  v-model="formData.proclamationNumber" required ></v-text-field>
                         
                         </v-col>

                         
                          <v-col cols="12"  md="4">
                          <v-menu
                              v-model="menu1"
                              :close-on-content-click="false"
                              :nudge-right="40"
                               transition="scale-transition"
                               offset-y
                               min-width="290px"
                          >
                          <template v-slot:activator="{ on }">
                             <v-text-field
                              :value="formData.proclamationDate"
                              label="Sərəncam tarixi"
                              append-icon="event"
                               readonly
                               v-on="on"
                              ></v-text-field>
                          </template>
                          <v-date-picker :first-day-of-week="1" v-model="formatproclamationDate" @input="menu1 = false"></v-date-picker>
                          </v-menu>
                        
                       </v-col>

                          <v-col cols="12" md="6">
        <v-textarea
          solo
          name="input-7-4"
          label="Ləğvin əsası"
           v-model="formData.base"
        ></v-textarea>
      </v-col>

                  <v-col cols="12" md="6">
        <v-textarea
          solo
          name="input-7-4"
          label="Ləğvin səbəbi"
          v-model="formData.reason"
        ></v-textarea>
      </v-col>

                      <v-col   cols="12"   md="12" > 
               
                     
                   <div class="table-componenet" v-if="allfiles.length != 0">
  <v-simple-table>
    <template v-slot:default>
      <thead>
        <tr>
          <th id='1' class="text-center">№</th>
          <th id='2'  class="text-center">Faylın adı</th>
          <th id='3'  class="text-center">Təsviri</th> 
              
          <th id='6'  class="text-center">Sil</th>
        </tr>
      </thead>
      <tbody>
         
        <tr v-for="(item, index) in allfiles" :key="item.id">
          <td>{{ index+1 }}</td>
          <td>{{ item.filename.name }}</td>
          <td>{{ item.file_des}}</td> 
          <td  style="width:20px"> 
            <v-btn @click="removeFile(index)" class="removeFile"><v-icon>clear</v-icon></v-btn>
          </td>
        </tr>
      </tbody>
    </template>
  </v-simple-table> 

  </div> 
 
                 </v-col> 
                 
                          <v-col   cols="12"   md="12" class="new_role_but"> 
                         <div style="text-align:center">
    <v-dialog
      v-model="dialog_new"
      width="500"
    >
      <template v-slot:activator="{ on }">
        
           <v-btn  icon  
           text
          v-on="on">
         
           <span>SƏNƏD ƏLAVƏ ET</span> 
         </v-btn>
      </template>

      <v-card>
        <v-card-title
          class="modal-header"
           
        >
          <span>Yeni Sənəd</span>
          <v-spacer></v-spacer>
          
           <v-btn
           icon
            color="primary"
            text
            @click="dialog_new = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
            <v-card-text>
         
           <v-row>
                  <v-col  class="text-center inputfile" cols="12"    md="12" > 
                       <input   multiple style="display:none;" ref="fileInput0" type="file" @change="onFileSelected"> 
                       <v-btn  class="file_button"  @click="$refs.fileInput0.click()"   label="File input"> {{fname}}</v-btn>
                 </v-col> 
                      <div class="clerafix"></div>
                  <v-col cols="12" md="12">
         <v-textarea class="txtarea" 
          solo
          name="input-7-4"
           label="Təsviri"
          v-model="description"
          
        ></v-textarea> 
             </v-col>
            </v-row> 
        <v-divider></v-divider> 
        <v-card-actions class='dialog-buttons'>
           <v-btn     class="btn-cancel" outlined>İMTİNA ET</v-btn> 
             <v-btn    type="submit" @click="filesArray()" class="btn-add" >TƏSDİQ ET</v-btn> 
        </v-card-actions>
  
        </v-card-text>
      </v-card>
         
    </v-dialog>
  </div>
                      
                     </v-col>
             </v-row>
        </v-card-text>

        <v-divider></v-divider>

      <v-card-actions class='dialog-buttons'>
           <v-btn   @click="dialog = false; reset()"  class="btn-cancel" outlined>İMTİNA ET</v-btn> 
             <v-btn :disabled="!isFormValid"  type="submit" class="btn-add" >LƏĞV ET</v-btn> 
        </v-card-actions>
          </v-form>
      </v-card>
    </v-dialog>
    <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";  
 import ErrorDialog from '../popups/ErrorDialog';
  export default {
    components:{ ErrorDialog},
    props:["contractId"], 
    data () {
      return {
          router:this.$router.currentRoute.path,
         description:'',
        allfiles:[],
        idForFile:1, 
        count:0,
        uploadedDocumentDatas:[],
        uploadedDocumentFiles:[],
        errorMsg:'', 
        dialog_err: false,
        dialog_new:false, 
        selectedFile:null,
        fname:'Faylı seçin',

         isFormValid:false,
         formData:{},
         dialog:false,
         menu:false,
         menu1:false,
      }
    },

     computed:{
        ...mapGetters(["loginuser"]),
           formatcancelDate:
        {
            get() {
              if (this.formData.cancelDate === undefined) { return null; }
              return  this.formData.cancelDate.split('.').join('-').replace(/(\d{2})-(\d{2})-(\d{4})/, "$3-$2-$1"); }  ,
              set(value) { this.formData.cancelDate = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },
         formatproclamationDate:
        {
            get() {
              if (this.formData.proclamationDate === undefined) { return null; }
              return  this.formData.proclamationDate.split('.').join('-').replace(/(\d{2})-(\d{2})-(\d{4})/, "$3-$2-$1"); }  ,
              set(value) { this.formData.proclamationDate = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },
          actionQueryString:function(){
          return this.loginuser.user.menuList.find(x => x.code =="TOP_MENU").menuItemList.find(x => x.routePath ==this.router).actionQueryString;
          
          },
        },
     methods: {
     ...mapActions(["fetchContracts", "cancelContract"]),
       
        reset() {
        this.$refs.form.reset(); 
      },
moreM(){
  document.querySelector('.more-menu').style.display='none'
},
      
       onFileSelected(event){
            this.selectedFile=event.target.files[0];
            this.fname= event.target.files[0].name;  
      },
            filesArray(){
             this.allfiles.push({
             id: this.idForFile,
             filename:this.selectedFile,
             file_des: this.description, 
        
         })
            this.uploadedDocumentFiles.push(this.selectedFile);
            this.uploadedDocumentDatas.push( {description: this.description});
        
          this.fname='Faylı seçin'
          this.selectedFile =null;
            this.description ='';
          this.idForFile++;
          this.dialog_new=false;
            },
          removeFile(index) {
      this.allfiles.splice(index, 1)
    },

        cancelContractSubmit(e) { 
        e.preventDefault(); 
        const contractCancelRequest = new FormData();
              contractCancelRequest.append('contractCancelRequest', new Blob([JSON.stringify(this.formData)], {type : 'application/json'})); 
              contractCancelRequest.append('uploadedDocumentDatas', new Blob([JSON.stringify(this.uploadedDocumentDatas)], {type : 'application/json'}));
               contractCancelRequest.contractId=this.contractId;
              for(var i=0;i<this.uploadedDocumentFiles.length;i++) {
               contractCancelRequest.append('uploadedDocumentFiles',  this.uploadedDocumentFiles[i],  this.uploadedDocumentFiles[i].name) 
                 }
        this.cancelContract(contractCancelRequest)
        .then(()=>{
           this.dialog=false; 
              this.fetchContracts(this.actionQueryString);
          this.reset();
           })  
                .catch(error => {  
                this.dialog_err=true;
                this.errorMsg=error.response.data; 
                this.dialog_new=false; 
         })
       
       }
       
        },
     
  created() {
   
  }
  }
</script>

<style scoped>
.v-btn{
        text-transform: unset!important;
}
.modal-header{
        background-color: #14b6db!important;
    padding: 5px 15px!important;
}
.modal-header span{
        font-size: 14px;
    font-weight: normal;
    color: white;
}
.modal-header .v-icon{
    color: white;
    font-size: 20px;
}

.v-dialog .v-card__actions{
    justify-content: center!important;
}

.btn-cancel{
    border: solid 1px #2294f2;
    color: #2294f2;
     
}
.btn-add{
     background-color: #2294f2!important;
     color: white!important;

}
.dialog-buttons .v-btn{
  width: 115px!important;
   box-shadow: unset;
}
.dialog-buttons{
    padding: 25px 0px;
} 
</style>