<template>
  <div >
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <template v-slot:activator="{ on }">
        
           <v-btn @click="getP();fetchFeeTariffs();" class="edit-but" color="#2294f2"><v-icon v-on="on">edit</v-icon> </v-btn>
      </template>

      <v-card>
        <v-card-title
          class="modal-header"
           
        >
          <span>Redaktə et</span>
          <v-spacer></v-spacer>
          
           <v-btn
           icon
            color="primary"
            text
            @click="dialog = false;fetchPaymentPlan( contractId);"
            
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
          <v-form  @submit="updatePaymentSubmit"  v-model="isFormValid" ref="form" >
        <v-card-text>
               <v-row>
               
              
                      <v-col cols="12"  md="12"   >
                         <!-- <v-text-field label="Ödəniş tarixi" placeholder="dd.mm.yyyy"  v-model="formData.deadline" required ></v-text-field>
                          -->
                          <v-menu
                              v-model="menu"
                              :close-on-content-click="false"
                              :nudge-right="40"
                               transition="scale-transition"
                               offset-y
                               min-width="290px"
                          >
                          <template v-slot:activator="{ on }">
                             <v-text-field
                              :value="formData.deadline"
                              label="Ödəniş tarixi"
                              append-icon="event"
                               readonly
                               v-on="on"
                              ></v-text-field>
                          </template>
                          <v-date-picker v-model="formatpaymentDeadline" @input="menu = false"></v-date-picker>
                          </v-menu>
                         </v-col>
                <v-col cols="12"  md="12"   >
                         <v-text-field label="Ödənişin miqdarı" disabled  v-model="formData.paymentAmount" required ></v-text-field>
                         
                         </v-col> 
                        
                         <v-col   cols="12"   md="12" v-if="formData.penaltyFeeTariff!=null"> 
                           <v-select v-model="formData.penaltyFeeTariff.id" :items="allfeeTarifs" item-value="id" item-text="description" label="Tipi"></v-select>
                           
                     </v-col>
                                          
                <v-col cols="12"  md="12"   >
                         <v-text-field label="Qeyd"  v-model="formData.note" required ></v-text-field>
                         
                         </v-col>
             </v-row>
        </v-card-text>
    <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
        <v-divider></v-divider>
 
        <v-card-actions class='dialog-buttons'>
           <v-btn   @click="dialog = false;  fetchPaymentPlan( contractId);"  class="btn-cancel" outlined>İMTİNA ET</v-btn> 
             <v-btn :disabled="!isFormValid"  type="submit" class="btn-add" >TƏSDİQ ET</v-btn> 
        </v-card-actions>
    </v-form>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex"; 
 import ErrorDialog from '../popups/ErrorDialog';
  export default {
    components:{ErrorDialog},
  props:["contractId", "paymentPlanId"],
    data () {
      return {
         formData:{ deadline:null },
         isFormValid:false,
         dialog: false,
         menu:false,
         validationRules: [ v => !!v || 'Sahəni boş buraxmaq olmaz!'],
           errorMsg:'',
        dialog_err:false,
      }
    },
    methods:{
...mapActions(["fetchPaymentPlan", "updatePaymentPlan", "getContract", "fetchFeeTariffs"]),
  getP(){ 
    this.fetchPaymentPlan(this.contractId)
     .then(()=>{
            this.formData=this.allpayplan.find(item => item.id === this.paymentPlanId); 
  console.log(this.allpayplan.find(item => item.id === this.paymentPlanId))
          })
  
  },

         updatePaymentSubmit(e) {   
              
          this.formData.penaltyFeeTariffId=this.formData.penaltyFeeTariff.id;
          e.preventDefault();
            this.formData.contractId=this.contractId;
            this.formData.paymentPlanId=this.paymentPlanId;
           
            this.updatePaymentPlan(this.formData)
              .then(()=>{ 
                 this.$emit('onSubmit');
                this.fetchPaymentPlan(this.contractId);
                this.getContract(this.contractId);
                this.fetchFeeTariffs();
                 this.dialog=false; 
              })  
             .catch(error => {  
                
                this.dialog_err=true;
                this.errorMsg=error.response.data;  
         })
        },
  },
     computed: {
       ...mapGetters(["allpayplan", "allfeeTarifs"]),  
      formatpaymentDeadline:
        {   
              get() { 
               if (this.formData.contractDate === undefined) { return null; }
                return  this.formData.deadline.toString()}  ,
              set(value) { this.formData.deadline = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },
     }, 
  }
</script>

<style scoped>
.v-btn{
        text-transform: unset!important;
}
.modal-header{
        background-color: #14b6db!important;
    padding: 5px 15px!important;
}
.modal-header span{
        font-size: 14px;
    font-weight: normal;
    color: white;
}
.modal-header .v-icon{
    color: white;
    font-size: 20px;
}
.new-payment{
  text-align: center!important;
}
.v-dialog .v-card__actions{
    justify-content: center!important;
}

.btn-cancel{
    border: solid 1px #2294f2;
    color: #2294f2;
     
}
.edit-but{
     color: #2294f2!important;
}
.btn-add{
     background-color: #2294f2!important;
     color: white!important;
     text-transform: uppercase!important;
}

.btn-new-p{
      padding: 0px 15px!important;
}
.dialog-buttons .v-btn{
  width: 115px!important;
   box-shadow: unset;
}
.dialog-buttons{
    padding: 25px 0px;
}
</style>