
<template>
    <v-row no-gutters>
      <v-col  cols="12"
        sm="3">
        <v-card class="menu-dictionary" >
         <v-list >
     
      <v-list-item-group color="primary">
        <v-list-item 
       
          v-for="(item, i) in alldictionary"
          :key="i"  
         @click="updatepos(i); ctrtype;"
          id="menuList"
        > 
          <v-list-item-content>
            <v-list-item-title   v-text="$t(item.code)"></v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        
      </v-list-item-group>
    </v-list>
  </v-card>
      </v-col>
       <v-col  cols="12"
        sm="9">
         <v-card  class="filter-dic">
            <div v-if="alldictionary.length" class='title-dic'>{{$t(slctdItm)}}</div>  
             <div class='button-dic'>
               <v-dialog
                 v-model="dialog"
                 width="500"
                 >
      <template v-slot:activator="{ on }" >
         <v-btn  icon   class='new_button'
           text
          v-on="on">
          <v-icon>create_new_folder</v-icon>
          <span>Yeni</span> 
         </v-btn>
         
      </template>

      <v-card>
        <v-card-title class="modal-header">
          <span v-if="alldictionary.length"> Soraqca > {{$t(slctdItm)}} > Yeni </span>
          <v-spacer></v-spacer>
          
           <v-btn
            icon
            color="primary"
            text
           
            @click=" formData={};dialog = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
        <v-form  @submit="addDicItemSubmit">
        <v-card-text >
                 <v-row>
                    <v-col cols="12"  md="12" >
                       <v-text-field v-model="formData.dictionaryItemCode" label="Adı"   required ></v-text-field>
                     </v-col> 
                     <v-col cols="12"  md="12" >
                       <v-text-field v-model="formData.dictionaryItemDescription" label="Təsviri"   required ></v-text-field>
                     </v-col> 
                           <v-col  v-if="slctdItmStr=='OrganizationUnitType'" cols="12"  md="12" >
                      
                     <v-select v-model="formData.dictionaryItemParentId" label="Ana tipi" :items="allorgUnitType" item-value="id" item-text="description"> </v-select>
       
                     </v-col> 
                     <v-col cols="12"  md="12" >
                        <v-checkbox
                              label="Silmək mümkün olmasın"
                              v-model="formData.readonly"
                         ></v-checkbox> 
                    </v-col> 
                 </v-row>                        
        </v-card-text>
         
        <v-divider></v-divider>

        <v-card-actions class='dialog-buttons'>
           <v-btn    @click="dialog = false; formData={}"  class="btn-cancel" outlined>İMTİNA ET</v-btn> 
             <v-btn   type="submit" class="btn-add" >ƏLAVƏ ET</v-btn>  
        </v-card-actions>
        </v-form>
      </v-card>
    </v-dialog>
            </div>
            <div class="clearfix"></div>
        </v-card>
        <v-card  class="table-dic">
         <div class="table-componenet">
  <v-simple-table>
    <template v-slot:default>
      <thead>
        <tr>
          <th id='1' class="text-center">№</th> 
          <th id='3'  class="text-center">Adı</th>
          <th id='4'  class="text-center">Təsviri</th>
       
          <th id='6'  class="text-center"></th>
        </tr>
      </thead>
      <tbody  class="table-contract">
        <tr v-for="(item, index) in alldicbycode" :key="index" >
          <td>{{ index+1 }}</td>
          <td>{{ item.code }}</td>
          <td>{{ item.description }}</td>
       
           <td  style="width:20px"> 
             <div v-if="!item.readonly">
             <Delete v-bind:id="item.id" v-bind:viewName="name"  v-bind:slctedItem="slctdItm" /> </div>
           
            </td> 
        </tr>
       
      </tbody>
    </template>
  </v-simple-table> 
  </div>
      </v-card>
      </v-col>
     <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
    </v-row>
  
</template>
<script>
 import ErrorDialog from '../popups/ErrorDialog';
import Delete from '../popups/Delete';
 import { mapGetters, mapActions } from "vuex";
 export default {
      components:{Delete,ErrorDialog},
     data () {
      return {
        name:"dictionary",
        dialog: false,
        formData:{ },
        title:'',
        position: 0,
        index: 0, 
        languageId: null,
           errorMsg:'', 
         dialog_err:false,
    }  },
       methods: {
    ...mapActions(["fetchDictionary", "fetchDictionaryByCode", "addNewDicCodeItem", 'getTranslationsById',"getTranslationsByResourceType","getDefaultLanguage", "fetchOrganizationUnitType"]),
       updatepos(indx){  return this.position=indx},

        addDicItemSubmit(e) {   
        e.preventDefault(); 
        this.formData.dictionaryCode=this.slctdItm;
        this.addNewDicCodeItem( this.formData)
        .then( () =>{
             this.fetchDictionaryByCode(this.slctdItm); 
             this.formData={};
             this.dialog=false; 
        })
        .catch(error => {  
           if(error.response.data.errorCode=='Session.NotFound'|| error.response.data.errorCode=='Session.IsExpired'){
                   this.$router.push({ path: '/' });   } 
                this.dialog_err=true;
                this.errorMsg=error.response.data;  
         })

        
         
       },
    clickFunctionTr() {
       var tr = document.querySelectorAll('.table-contract tr'); 
      
       for (var i = 0; i < tr.length; i++) {
     
       
         tr[i].addEventListener("click", function(){   
          if(document.querySelector('.table-contract tr.active_tr')!=null){
           document.querySelector('.table-contract tr.active_tr').classList.remove('active_tr');}
            this.classList.add('active_tr')
       }, false
         )} 
       },
       clickFunction() {
         this.fetchDictionary()
        .then(()=>{ 
         var list = document.getElementById("menuList").firstElementChild;
         list.click();
         
         })
          .catch(error => {  
        if(error.response.data.errorCode=='Session.NotFound'){ this.$router.push({ path: '/' });   }  
         })
                } ,

                 getT(item){ 
                    if (this.alltranslations.i18nResourceItemList.find(x => x.key === `Dictionaries.${item}`)!= null && this.alltranslations.i18nResourceItemList!= null){
                  return this.alltranslations.i18nResourceItemList.find(x => x.key === `Dictionaries.${item}`).value;
                    }
                         else {
                                
                     return item
                   }
                      },

                },

       computed: {
         ...mapGetters(["alldictionary", "alldicbycode","alltranslations","allorgUnitType"]),
           slctdItm(){  return this.alldictionary[this.position].code  },
            slctdItmStr(){  
              if(this.alldictionary[this.position]){
              return this.alldictionary[this.position].code 
              }
              else{
                return false
              }
              },
           ctrtype:function(){    return this.fetchDictionaryByCode(this.slctdItm)  },  
 
               },

      created()
            {
                   this.clickFunction();
                     this.fetchOrganizationUnitType();
      
           }
  }
</script>

<style scoped>
    .row.no-gutters{
        height: 100%;
    }

     .row.no-gutters>div {
        height: 100%;
    }
      .row.no-gutters .menu-dictionary{
        height: 100%;
            margin-right: 10px;
    }
       .table-dic{
           height: 90%;
       }
       .filter-dic{
               margin: 10px 0px;
    padding: 10px;
       }

       .title-dic{
           float: left;
           padding-top:5px;
       }
        .button-dic{
           float: right
       }
        .clearfix{
           clear: both
       }
</style>