                     </v-col>

                         <v-col cols="12"  md="4"  title="Satışa çıxarılan səhmlərin nominal dəyəri"  v-if="checkFieldChilds('soldAsset','totalShareNominalValue')" >
                         <v-text-field   oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');" label="Satışa çıxarılan səhmlərin nominal dəyəri"   required v-model="formData.soldAsset.totalShareNominalValue"  v-bind:class="{ required: checkFieldChilds('soldAsset','totalShareNominalValue').mandatory}"></v-text-field>
                         </v-col>

                         <v-col cols="12"  md="4" title="Satışa çıxarılan bir səhmin nominal dəyəri"  v-if="checkFieldChilds('soldAsset','singleShareNominalValue')">
                         <v-text-field   oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');" label="Satışa çıxarılan bir səhmin nominal dəyəri"   required v-model="formData.soldAsset.singleShareNominalValue"  v-bind:class="{ required: checkFieldChilds('soldAsset','singleShareNominalValue').mandatory}"></v-text-field>
                         </v-col>

                        <v-col cols="12"  md="4" title="Satışa çıxarılan səhmlərin ilkin hərrac qiyməti"  v-if="checkFieldChilds('soldAsset','totalShareInitialBidPrice')">
                         <v-text-field   oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');" label="Satışa çıxarılan səhmlərin ilkin hərrac qiyməti"   required v-model="formData.soldAsset.totalShareInitialBidPrice"  v-bind:class="{ required: checkFieldChilds('soldAsset','totalShareInitialBidPrice').mandatory}"></v-text-field>
                         </v-col>
                     
                        <v-col cols="12"  md="4"   title="Satışa çıxarılan səhmlərin axırıncı hərrac qiyməti"  v-if="checkFieldChilds('soldAsset','totalShareFinalBidPrice')" >
                         <v-text-field   oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');" label="Satışa çıxarılan səhmlərin axırıncı hərrac qiyməti"   required v-model="formData.soldAsset.totalShareFinalBidPrice"  v-bind:class="{ required: checkFieldChilds('soldAsset','totalShareFinalBidPrice').mandatory}"></v-text-field>
                         </v-col>
                     
                     
                         <v-col cols="12"  md="4"  v-if="checkFieldChilds('soldAsset','authorizedCapitalPercent')" >
                         <v-text-field   oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');" label="Nizamnamə kaptalının faizi"   required v-model="formData.soldAsset.authorizedCapitalPercent"  v-bind:class="{ required: checkFieldChilds('soldAsset','authorizedCapitalPercent').mandatory}"></v-text-field>
                         </v-col>

                        <v-col cols="12"  md="4"   v-if="checkFieldChilds('soldAsset','authorizedCapitalValue')">
                         <v-text-field   oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');" label="Nizamnamə kaptalının qiyməti "   required v-model="formData.soldAsset.authorizedCapitalValue"  v-bind:class="{ required: checkFieldChilds('soldAsset','authorizedCapitalValue').mandatory}"></v-text-field>
                         </v-col>
                     
                        <v-col cols="12"  md="4"    v-if="checkFieldChilds('soldAsset','authorizedCapitalVolume')">
                         <v-text-field   oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');" label="Nizamnamə kaptalının dəyəri "   required v-model="formData.soldAsset.authorizedCapitalVolume"  v-bind:class="{ required: checkFieldChilds('soldAsset','authorizedCapitalVolume').mandatory}"></v-text-field>
                         </v-col>

                        
                      </v-row>
