                      <v-row>
                       
                        <v-col   cols="12"   md="4" v-if="checkFieldSubChilds('soldAsset','address', 'address')"> 
                          <v-text-field label="Ünvan"   required v-model="formData.soldAsset.address.address"   v-bind:class="{ required: checkFieldSubChilds('soldAsset','address', 'address').mandatory}"></v-text-field>
                     </v-col>

                      <v-col   cols="12"   md="4" v-if="checkFieldSubChilds('soldAsset','address', 'cityTownId')"> 
