                     </v-col>

                      <v-col   cols="12"   md="4" v-if="checkFieldSubChilds('soldAsset','address', 'countryId')"> 
                           <v-select v-model="formData.soldAsset.address.countryId" :items="allcountry" item-value="id" item-text="description" label="Ölkə" v-bind:class="{ required: checkFieldSubChilds('soldAsset','address', 'countryId').mandatory}"></v-select>
                          
                     </v-col>

                      <v-col   cols="12"   md="4" v-if="checkFieldSubChilds('soldAsset','address', 'regionId')"> 
                           <v-select v-model="formData.soldAsset.address.regionId" :items="allregion" item-value="id" item-text="description" label="Şəhər" v-bind:class="{ required: checkFieldSubChilds('soldAsset','address', 'regionId').mandatory}"></v-select>
                           
                     </v-col>
                       </v-row>




 
                      <v-row  > 
                       
                    <v-col cols="12"  md="4"  v-if="checkField('auctionDate')">
                          <v-menu
                              v-model="menu55"
                              :close-on-content-click="false"
                              :nudge-right="40"
                               transition="scale-transition"
                               offset-y
                               min-width="290px"
                          >
                          <template v-slot:activator="{ on }">
                             <v-text-field
                              :value="formData.auctionDate"
                              label="Hərrac tarixi"
                              append-icon="event"
                               readonly
                               v-on="on"
                                 v-bind:class="{ required: checkFieldMandatory('auctionDate') }"
                              ></v-text-field>
                          </template>
                          <v-date-picker  :first-day-of-week="1" v-model="formatauctionDate" @input="menu55 = false"></v-date-picker>
                          </v-menu>
                           
                       </v-col>

                         <v-col cols="12"  md="4"    v-if="checkField('auctionNumber')" >
                         <v-text-field label="Hərrac №"    v-bind:class="{ required: checkFieldMandatory('auctionNumber') }"  v-model="formData.auctionNumber" required ></v-text-field>
                          
                         </v-col>

                          <v-col cols="12"  md="4"   v-if="checkField('protocolNumber')"  >
                         <v-text-field label="Protokol №"  v-model="formData.protocolNumber" required   v-bind:class="{ required: checkFieldMandatory('protocolNumber') }" ></v-text-field>
                          
                         </v-col>
                         
                          <v-col cols="12"  md="4"   v-if="checkField('registrationNumber')"  >
                         <v-text-field label="Qeydiyyat №"  v-model="formData.registrationNumber" required   v-bind:class="{ required: checkFieldMandatory('registrationNumber') }"></v-text-field>
                         
                         </v-col>

                    <v-col cols="12"  md="4"  v-if="checkField('rentContractDate')" >
                          <v-menu
                              v-model="menu65"
                              :close-on-content-click="false"
                              :nudge-right="40"
                               transition="menu2scale-transition"
                               offset-y
                               min-width="290px"
                          >
                          <template v-slot:activator="{ on }">
                             <v-text-field
                              :value="formData.rentContractDate"
                              label="İcarə müqaviləsinin tarixi"
                              append-icon="event"
                               readonly
                               v-on="on"
                                v-bind:class="{ required: checkFieldMandatory('rentContractDate') }"
                              ></v-text-field>
                          </template>
                          <v-date-picker  :first-day-of-week="1" v-model="formatrentContractDate" @input="menu65 = false"></v-date-picker>
                          </v-menu>
                           
                       </v-col>
                         
                          <v-col cols="12"  md="4" title="İcarə müqaviləsinin nömrəsi"   v-if="checkField('rentContractNumber')">
                         <v-text-field v-bind:class="{ required: checkFieldMandatory('rentContractNumber') }"  label="İcarə müqaviləsinin nömrəsi"  v-model="formData.rentContractNumber" required ></v-text-field>
                           
                         </v-col>
                      </v-row>
      
                    <v-row class="title-tabs"> 
                       <v-col cols="12"  class=" " >
                           <span>Sənədlər</span>
                                      
                   <div style="float:right">
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <template v-slot:activator="{ on }">
        
          <v-btn  icon   class='hrmorevert'
           text
          v-on="on">
          <v-icon>add_box</v-icon>
        
         </v-btn>
      </template>

      <v-card>
        <v-card-title
          class="modal-header"
           
        >
          <span>Yeni Sənəd</span>
          <v-spacer></v-spacer>
          
           <v-btn
           icon
            color="primary"
            text
            @click="dialog = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
            <v-card-text>
         
           <v-row>
                  <v-col  class="text-center inputfile" cols="12"    md="12" > 
                       <input   multiple style="display:none;" ref="fileInput0" type="file" @change="onFileSelected"> 
                       <v-btn  class="file_button"  @click="$refs.fileInput0.click()"   label="File input"> {{fname}}</v-btn>
                 </v-col> 
                      <div class="clerafix"></div>
                  <v-col cols="12" md="12">
         <v-textarea class="txtarea" 
          solo
          name="input-7-4"
           label="Təsviri"
          v-model="description"
          
        ></v-textarea> 
             </v-col>
            </v-row> 
        <v-divider></v-divider> 
        <v-card-actions class='dialog-buttons'>
           <v-btn     class="btn-cancel" outlined>İMTİNA ET</v-btn> 
             <v-btn    type="submit" @click="filesArray()" class="btn-add" >TƏSDİQ ET</v-btn> 
        </v-card-actions>
  
        </v-card-text>
      </v-card>
        
    </v-dialog>
  </div>
                  </v-col>
         
                      </v-row> 
                            <v-row>
                  <v-col   cols="12"   md="12" > 
               
                     
                   <div class="table-componenet">
  <v-simple-table>
    <template v-slot:default>
      <thead>
        <tr>
          <th id='1' class="text-center">№</th>
          <th id='2'  class="text-center">Faylın adı</th>
          <th id='3'  class="text-center">Təsviri</th> 
              
          <th id='6'  class="text-center">Sil</th>
        </tr>
      </thead>
      <tbody>
        <tr v-if="allfiles.length === 0" class="norecord">
          <td colspan="4">Sənəd əlavə olunmayıb...</td>
        </tr>
        <tr v-for="(item, index) in allfiles" :key="item.id">
          <td>{{ index+1 }}</td>
          <td>{{ item.filename.name }}</td>
          <td>{{ item.file_des}}</td> 
          <td  style="width:20px"> 
            <v-btn @click="removeFile(index)" class="removeFile"><v-icon>clear</v-icon></v-btn>
          </td>
        </tr>
      </tbody>
    </template>
  </v-simple-table> 

  </div> 
 
                 </v-col> 
    
       
             </v-row> 
          
          </v-card-text>
        </v-card>     
               
      </v-tab-item>
    </v-tabs-items>
     
     <v-row class='tab-buttons'>
           <v-col cols="12"  md="6" class='left-bt'  >
             <v-btn  @click="$router.go(-1)" outlined>İMTİNA ET</v-btn>  
             <v-btn class="btn-next"  @click="changeTabPrev()" v-if='!firstone()'>ƏVVƏLKİ</v-btn>  
           </v-col>
           <v-spacer></v-spacer>
            <v-col class="text-right right-bt" cols="12"  md="6">
              <v-btn @click="addContractDraft()" class="btn-save" >YADDA SAXLA</v-btn> 
              <v-btn class="btn-next" @click="changeTabNext()" v-if='!lastone()'>NÖVBƏTİ</v-btn> 
                <v-btn type="submit"  class="btn-next" v-show='lastone()'>TƏSDİQ ET</v-btn> 
             </v-col>
   </v-row>  
</div>
 </v-container>
     </v-form>    

    <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
  </v-card>
  
 </div>
</template>

<script> 

 
import ErrorDialog from '../popups/ErrorDialog';
import { mapGetters, mapActions } from "vuex";  

 
  
  export default {

    components:{ErrorDialog},
    

    data () {
      return {  
        allfiles:[ ],
        idForFile:1,
        dialog:false,
        count:0,
        errorMsg:'', 
        dialog_err: false,
        saleType:"0",
        selectedFile:null,
        fname:'Faylı seçin',
 
        formData: {    
          buyer:{},
          soldAsset: { address: {} },
          debt:{} 
        },

        obj:{},
        formFields:[],
         subformFields:[],
         tab: null,
         valid: false, 
         menu1: false,
         menu2: false,
         menu3: false,
         menu4: false,
         menu5: false,
         menu6: false, 
         menu15: false,
         menu25: false,
         menu35: false,
         menu45: false,
         menu55: false,
         menu65: false,
         description:'',
         sellers:[],
         nameRules: [
         v => !!v || 'Bu sahə mütləq doldurulmalıdır!', 
        ],

        uploadedDocumentDatas:[],
        uploadedDocumentFiles:[],

          discountAmount:null,
          discountPercent:null, 
         salePrice:null, 
       }
    },
    
      
     computed: { 

   

       saleTypeDis(){
        if(this.saleType==0){
          return true
        }
         
          return false
        
       },
         formatcontractDate:
        {
            get() {
              if (this.formData.contractDate === undefined) { return null; }
              return  this.formData.contractDate.split('.').join('-').replace(/(\d{2})-(\d{2})-(\d{4})/, "$3-$2-$1"); }  ,
              set(value) { this.formData.contractDate = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },
         formatexecutiveOrderDate:
        {
            get() {
              if (this.formData.executiveOrderDate === undefined) { return null; }
              return  this.formData.executiveOrderDate.split('.').join('-').replace(/(\d{2})-(\d{2})-(\d{4})/, "$3-$2-$1"); }  ,
              set(value) { this.formData.executiveOrderDate = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },
         formatauctionDate:
        {
            get() {
              if (this.formData.auctionDate === undefined) { return null; }
              return  this.formData.auctionDate.split('.').join('-').replace(/(\d{2})-(\d{2})-(\d{4})/, "$3-$2-$1"); }  ,
              set(value) { this.formData.auctionDate = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },
         formatrentContractDate:
        {
            get() {
              if (this.formData.rentContractDate === undefined) { return null; }
              return  this.formData.rentContractDate.split('.').join('-').replace(/(\d{2})-(\d{2})-(\d{4})/, "$3-$2-$1"); }  ,
              set(value) { this.formData.rentContractDate = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },
         formatproclamationDate:
        {
            get() {
              if (this.formData.soldAsset.proclamationDate === undefined) { return null; }
              return  this.formData.soldAsset.proclamationDate.split('.').join('-').replace(/(\d{2})-(\d{2})-(\d{4})/, "$3-$2-$1"); }  ,
              set(value) { this.formData.soldAsset.proclamationDate = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },

        formatpowerOfAttorneyDate:
        {
            get() {
              if (this.formData.buyer.powerOfAttorneyDate === undefined) { return null; }
              return  this.formData.buyer.powerOfAttorneyDate.split('.').join('-').replace(/(\d{2})-(\d{2})-(\d{4})/, "$3-$2-$1"); }  ,
              set(value) { this.formData.buyer.powerOfAttorneyDate = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },

        
       ...mapGetters(["allformContract","allcontracttype","allcontracttype","allcontractliability","allsellers","alltown","allregion","allcountry" ]),
      
  },
     
       methods:{ 

            filesArray(){
             this.allfiles.push({
             id: this.idForFile,
             filename:this.selectedFile,
             file_des: this.description, 
        
         })
            this.uploadedDocumentFiles.push(this.selectedFile);
            this.uploadedDocumentDatas.push( {description: this.description});
        
          this.fname='Faylı seçin'
          this.selectedFile =null;
            this.description ='';
          this.idForFile++;
          this.dialog=false;
            },
          removeFile(index) {
      this.allfiles.splice(index, 1)
    },

           changeTabNext(){
             window.scrollTo(0, 0);
              var i=Number(this.tab.slice(4, 5).trim());
                 i+=1;
                this.tab='tab-'+i;  
                },

       changeTabPrev(){
         window.scrollTo(0, 0);
              var i=Number(this.tab.slice(4, 5).trim());
                 i-=1;
                this.tab='tab-'+i;  
                },

            lastone(){
                  if(this.tab=='tab-5'){
                    this.$refs.form.validate();  
                    return true;
                    }
              },

                firstone(){
                  if(this.tab=='tab-1'){
                  return true;
                    }
              }, 

          ...mapActions(["getForms","fetchContracts","fetchSellers","addNewagreement","fetchDictionaryByCode","fetchContType","fetchContractLiability","fetchCity","fetchCountry","fetchRegion"]),
          
        addContractSubmit(e) { 
               
            e.preventDefault();
               
              if(this.formData.buyer.identityDocumentSerial){
                this.formData.buyer.identityDocumentSerial.toUpperCase();
              }
              this.formData.debt.discountAmount=this.discountAmount; 
              this.formData.debt.discountPercent=this.discountPercent;
               this.formData.debt.salePrice=this.salePrice;
              this.formData.formName="ContractForm."+ this.getContType(this.formData.contractTypeId);
            
                  for(var key in this.formData) {
              if(this.formData[key] === "") {  
                
                delete this.formData[key];
               
              } 
              }

            for(var key1 in this.formData.buyer) {
              if(this.formData.buyer[key1] === "") {
               delete this.formData.buyer[key1]
        }
    }
            for(var key2 in this.formData.soldAsset) {
              if(this.formData.soldAsset[key2] === "") {
               delete this.formData.soldAsset[key2]
        }
    }
            for(var key3 in this.formData.debt) {
              if(this.formData.debt[key3] === "") {
               delete this.formData.debt[key3]
             
        }
    }

              const contractCreateRequest = new FormData();
              contractCreateRequest.append('contractCreateRequest', new Blob([JSON.stringify(this.formData)], {type : 'application/json'})); 
              contractCreateRequest.append('uploadedDocumentDatas', new Blob([JSON.stringify(this.uploadedDocumentDatas)], {type : 'application/json'}));
              for(var i=0;i<this.uploadedDocumentFiles.length;i++) {
               contractCreateRequest.append('uploadedDocumentFiles',  this.uploadedDocumentFiles[i],  this.uploadedDocumentFiles[i].name) 
                 }

         

               this.$refs.form.validate();

               
             this.addNewagreement(contractCreateRequest)
              .then(()=>{
                  
                    this.$router.push({ path: '/index' }) 
           })
           .catch(error => {  
              if(error.response.data.errorCode=='Session.NotFound'|| error.response.data.errorCode=='Session.IsExpired'){
                   this.$router.push({ path: '/' });   } 
               this.dialog_err=true;
                this.errorMsg=error.response.data; 
             
         }) 
     
 
           
        },

           addContractDraft() { 
                      for(var key in this.formData) {
              if(this.formData[key] === "") {
               delete this.formData[key]
        }
    }

         for(var key1 in this.formData.buyer) {
              if(this.formData.buyer[key1] === "") {
               delete this.formData.buyer[key1]
        }
    }
             this.formData.saveAsDraft=true;  
             this.formData.debt.discountAmount=this.discountAmount; 
              this.formData.debt.discountPercent=this.discountPercent;  
              this.formData.debt.salePrice=this.salePrice; 
                this.formData.formName="ContractForm."+ this.getContType(this.formData.contractTypeId);

            const contractCreateRequest = new FormData();
              contractCreateRequest.append('contractCreateRequest', new Blob([JSON.stringify(this.formData)], {type : 'application/json'})); 
              contractCreateRequest.append('uploadedDocumentDatas', new Blob([JSON.stringify(this.uploadedDocumentDatas)], {type : 'application/json'}));
              for(var i=0;i<this.uploadedDocumentFiles.length;i++) {
               contractCreateRequest.append('uploadedDocumentFiles',  this.uploadedDocumentFiles[i],  this.uploadedDocumentFiles[i].name) 
                 }
              this.addNewagreement(contractCreateRequest)
              .then(()=>{
                
                    this.$router.push({ path: '/draft' }) 
           })
           .catch(error => {  
              if(error.response.data.errorCode=='Session.NotFound'|| error.response.data.errorCode=='Session.IsExpired'){
                   this.$router.push({ path: '/' });   } 
             this.dialog_err=true;
                this.errorMsg=error.response.data; 
         })

           
        },
          onFileSelected(event){
            this.selectedFile=event.target.files[0];
            this.fname= event.target.files[0].name; 


      },   getContTypeTitle(id){   
                    
                  return  this.allcontracttype.find(x => x.id == id).description;
                      
                  },
                getContType(id){   
                    
                  return  this.allcontracttype.find(x => x.id == id).code;
                      
                  },
               
                getFormData(){
                  var id=this.formData.contractTypeId||1;
                  if (this.allformContract!= null){
                  this.formFields= this.allformContract.find(x => x.name === `ContractForm.${this.getContType(id)}`).formFieldList;
                 
                      }
                         else {
                                
                     return false
                   }
                      },

                  checkField(name){
                    if (this.formFields!= null){
                  return this.formFields.find(x => x.name == name);
                   }
                     else {
                                
                     return false
                   }
                  },

                checkFieldBuyer(name,t){
                      if (this.checkField('buyer')!= null){
                  return this.checkField('buyer').subFieldList.find(x => x.name == name).allowedValueSet.includes(t)
                      }

                     else {
                                
                     return false
                   }
                  },

                   checkFieldMandatory(name){
                   return this.checkField(name).mandatory;
                  },

                  checkFieldChilds(parentName,childName){
              if (this.formFields.find(x => x.name === parentName)!= null){ 
                   return this.formFields.find(x => x.name === parentName).subFieldList.find(x => x.name === childName); 
              }
                   else {
                                
                     return false
                   }
                  },


            checkFieldSubChilds(parentName,childName, subChild){
              if (this.checkFieldChilds(parentName,childName)!= null){ 
                   return this.checkFieldChilds(parentName,childName).subFieldList.find(x => x.name === subChild); 
              }
                   else {
                                
                     return false
                   }
                  },

                   checkFieldChildsByType(name){
                    if (this.checkFieldChilds('buyer',name)!= null){ 
                 return this.checkFieldChilds('buyer',name).visibleBasedOnFieldValueSet.includes(this.formData.buyer.type);
       
                 }
                   
                   else {
                                
                     return false
                   }
                   
                   },
                        checkFieldChildsByTypeMandatory(name){
                    if (this.checkFieldChilds('buyer',name)!= null){ 
                 return this.checkFieldChilds('buyer',name).mandatory;
       
                 }
                   
                   else {
                                
                     return false
                   }
                   
                   }
  },
      watch : {
               discountAmount:function(val) {
                  this.discountAmount = val;
                  if(this.salePrice && val  && this.saleType==1){
                 
                  this.discountPercent = Math.round((val * 100/this.salePrice)*100)/100;
                  }
               },

               discountPercent: function (val) {
                   this.discountPercent = val
                    if(this.salePrice && val && this.saleType==0){ 
                      this.discountAmount =  Math.round((val*this.salePrice/100)*100)/100;
                    }
                    
               },
               salePrice: function (val) {
                   this.salePrice = val;
                    if(  this.discountPercent ){
                    this.discountAmount =  Math.round((val*this.discountPercent/100)*100)/100; 
                  
                    }
               },

          
                 
            },
  created(){ 
    this.fetchSellers();
    this.fetchContType();
    this.fetchContractLiability();
    this.fetchCity();
    this.fetchCountry();
    this.fetchRegion();
    this.getForms() 
    .catch(error => {  
        if(error.response.data.errorCode=='Session.NotFound'){ this.$router.push({ path: '/' });   }  
         }) 
   
  }
  }
</script>

