<template>
<div class="contract_page">
  <Filterbar/>
  <div class="table-componenet">
  <v-simple-table>
    <template v-slot:default>
      <thead>
        <tr>
     
          <th id='1'>№</th> 
          <th id='2'>Kodu</th> 
          <th id='3'>Təsviri</th> 
          <th id='4'>Redaktə et</th>
          <th id='4'>Səlahiyyətləri</th>  
        </tr>
      </thead>
      <tbody class="table-contract">
        <tr v-for="(item,i) in allRoles" :key="i">
           <td>{{ i+1 }}</td>  
          <td>{{item.code}}</td>  
          <td>{{item.description}}</td> 
         
          <td><UpdateRole v-bind:roleId="item.id"/></td>
          <td><GrantRoleToMenuItemList v-bind:roleId="item.id"  v-bind:name="item.description"/></td>
        </tr>
      </tbody>
    </template>
  </v-simple-table> 
  </div>
  </div>
</template>

<script>
import Filterbar from '../components/Filterbar'
import UpdateRole from '../popups/UpdateRole'
import GrantRoleToMenuItemList from '../popups/GrantRoleToMenuItemList'
import { mapGetters, mapActions } from "vuex";
export default {
  components:{
   Filterbar,UpdateRole, GrantRoleToMenuItemList
  },
    data () {
      return {
          
      }
    },
      methods: {
    ...mapActions(["fetchRoles"]),
    clickFunction() {
       var tr = document.querySelectorAll('.table-contract tr'); 
      
       for (var i = 0; i < tr.length; i++) {
     
       
         tr[i].addEventListener("click", function(){   
          if(document.querySelector('.table-contract tr.active_tr')!=null){
           document.querySelector('.table-contract tr.active_tr').classList.remove('active_tr');}
            this.classList.add('active_tr')
       }, false
         )}
       }
        },
  computed: mapGetters(["allRoles"]),
  created() {
    this.fetchRoles()
    .then(()=>{
      this.clickFunction();
    })
  }
  
}
</script>