<template>
  <div>
    <v-dialog
      v-model="dialog"
      width="700"
    >
      <template v-slot:activator="{ on }">
        
         <v-btn  icon   class='new_button'
          text
          @click="fetchEmptyPos()"
          v-on="on">
          <v-icon>create_new_folder</v-icon>
          <span>Yeni</span>
         </v-btn>
         
      </template>

      <v-card>
        <v-card-title
          class="modal-header"
           
        >
          <span> İnsan resursları > Əməkdaşlar > Yeni əməkdaş yarat</span>
          <v-spacer></v-spacer>
          
           <v-btn
           icon
            color="primary"
            text
            @click="dialog = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
          <v-form  @submit="addWorkerSubmit"  v-model="isFormValid" ref="form" >
        <v-card-text>
                   <v-row>
                  
                         <v-col cols="12"  md="4"   >
                         <v-text-field  class="required" :rules="validationRules" v-model="formData.name" label="Adı"   required ></v-text-field>
                   
                         </v-col>
                      
                         <v-col cols="12"  md="4"   >
                         <v-text-field class="required" :rules="validationRules"  v-model="formData.surname" label="Soyadı"   required ></v-text-field>
                     
                         </v-col>
                           
                            <v-col cols="12"  md="4"   >
                         <v-text-field  class="required" :rules="validationRules" v-model="formData.patronymic" label="Ata adı"   required ></v-text-field>
                        
                         </v-col>
                      
                          <v-col cols="12"  md="4"   >
                         <v-text-field class="required"  placeholder="example@mail.ru" :rules="validationEmailRules"  v-model="formData.emailAddress" label="Email"   required ></v-text-field>
                        
                         </v-col>
                  <v-col cols="12"  md="4"> 
                         <v-menu
                              v-model="menu1"
                              :close-on-content-click="false"
                              :nudge-right="40"
                               transition="scale-transition"
                               offset-y
                               min-width="290px"
                          >
                          <template v-slot:activator="{ on }">
                             <v-text-field
                              :value="formData.birthDate" 
                              label="Doğum tarixi"
                              append-icon="event"
                               readonly
                             
                               v-on="on"  class="required"
                              ></v-text-field>
                          </template>
                         
                          <v-date-picker :max="yesterday"   :first-day-of-week="1" v-model="formatbirthdate" @input="menu1 = false"></v-date-picker>
                          </v-menu> 
                        
                         </v-col>
                        <v-col cols="12"  md="4"> 
                         <v-menu
                              v-model="menu2"
                              :close-on-content-click="false"
                              :nudge-right="40"
                               transition="scale-transition"
                               offset-y
                               min-width="290px"
                          >
                          <template v-slot:activator="{ on }">
                             <v-text-field
                              :value="formData.entranceDate" 
                              label="İşə qəbul tarixi"
                              append-icon="event"
                               readonly
                               v-on="on"  
                               class="required"
                              ></v-text-field>
                          </template>
                         
                          <v-date-picker  :max="nowDate" :first-day-of-week="1" v-model="formatenterancedate" @input="menu2 = false"></v-date-picker>
                          </v-menu>
                        
                         </v-col>
                         <v-col   cols="12"   md="4" > 
                           <v-select :rules="validationRules" class="required"   v-model="formData.personnelPositionId"    label="Vəzifəsi" :items="emptyP" item-value="id" item-text="description">
                            
                             </v-select>   
                        </v-col>
                         
                           <v-col cols="12"  md="4"   >
                         <v-text-field   v-model="formData.faxNumber" label="Faks nömrəsi"  ></v-text-field>
                        
                         </v-col>
                             <v-col cols="12"  md="4"   >
                         <v-text-field class="required"    :rules="validationRules" v-model="formData.phoneNumber" label="Telefon nömrəsi"   required ></v-text-field>
                        
                         </v-col>
                           <v-col cols="12"  md="4"   >
                         <v-text-field class="required" :rules="validationRules" v-model="formData.pinCode" maxLength="7" label="FİN kodu"   required >
                                     <v-tooltip bottom  slot="append">
      <template v-slot:activator="{ on, attrs }">
        
         <v-icon v-on="on" color="primary" dark  v-bind="attrs">
                  forum
                </v-icon>
      </template>
     <div class="align-center">
        <v-img 
          class="shrink mr-2"
          contain
          src="../assets/tooltip.png"
          transition="scale-transition"
          width="500"
        />
     </div>
    </v-tooltip>
                         </v-text-field>
                        
                         </v-col>
                      </v-row>
                 
          <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions class='dialog-buttons'>
           <v-btn   @click="dialog = false; reset()"  class="btn-cancel" outlined>İMTİNA ET</v-btn> 
             <v-btn   :disabled="!isFormValid"   type="submit" class="btn-add" >ƏLAVƏ ET</v-btn> 
             
        </v-card-actions>
    </v-form>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import ErrorDialog from '../popups/ErrorDialog';
// import jQuery from 'jquery';
//     let $ = jQuery;
// import DatePicker from 'vue2-datepicker'
import 'vue2-datepicker/index.css';
//  import 'vue2-datepicker/locale/aze';
import { mapGetters, mapActions } from "vuex";  
  export default {
    components:{ErrorDialog },
    data () {
      return {
        nowDate: new Date().toISOString().slice(0,10),
        yesterday:new Date(new Date().setDate(new Date().getDate()-1)).toISOString().slice(0,10),
        date: new Date(),  
        formData:{phoneNumber:'+994'},
        errorMsg:'',
        dialog_err:false,
        dialog: false,
        menu1:false, 
        menu2:false,  
        isFormValid:false, 
        validationRules: [
        v => !!v || 'Sahəni boş buraxmaq olmaz!', 
         ],
           validationEmailRules: [
        v => /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(v) || 'Email düzgün qeyd olunmayıb!', 
         ],
         emptyP:[]
      }
    },

     computed:{
      
        formatbirthdate:
        {
            get() {
              if (this.formData.birthDate === undefined) { return null; }
              return  this.formData.birthDate.split('.').join('-').replace(/(\d{2})-(\d{2})-(\d{4})/, "$3-$2-$1"); }  ,
              set(value) { this.formData.birthDate = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },

         formatenterancedate:
        {
            get() {
              if (this.formData.entranceDate === undefined) { return null; }
              return  this.formData.entranceDate.split('.').join('-').replace(/(\d{2})-(\d{2})-(\d{4})/, "$3-$2-$1"); }  ,
              set(value) { this.formData.entranceDate = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },
     
    ...mapGetters(["allPosition" ]),

        
      },
 
  methods: {
    
  
     ...mapActions(["fetchPosition", "addWorker","fetchWorkers", "fetchPositionCombo" ]),
       reset() { this.$refs.form.reset();  }, 

        addWorkerSubmit(e) {    
        e.preventDefault(); 
        this.addWorker(this.formData)
        .then(()=>{ 
         
         this.reset();
         this.fetchWorkers()
         .then(()=>{
            
         this.dialog=false; 
         })
        })
         .catch(error => {  
                this.dialog_err=true;
                this.errorMsg=error.response.data;  
         })
        
       },

fetchEmptyPos(){
  this.fetchPositionCombo()
  .then((res)=>{
     this.emptyP=res.data.filter(pos=>{return pos.personnelId === undefined});
    
  })
}

        },
 
  }  
  
</script>

<style scoped>
.v-btn{
        text-transform: unset!important;
}
.modal-header{
        background-color: #14b6db!important;
    padding: 5px 15px!important;
}
.modal-header span{
        font-size: 14px;
    font-weight: normal;
    color: white;
}
.modal-header .v-icon{
    color: white;
    font-size: 20px;
}

.v-dialog .v-card__actions{
    justify-content: center!important;
}

.btn-cancel{
    border: solid 1px #2294f2;
    color: #2294f2;
     
}
.btn-add{
     background-color: #2294f2!important;
     color: white!important;

}
.dialog-buttons .v-btn{
  width: 115px!important;
   box-shadow: unset;
}
.dialog-buttons{
    padding: 25px 0px;
}
</style>

