<template>
  <div class="new-payment">
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <template v-slot:activator="{ on }">
        
          <v-btn class="btn-add btn-new-p"
           text
          v-on="on"><span>Yeni ödəniş yarat</span>
         </v-btn>
      </template>

      <v-card>
        <v-card-title
          class="modal-header"
           
        >
          <span>Yeni Ödəniş</span>
          <v-spacer></v-spacer>
          
           <v-btn
           icon
            color="primary"
            text
            @click="dialog = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
          <v-form  @submit="addPaymentSubmit"  v-model="isFormValid" ref="form" >
        <v-card-text>
               <v-row>
                 
                          <v-col cols="12"  md="12">
                          <v-menu
                              v-model="menu"
                              :close-on-content-click="false"
                              :nudge-right="40"
                               transition="scale-transition"
                               offset-y
                               min-width="290px"
                          >
                          <template v-slot:activator="{ on }">
                             <v-text-field
                              :value="formData.paymentDate"
                              label="Ödəniş tarixi"
                              append-icon="event"
                               readonly
                               v-on="on"
                              ></v-text-field>
                          </template>
                          <v-date-picker :first-day-of-week="1" v-model="formatpaymentDate" @input="menu = false"></v-date-picker>
                          </v-menu>
                        
                       </v-col>
                 

                <v-col cols="12"  md="12"   >
                         <v-text-field label="Ödənişin miqdarı"  v-model="formData.paymentAmount" required ></v-text-field>
                         
                         </v-col>
             </v-row>
        </v-card-text> 
        <v-divider></v-divider> 
        <v-card-actions class='dialog-buttons'>
           <v-btn   @click="dialog = false; formData.formatpaymentDate=null; formData.paymentAmount=null"  class="btn-cancel" outlined>İMTİNA ET</v-btn> 
             <v-btn :disabled="!isFormValid"  type="submit" class="btn-add" >TƏSDİQ ET</v-btn> 
        </v-card-actions>
    </v-form>
      </v-card>
          <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
    </v-dialog>
  </div>
</template>
<script>
import {  mapActions, mapGetters } from "vuex"; 
 import ErrorDialog from '../popups/ErrorDialog';
  export default {
 props:["contractId"],
 components:{ErrorDialog},
    data () {
      return {
         formData:{ },
         isFormValid:false,
         dialog: false,
         menu:false,
         errorMsg:'',
         router: this.$router.currentRoute.path,
         dialog_err:false,
         validationRules: [ v => !!v || 'Sahəni boş buraxmaq olmaz!'],
      }
    },
    methods:{
       reset() {   this.$refs.form.reset();   },
    ...mapActions(["createPayment", "fetchPayment", "getContract","fetchContracts"]),
     addPaymentSubmit(e) {   
        e.preventDefault(); 
       
        this.formData.contractId=this.contractId;
        this.createPayment(this.formData)
        .then(()=>{

         this.fetchContracts(this.actionQueryString);
         this.fetchPayment(this.contractId);
         this.getContract(this.contractId);
         this.reset();
         this.dialog=false; 
        })
        .catch(error => {  
                this.dialog_err=true;
                this.errorMsg=error.response.data;  
         })
       
       }
},
     computed: {
        ...mapGetters(["loginuser"]),
        actionQueryString:function(){
          return this.loginuser.user.menuList.find(x => x.code =="TOP_MENU").menuItemList.find(x => x.routePath ==this.router).actionQueryString;
          
          },
       formatpaymentDate:
        {
            get() {
              if (this.formData.paymentDate === undefined) { return null; }
              return  this.formData.paymentDate.split('.').join('-').replace(/(\d{2})-(\d{2})-(\d{4})/, "$3-$2-$1"); }  ,
              set(value) { this.formData.paymentDate = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },
     },
  
   
  }
</script>

<style scoped>
.v-btn{
        text-transform: unset!important;
}
.modal-header{
        background-color: #14b6db!important;
    padding: 5px 15px!important;
}
.modal-header span{
        font-size: 14px;
    font-weight: normal;
    color: white;
}
.modal-header .v-icon{
    color: white;
    font-size: 20px;
}
.new-payment{
  text-align: center!important;
}
.v-dialog .v-card__actions{
    justify-content: center!important;
}

.btn-cancel{
    border: solid 1px #2294f2;
    color: #2294f2;
     
}
.btn-add{
     background-color: #2294f2!important;
     color: white!important;
     text-transform: uppercase!important;
}

.btn-new-p{
      padding: 0px 15px!important;
}
.dialog-buttons .v-btn{
  width: 115px!important;
   box-shadow: unset;
}
.dialog-buttons{
    padding: 25px 0px;
}
</style>