<template >
  <div  >
    <v-dialog
      v-model="dialog"
      width="550"
     
    >
      <template v-slot:activator="{ on }">
        
          <v-btn icon   class='hrmorevert' @click="fetchUserRoles(userId);fetchRoles()"
           text
          v-on="on">
          <v-icon>assignment</v-icon>
          <span>Səlahiyyətlər</span>
         </v-btn>
      </template>

      <v-card>
        <v-card-title
          class="modal-header"
           
        >
          <span>İstifadəçi səlahiyyətləri</span>
          <v-spacer></v-spacer>
          
           <v-btn
           icon
            color="primary"
            text
            @click="dialog = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
         
        <v-card-text>
               <v-row>
                 <v-col><div class="user_info">{{name_user}} {{surname}} {{patronymic}}</div></v-col>
                  <v-col   cols="12"   md="12" > 
               
                     
                   <div class="table_user">
  <v-simple-table>
    <template v-slot:default>
      
      <tbody>
         
        <tr v-for="(item, index) in getuserRoles" :key="index">
    
          <td>{{ item.description}}</td> 
          <td  style="width:20px"> 
              <Delete  v-bind:userId="userId" v-bind:id="item.id" v-bind:viewName="name"/>
          </td>
        </tr>
      
      </tbody>
    </template>
  </v-simple-table> 
  
  </div> 
 
                 </v-col> 
                
                 <v-col  cols="12"   md="12" class="new_role_but  role_dialog">
                 <v-dialog
      v-model="dialog_newrole"
      width="550"    
       
       content-class="vdialognew"
    >
      <template v-slot:activator="{ on }">
        
          <v-btn  icon  
           text
          v-on="on"> 
          <span>YENİ SƏLAHİYYƏT ƏLAVƏ ET</span>
         </v-btn>
      </template>

      <v-card>
        <v-card-title
          class="modal-header"
           
        >
          <span id='md-click'>Yeni</span>
          <v-spacer></v-spacer>
          
           <v-btn
           icon
            color="primary"
            text
            @click="dialog_newrole = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
       
          <v-form  @submit="submitaddUserRoles"  ref="form" >
        <v-card-text>
                  <v-row>
                 
                      <v-col cols="12" sm="12">
          <v-select
          class="multiple_select"
            v-model="formData.roleIdList"
            :items="allRoles"
            attach
            chips
            label="Səlahiyyətlər"
            multiple
            item-value="id" 
            item-text="description"
            
          >
             <template v-slot:append-item>
               <v-divider class="mt-2"></v-divider>
           
      <div class='dialog-buttons' style="text-align: center!important; padding: 15px 0px 10px!important;">
             <v-btn  @click="multipleSelectAdd()"  class="btn-add" >ƏLAVƏ ET</v-btn> 
        </div>
        </template>
          </v-select>
        </v-col>     
 
             </v-row>
        </v-card-text>

        <v-divider></v-divider>

      <v-card-actions class='dialog-buttons'>
           <v-btn   @click="dialog_newrole = false; reset()"  class="btn-cancel" outlined>İMTİNA ET</v-btn> 
             <v-btn    type="submit" class="btn-add" >ƏLAVƏ ET</v-btn> 
        </v-card-actions>
          </v-form>
      </v-card>
    </v-dialog> 
                 </v-col>
             </v-row>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions class='dialog-buttons'>
           <v-btn   @click="dialog = false;"  class="btn-cancel" outlined>BAĞLA</v-btn> 
            
        </v-card-actions>
  
      </v-card>
    </v-dialog>
     <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
  </div>
</template>
<script>
import Delete from '../popups/Delete';
import ErrorDialog from '../popups/ErrorDialog';
 import { mapGetters, mapActions } from "vuex";
 export default {
      components:{Delete,ErrorDialog},
    props:["userId", "name_user", "surname","patronymic"],
  
    data () {
      return { 
         name:"userrole",
        errorMsg:'',
       dialog:false,
       dialog_err:false,
       dialog_newrole:false,
     
     formData:{},
      }
    },

     computed:{ ...mapGetters(["allRoles","getuserRoles" ]), },
     methods: {
     ...mapActions([ "fetchUserRoles", "fetchRoles", "addUserRoles" ]),
       
       reset() { this.$refs.form.reset();  }, 
       multipleSelectAdd(){
          document.querySelector('.multiple_select div.v-menu__content ').classList.remove('menuable__content__active');
              document.querySelector('.multiple_select div.v-menu__content ').style.display = 'none';
          //  document.getElementById('md-click').click();
       },
       submitaddUserRoles(e){
           e.preventDefault(); 
           this.formData.userId=this.userId;
           this.formData.loginAllowed=true;
           this.addUserRoles(this.formData)
          .then(()=>{
           this.fetchUserRoles(this.userId);
           this.dialog_newrole=false;
           this.reset(); })
          .catch(error => {  
                this.dialog_err=true;
                this.errorMsg=error.response.data;  
         })
       }

        },
     
  
  }
</script>

<style >
  .vdialognew.v-dialog {
    
    overflow-y: visible!important;}
.multiple_select .v-menu__content{
z-index: 999!important;
}
.new_role_but{
  text-align: center;
}
.table_user .theme--light.v-data-table tbody tr td  {
    border-right: none!important;
    text-align: left!important;
    border-bottom: 1px solid rgba(0, 0, 0, 0.12);
} 

.new_role_but button{
  text-align: center; 
    border: 1px solid #14b6db!important;
    border-radius: unset;
    padding: 10px!important;
    color: #14b6db!important;
        font-size: 12px!important;
}
.user_info{
      text-align: center;
    padding: 10px 5px;
    border: 1px solid #14b6db!important;
    color: #14b6db!important;
}
.v-btn{
        text-transform: unset!important;
}
.modal-header{
        background-color: #14b6db!important;
    padding: 5px 15px!important;
}
.modal-header span{
        font-size: 14px;
    font-weight: normal;
    color: white;
}
.modal-header .v-icon{
    color: white;
    font-size: 20px;
}

.v-dialog .v-card__actions{
    justify-content: center!important;
}

.btn-cancel{
    border: solid 1px #2294f2;
    color: #2294f2;
     
}
.btn-add{
     background-color: #2294f2!important;
     color: white!important;

}
.dialog-buttons .v-btn{
  width: 115px!important;
   box-shadow: unset;
}
.dialog-buttons{
    padding: 25px 0px;
}
.norecord{
      font-style: italic;
    border-bottom: 1px solid #dfdbdb; 
    letter-spacing: 1.88px;}

    .current_debt{
          text-align: center;
    border-top: 1px solid rgba(0, 0, 0, 0.12)!important;
    border-bottom: 1px solid rgba(0, 0, 0, 0.12)!important;
    padding: 10px 0px;
    color: black;
    font-size: 16px;
    }
</style>