<template>
  <div>
    <v-dialog
      v-model="dialog"
      width="400"
    >
      <template v-slot:activator="{ on }">
         <v-btn  icon   class='hrmorevert'
           text
          v-on="on">
          <v-icon>folder_open</v-icon>
          <span>İşdən çıxma</span>
         </v-btn>
         
      </template>

      <v-card>
        <v-card-title class="modal-header">
          <span> Insan resursları > Əməkdaşlar >İşdən çıxma</span>
          <v-spacer></v-spacer>
          
           <v-btn
            icon
            color="primary"
            text
            @click="dialog = false"
          ><v-icon>cancel</v-icon>
            
          </v-btn>
          
        </v-card-title>
             <v-form @submit="leaveWorkerSubmit" v-model="isFormValid" ref="form" >
        <v-card-text >
                  <v-row>
                 
                          <v-col cols="12"  md="12">
                          <v-menu
                              v-model="menu"
                              :close-on-content-click="false"
                              :nudge-right="40"
                               transition="scale-transition"
                               offset-y
                               min-width="290px"
                          >
                          <template v-slot:activator="{ on }">
                             <v-text-field
                              :value="formData.leaveDate"
                              label="İşdən çıxma tarixi"
                              append-icon="event"
                               readonly
                               v-on="on"
                              ></v-text-field>
                          </template>
                          <v-date-picker :first-day-of-week="1" v-model="formatleaveDate" @input="menu = false"></v-date-picker>
                          </v-menu> 
                       </v-col> 
                 </v-row> 
                  <ErrorDialog v-model="dialog_err"  v-bind:errorMsg="errorMsg"/>
           </v-card-text> 
        <v-divider></v-divider>

        <v-card-actions class='dialog-buttons'>
           <v-btn    v-if="hideButtons"  @click="dialog = false"  class="btn-cancel" outlined>İMTİNA ET</v-btn> 
             <v-btn    v-if="hideButtons"    type="submit" class="btn-add" >TƏSDİQ ET</v-btn> 
               <v-btn   v-if="!hideButtons"   @click="dialog = false;hideButtons=true" class="btn-add" >BAĞLA</v-btn> 
        </v-card-actions>
        </v-form>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import ErrorDialog from '../popups/ErrorDialog';
import { mapGetters, mapActions } from "vuex"; 
  export default {
      components:{ErrorDialog },
  props:['personelId'],
    data () {
      return { 
        hideButtons:true,
        formData: {},
        menu:false,
        dialog: false,
         validationRules: [
        v => !!v || 'Sahəni boş buraxmaq olmaz!', 
         ],
            errorMsg:'',
        dialog_err:false,
           isFormValid:false,
      }
    },
    computed:{
      ...mapGetters(["allRoles","allusers", "secretuserlg" ]),
       formatleaveDate:
        {
            get() {
              if (this.formData.leaveDate === undefined) { return null; }
              return  this.formData.leaveDate.split('.').join('-').replace(/(\d{2})-(\d{2})-(\d{4})/, "$3-$2-$1"); }  ,
              set(value) { this.formData.leaveDate = value.replace(/(\d{4})-(\d{2})-(\d{2})/, "$3.$2.$1"); }  
       },

    },
    methods: {
     ...mapActions(["fetchWorkers","leaveWorker"]),
    reset() {
        this.$refs.form.reset(); 
      },
          leaveWorkerSubmit(e) { 
              e.preventDefault(); 
              this.formData.personnelId=this.personelId;
              this.leaveWorker(this.formData)
              .then(()=>{
                  this.dialog=false; 
                  this.fetchWorkers();
                  this.reset() })
              .catch(error =>{ 
                     this.dialog=false; 
                   this.dialog_err=true;
                   this.errorMsg=error.response.data; 
                })   
                  }

  
    },
      created() {
    // this.fetchWorkers(); 
      }
  }
</script>

<style>
 
.v-btn{
        text-transform: unset!important;
}
.modal-header{
        background-color: #14b6db!important;
    padding: 5px 15px!important;
}
.modal-header span{
        font-size: 14px;
    font-weight: normal;
    color: white;
}
.modal-header .v-icon{
    color: white;
    font-size: 20px;
}

.v-dialog .v-card__actions{
    justify-content: center!important;
}

.btn-cancel{
    border: solid 1px #2294f2;
    color: #2294f2;
     
}
.btn-add{
     background-color: #2294f2!important;
     color: white!important;

}
.dialog-buttons .v-btn{
  width: 115px!important;
   box-shadow: unset;
}
.dialog-buttons{
    padding: 25px 0px;
}
.hrmorevert span{
      align-self: center;
    font-size: 1rem;
    letter-spacing: normal;
}

.hrmorevert.v-btn.v-size--default {
  padding: 0px!important;
  color: #1976d2!important;
  height: auto!important;
}
</style>