<template>      
    <v-row class='filterdiv' align="center">
      <v-col v-if="departmentFilter" class="d-flex form-item" cols="12" sm="5" md='3'>
        
    <v-select  outlined label="Şöbələr" :items="allOrgs" item-value="id" item-text="description"> </v-select> 
      </v-col>
       <v-col :title="getContTypeTitle(conType)" v-if="contractTypeFilter" class="d-flex form-item" cols="12" sm="5" md='3'>
       
        <v-select v-model="conType" outlined label="Müqavilənin tipi" :items="filterArray" item-value="id" item-text="description" @change="filterArrayFun(conType)">
    
           </v-select>
      

      </v-col>
      <v-spacer></v-spacer>
     
     
       <div v-if="newbuttonWorkers" class="buttons-bar" cols="12" sm="2" md='1'>
           <NewWorker/>
      </div>
        <div v-if="newbuttonSellers" class="buttons-bar" cols="12" sm="2" md='1'>
           <NewSeller/>
      </div>
        <div v-if="newbuttonpersonnelPos" class="buttons-bar" cols="12" sm="2" md='1'>
           <NewPersonnelPos/>
      </div>
          <div v-if="newbuttonrole" class="buttons-bar" cols="12" sm="2" md='1'>
           <NewRole/>
        </div>

           <div v-if="newbuttonFeeTariffs" class="buttons-bar" cols="12" sm="2" md='1'>
           <NewFeeTariff/>
      </div>
    </v-row>
</template>

<script>
import NewWorker from '../popups/NewWorker';
import NewSeller from '../popups/NewSeller';
import NewPersonnelPos from '../popups/NewPersonnelPos';
import NewRole from '../popups/NewRole';
import NewFeeTariff from '../popups/NewFeeTariff';
import { mapGetters, mapActions } from "vuex";
export default {
     components:{NewWorker, NewSeller,NewPersonnelPos, NewRole, NewFeeTariff},
    name:"Filterbar",
       data () {
      return {
         router: this.$router.currentRoute.path,
        // type:null,
        contt:[{
            code: "Hamısı",
            description: "Hamısı",
            id: 100}
        ],
        
         filterArray:[]
      }
    },
    computed:{
    ...mapGetters(["allOrgs", "allcontracttype","loginuser"]),

    conType:
      {
        get () { return this.$store.getters.conType },
        set (value) { this.$store.commit('updateType', value) }
      },
    departmentFilter() {
        return this.$route.meta.departmentFilter;
    },
    contractTypeFilter() {
        return this.$route.meta.contractTypeFilter;
    },
  
    newbuttonWorkers() {
        return this.$route.meta.newbuttonWorkers;
    },
      newbuttonSellers() {
        return this.$route.meta.newbuttonSellers;
    } ,
      newbuttonpersonnelPos() {
        return this.$route.meta.newbuttonpersonnelPos;
    },
       newbuttonrole() {
        return this.$route.meta.newRole;
    },
       newbuttonFeeTariffs() {
        return this.$route.meta.newFeeTariffs;
    }, 

     actionQueryString:function(){
          return this.loginuser.user.menuList.find(x => x.code =="TOP_MENU").menuItemList.find(x => x.routePath ==this.router).actionQueryString;
          
          },
    },
    methods: {
     ...mapActions(["fetchUnitOrgs",  "fetchContType", "fetchContracts"]),

      getContTypeTitle(id){   
                    if(this.conType && this.conType!=100){
                  return  this.allcontracttype.find(x => x.id == id).description;
                      }
                      else{
                        return ""
                      }
                  },

     filterArrayFun(t){
          if(t!=100){
           return this.fetchContracts(this.actionQueryString+'&&contractTypeId='+ t)
          }
          else{
            return this.fetchContracts(this.actionQueryString)
          }
     }
       
     },
 
      created() {
        //  this.fetchUnitOrgs(); 
       this.fetchContType()
       .then(()=>{
         this.filterArray=this.contt.concat(this.allcontracttype);
        
       }) 

       
  }
}
</script>

<style>
     .filterdiv{
          display: flex; 
           margin: 0px!important;
          box-shadow: 0 0 13px 0 rgba(0, 0, 0, 0.15);
           background-color: #ffffff;
           margin: 10px 0px!important;
     }
     .v-input{
         font-size: 14px!important;
     }
      .v-input__append-inner{
              margin-top: 4px!important;
      }
      .v-select__selections{
         padding: 0px!important;

      }
      .v-input__slot{
          min-width: unset!important;
             margin-bottom: 0px!important;
              min-height: unset!important;
      }
      .v-text-field.v-text-field--enclosed .v-text-field__details
       {
      margin-bottom: 0px!important;  
}

 .v-text-field--outlined .v-label {
    top: 6px!important;
}
 .filterdiv .v-label.v-label--active.theme--light.primary--text {
    top:18px!important
}  

 .filterdiv .v-label.v-label--active.theme--light {
    top:18px!important
} 

.form-item{
    padding-bottom: 0px!important;
}

.buttons-bar   { 
  text-align: center!important;
  padding: 6px 5px;
}

.buttons-bar a,.buttons-bar button{ 
  color: #2294F2!important;
}

.new_button span{
  display: block;
  text-align: center!important;
  color: #2294F2!important;
}
</style>